import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Workbook } from 'exceljs';
import * as fs from 'file-saver';
import { environment } from 'src/environments/environment';
import { fetchStrapiFormat } from '../helpers/functions';
import * as dayjs from 'dayjs';
import { UserService } from './user.service';
import * as relativeTime from 'dayjs/plugin/relativeTime';
dayjs.extend(relativeTime);
import 'dayjs/locale/fr';
@Injectable({
  providedIn: 'root'
})
export class ExportExcelService {
  private API_URL = environment.API_URL;
  private logo : any;

  constructor(private http: HttpClient,private userService: UserService
  ) {
    this.getBase64ImageFromUrl(JSON.parse(localStorage.getItem('Theme'))["logo"].url).then(result =>{ 
      this.logo = result
    })
  .catch(err => console.error(err));
   }
   async getBase64ImageFromUrl(imageUrl) {
    var res = await fetch(imageUrl);
    var blob = await res.blob();
  
    return new Promise((resolve, reject) => {
      var reader  = new FileReader();
      reader.addEventListener("load", function () {
          resolve(reader.result);
      }, false);
  
      reader.onerror = () => {
        return reject(this);
      };
      reader.readAsDataURL(blob);
    })
  }

  CertificationSend(data){
    let url = this.API_URL + "/certification/send";
    return this.http.post(url,data);
  }

  RapportStats(id_session,data) {
    let url = this.API_URL + "/activites-log-store/stats/" + id_session;
    return this.http.put<any>(url,data);

  }
  RapportSession(id_session) {
    let url = this.API_URL + "/activites-log-store/rapport/" + id_session;
    return this.http.get<any>(url);

  }
  SessionInfo(data) {
    let url = this.API_URL + "/activites-log-store/sessioninfo/recup";
    return this.http.post(url, data);
  }

  exportExcel(excelData) {

    const user = this.userService.getUser();
    const header = excelData.headers
    const data = excelData.data;
    const info_session = excelData.info_session
    const activityLog = excelData.activityLog
   
    const title = info_session.titre;
    //Create a workbook with a worksheet
    let workbook = new Workbook();
    let worksheet = workbook.addWorksheet(title);


    //Add Row and formatting
    worksheet.mergeCells('C1', 'E3');
    let titleRow = worksheet.getCell('C1');
    titleRow.value = title
    titleRow.font = {
      name: 'Calibri',
      size: 14,
      underline: 'single',
      bold: true,
      color: { argb: '0B2FAA' }
    }
    titleRow.alignment = { vertical: 'middle', horizontal: 'center' }

    // Date
    worksheet.mergeCells('C4:E4');
    dayjs.locale('fr')

    let dateCell = worksheet.getCell('C4');
    dateCell.value = dayjs(info_session.date_start).format('DD/MM/YYYY') + " - " + dayjs(info_session.date_finish).format('DD/MM/YYYY');
    dateCell.font = {
      name: 'Calibri',
      size: 9,
      bold: true
    }
    dateCell.alignment = { vertical: 'middle', horizontal: 'center' }

    let myLogoImage = workbook.addImage({
      base64: this.logo,
      extension: 'png',
    });
    worksheet.mergeCells('A1:B4');
    worksheet.addImage(myLogoImage, 'A1:B4');

    worksheet.addRow([]);
    const courseTime = this.FormatDate(activityLog.total_course_time)
    worksheet.mergeCells('A6:B6');
    let UserName = worksheet.getCell('A6');
    UserName.value ='Apprenant : ' + user.username;
    worksheet.mergeCells('A7:B7');
    let FormationTitle = worksheet.getCell('A7');
    FormationTitle.value ='Formation : ' + info_session.formation.title;
    worksheet.mergeCells('A8:B8');
    let courseTimme = worksheet.getCell('A8');
    courseTimme.value ='Temps total : ' + courseTime;
    worksheet.addRow([]);
    let headerRow = worksheet.addRow(header);
    headerRow.eachCell((cell, number) => {
      cell.fill = {
        type: 'pattern',
        pattern: 'solid',
        fgColor: { argb: '4167B8' },
        bgColor: { argb: '' }
      }
      cell.font = {
        bold: true,
        color: { argb: 'FFFFFF' },
        size: 12
      }
    })
  
    if (data) {
      data.forEach(d => {

        d[d.length - 1] = this.FormatDate(d[d.length - 1]);

        let row = worksheet.addRow(d);

        let sales = row.getCell(4);
        let color = 'FF99FF99';
        if (+sales.value < 200000) {
          color = 'FF9999'
        }

        sales.fill = {
          type: 'pattern',
          pattern: 'solid',
          fgColor: { argb: color }
        }
      }
      );
    }

    worksheet.getColumn(2).width = 24;
    worksheet.getColumn(3).width = 24;
    worksheet.getColumn(4).width = 13;
    worksheet.getColumn(5).width = 12;

    worksheet.addRow([]);
    worksheet.addRow([]);
    let footerRow = worksheet.addRow(['Rapport généré le ' + dayjs().format('DD/MM/YYYY') + " à " + dayjs().format("HH:mm:ss")]);
    footerRow.getCell(1).fill = {
      type: 'pattern',
      pattern: 'solid',
      fgColor: { argb: 'FFB050' }
    };

    worksheet.mergeCells(`A${footerRow.number}:E${footerRow.number}`);
 
    workbook.xlsx.writeBuffer().then((data) => {
      let blob = new Blob([data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
    
      fs.saveAs(blob, title.replace(/ /g,"_") +"_"+ user.username.replace(/ /g,"_") + '.xlsx');
    })

  }
  FormatDate(timesecond) {
    var date = new Date(0);
    date.setSeconds(timesecond);
    var timeString = date.toISOString().substr(11, 8);
    return timeString;
  }
}