import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../src/environments/environment';
import { fetchStrapiFormat } from 'src/app/helpers/functions';

@Injectable({
    providedIn: 'root'
})
export class SuiviFormationService {

    private API_URL= environment.API_URL;

    constructor(
        private http : HttpClient
    ) {}

    FindLog(id_activity,id_module, id_session){
    
            return fetchStrapiFormat(this.http, { url: '/activites-logs/find/' + id_session + "/" + id_module + "/" + id_activity});


    }
    LatestLog(id_session){
        return fetchStrapiFormat(this.http, { url: '/activites-logs/latest/'+ id_session });

    }
    SaveLog(data) {
        let url = `${this.API_URL}/activites-logs/create`;
        return this.http.post(url,data);
    }

   UpdateLog(id,data) {
        let url = this.API_URL+"/activites-logs/update/"+id;
        return this.http.put(url,data);
    }

}