import { Component, OnInit } from '@angular/core';
import { ApplyInstructorService } from './apply-instructor.service';
import { ModalService } from '../../common/popup-form/popup-form.service';
import { faUserGraduate, faFamilyPants } from '@fortawesome/pro-duotone-svg-icons';
import { AnimationOptions } from 'ngx-lottie';
@Component({
    selector: 'app-apply-instructor',
    templateUrl: './apply-instructor.component.html',
    styleUrls: ['./apply-instructor.component.scss'],
})
export class ApplyInstructorComponent implements OnInit {
    faUserGraduate = faUserGraduate;
    faFamilyPants = faFamilyPants;
    public data: any;
    options: AnimationOptions = {
        path: 'https://assets9.lottiefiles.com/packages/lf20_gepgb44u.json',
      };
    public defaultImg = JSON.parse(localStorage.getItem('Theme'));
    public theme = JSON.parse(localStorage.getItem('Theme')).theme;

    constructor(
        private content: ApplyInstructorService,
        private modalService: ModalService
    ) {
        this.content.getData().then((data: any) => {
            this.data = data;
        });
    }

    ngOnInit(): void {}

    openModal() {
        this.modalService.openTeacherModal();
    }
}
