import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../src/environments/environment';
import { fetchStrapiFormat } from '../../../helpers/functions';
@Injectable({
    providedIn: 'root',
})
export class CoursesService {
    private API_URL = environment.API_URL;

    constructor(private http: HttpClient) {}

    getFormationCards(limit){
        return fetchStrapiFormat(this.http, {
            url: '/getFormationCards/'+limit,
        });
    }


    getData(slug) {
        let url = this.API_URL + '/formations/getdata/'+slug;
        return this.http.get(url);
    }
    getCategoriesData() {
        let url = `${this.API_URL}/formations-categories?_sort=id:DESC`;
        return this.http.get(url);
    }


    getCount() {
        let url = `${this.API_URL}/formations/count`;
        return this.http.get(url);
    }
}
