import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../src/environments/environment';
import { fetchStrapiFormat } from '../../../helpers/functions';

@Injectable({
    providedIn: 'root',
})
export class CategoriesService {
    private API_URL = environment.API_URL;

    constructor(private http: HttpClient) {}

    getData() {

        let url = `${this.API_URL}/getCategs`;

        return this.http.get(url);
    }

    getAllCoursesCategories() {
     
        let url = `${this.API_URL}/getCategAndCount`;

        return this.http.get(url);


    }

    professionProspect(){
           

        return fetchStrapiFormat(this.http, {
      
            url: '/prospection',
            
        });

    }
    

    getAllCoursesByCategories(id) {
        let url = `${this.API_URL}/formationsByCategPreview/${id}`;

        return this.http.get(url);
    }
}
