import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../src/environments/environment';
import { fetchStrapiFormat } from 'src/app/helpers/functions';

@Injectable({
    providedIn: 'root'
})
export class ProfileService {

    private API_URL= environment.API_URL;

    constructor(
        private http : HttpClient
    ) {}

    getData(){
      
        return fetchStrapiFormat(this.http, { url: '/nav-bar-profil', populate: ['profilLists'] });

    }

    getSessions(){
      
        return fetchStrapiFormat(this.http, { url: '/users-permissions/session'});

    }

    CanAccesSession(slug,id_session){
        return this.http.get<any>(this.API_URL + '/users-permissions/session/formation/' + slug +"/"+id_session);
 

    }

    LatestCourse(){
        return fetchStrapiFormat(this.http, { url: '/activites-logs/course'});

    }

    Image(){
        return fetchStrapiFormat(this.http, { url: '/users-permissions/image'});

    }

 
    PostLogSuivi(data) {
        let url = `${this.API_URL}/activites-log-store/post`;
        return this.http.post(url,data);
    }
    UpdateLogInSuivi(id,data) {
        let url = this.API_URL+"/activites-log-store/updateLogin/"+id;
        return this.http.put(url,data);
    }

    GetTimePourcentCourse(id_session){
        let url =  this.API_URL+"/activites-log-store/findLatest/" +id_session;
        return this.http.get(url);

    }

}