import { Component, Input, OnInit } from '@angular/core';
import { FaqService } from './faq.service';
import {ModalService} from '../../common/popup-form/popup-form.service'
import { ActivatedRoute, Router } from '@angular/router';
import { HomePageService } from '../homepage/homepage.service';
import { faMessagesQuestion } from '@fortawesome/pro-duotone-svg-icons';
@Component({
    selector: 'app-faq',
    templateUrl: './faq.component.html',
    styleUrls: ['./faq.component.scss']
})
export class FaqComponent implements OnInit {
    faMessagesQuestion = faMessagesQuestion;
    public theme: any;
    public data: any;
    @Input() displayhome : boolean;
    public title : any;
    
    constructor(
		private content: FaqService,
        private homepageService : HomePageService,
        private modalService: ModalService,
        private router: Router
    ) {

       
    }

OpenModal(){
    this.modalService.openFaqModal();
}

  async  ngOnInit() {
  

        this.theme = JSON.parse(localStorage.getItem('Theme')).theme;
        (this.router.url.includes("ressources")) ? this.displayhome = false :  this.displayhome = true;
        if(this.displayhome){
            this.title = JSON.parse(this.title);
            this.content.getDataHome().then((data: any) => {
                this.data = data[0];
    
            });
        }else{
            this.content.getData().then((data: any) => {
                this.data = data;
    
            });
        }
    }

}