import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../src/environments/environment';

@Injectable({
    providedIn: 'root'
})
export class CoursesDetailsService {

    private API_URL= environment.API_URL;

    constructor(
        private http : HttpClient
    ) {}

    courseExist(id) {
        let url = `${this.API_URL}/formation/exist/${id}`;
        return this.http.get(url);
    }

    getCourse(id) {
        let url = `${this.API_URL}/formations/${id}`;
        return this.http.get(url);
    }

    getRatingsForCourseDetail(id){
        let url = `${this.API_URL}/ratings/forcoursedetail/${id}`;
        return this.http.get(url);
    }
    GetUserRate(idFormation){
        let url = `${this.API_URL}/ratings/getuserrate/${idFormation}`;
        return this.http.get(url);
    }

    getProgrammeSuivi(id_formation, id_session){
        let url = `${this.API_URL}/getProgrammeSuivi/${id_formation}/${id_session}`;
        return this.http.get(url);
    }
    getProgrammeCourse(id){
        let url = `${this.API_URL}/getProgramme/${id}`;
        return this.http.get(url);
    }

    getProgrammeExist(id){
        let url = `${this.API_URL}/program/exists/${id}`;
        return this.http.get(url);
    }

    getTeacherDetails(id){
        let url = `${this.API_URL}/teacherdetails/${id}`;
        return this.http.get(url);
    }

    getSuggestion(id){
        let url = `${this.API_URL}/getSuggestion/${id}`;
        return this.http.get(url);
    }

    
    getActiviteContent(id_activity,id_formation,id_session){
        let url = `${this.API_URL}/getActiviteContent/${id_session}/${id_formation}/${id_activity}`;
        return this.http.get(url);
    }

 
    getQandA(id,id_session){
        let url = `${this.API_URL}/getQandAByFormation/${id}/${id_session}`;
        return this.http.get(url);
    }
    
    getDetailCourse(id_formation){
        let url = `${this.API_URL}/users-permissions/detaillogcourse/${id_formation}`;
        return this.http.get(url);
    }
    


    // getFormationExist(id){
    //     let url = `${this.API_URL}/formation/exist/${id}`;
    //     return this.http.get(url);
    // }


}