import { Component, OnInit } from '@angular/core';
import { HomePageService } from './../homepage.service';

@Component({
    selector: 'app-homepage-stats',
    templateUrl: './homepage-stats.component.html',
    styleUrls: ['./homepage-stats.component.scss'],
})
export class HomePageStatsComponent implements OnInit {
    public data: any;
    public StatsInfo = [
        {Stat : 0, Text : "Taux de satisfaction"},
        {Stat : 0, Text : "Apprenants formés"},
        {Stat : 0, Text : "Cours dispensés"},
    ]

    constructor(private content: HomePageService) {
  
       


      
       
    }

   async  ngOnInit() {
    this.data = JSON.parse(this.data);

    if(this.data.stat && this.data.stat.length > 0 ){
        var i = 0;
        this.data.stat.forEach((el)=>{
         this.StatsInfo[i] = el;
         i += 1;
     })
    }
   
(this.StatsInfo[1].Stat == 0 ) ? this.content.getOrders().then((data: any) => {
 if (data != undefined && data.count != undefined)
        this.StatsInfo[1].Stat = data.count;
}) : null;

(this.StatsInfo[2].Stat == 0 ) ?  this.content.getFormationsCount().then((data: any) => {
    if (data != undefined && data.count != undefined)
    this.StatsInfo[2].Stat = data.count;
}) : null;


   }
}
