import { Component, OnInit } from '@angular/core';
import { ContactUsService } from './contact-us.service';
import { HttpClient } from '@angular/common/http';
import { FormBuilder, Validators } from '@angular/forms';
import { environment } from '../../../../environments/environment';
import { NotifierService } from 'angular-notifier';
import { Router } from '@angular/router';
import { ModalService } from '../../common/popup-form/popup-form.service';
import { faPhone, faLocationQuestion } from '@fortawesome/pro-duotone-svg-icons';

@Component({
    selector: 'app-contact-us',
    templateUrl: './contact-us.component.html',
    styleUrls: ['./contact-us.component.scss'],
})
export class ContactUsComponent implements OnInit {
    faPhone = faPhone;
    faLocationQuestion = faLocationQuestion;
    private readonly notifier: NotifierService;
    private API_URL = environment.API_URL;
    public theme = JSON.parse(localStorage.getItem('Theme')).theme;
    public data: any;
    public selected: any;
    public datanav: any;

    contactForm = this.formBuilder.group({
        name: ['', [Validators.required]],
        email: ['', [Validators.required]],
        phoneNumber: ['', [Validators.required]],
        message: ['', [Validators.required]],
    });

    constructor(
        private content: ContactUsService,
        private formBuilder: FormBuilder,
        private http: HttpClient,
        notifierService: NotifierService,
        private modalService: ModalService,
        private router: Router,
    ) {
        this.content.getData().then((res: any) => {
            // data = data[0];
            this.data = res;

   
        });
        this.notifier = notifierService;

        this.content.getDataNav().then((data: any) => {
            // data = data[0];

            for (let i = 0; i < data.contactLists.length; i++) {
                data.contactLists[i]['selected'] = false;
            }
            this.datanav = data;

            if (this.router.url.includes('#') && data.contactLists.find((el)=> el.menuLink.includes(this.router.url.split('#')[1])).publier) {
                var findkey = this.router.url.split('#')[1];
                data.contactLists[
                    Object.keys(data.contactLists).find(
                        (key) => data.contactLists[key].menuLink === findkey
                    )
                ]['selected'] = true;
            } else {
             
                data.contactLists[
                    Object.keys(data.contactLists).find(
                        (key) => data.contactLists[key].menuLink === 'informations'
                    )
                ]['selected'] = true;
            }
            this.selected = this.datanav.contactLists.find((el) => el.selected == true);
        });
    }

    ngOnInit(): void {}

    
    notifyError() {
        this.notifier.notify(
            'info',
            'Cette fonctionnalité arrive bientôt ! 👍'
        );
    }

    openModalEmail(){
        this.modalService.openEmailModal()
    }

    

    toggleNavbar(e) {
        if (!this.datanav.contactLists[e].publier) {
            return;
        }

        for (let i = 0; i < this.datanav.contactLists.length; i++) {
            i == e
                ? (this.datanav.contactLists[i]['selected'] = true)
                : (this.datanav.contactLists[i]['selected'] = false);
        }
        this.findSelected();
    }

    findSelected() {
        this.selected = this.datanav.contactLists.find(
            (el) => el.selected == true
        );
    }

    onSubmit(): void {
        let checkData = this.contactForm.value;
        const { name, email, phoneNumber, message } = checkData;
        let url = `${this.API_URL}/contacts`;
        this.http
            .post<any>(url, {
                data: {
                    name,
                    email,
                    phoneNumber,
                    message,
                },
            })
            .subscribe(
                (response) => {
                    this.notifier.notify(
                        'success',
                        'Message envoyé, à très vite !'
                    );
                    this.contactForm.reset();
                },
                (e) => {
                    this.notifier.notify('error', 'Email non valide..');
                }
            );
    }
}
