import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';

export function filepathToUrl(filepath: string) {
    if (typeof filepath == 'string' && filepath.startsWith('/')) {
        return environment.API_URL + filepath;
    } else if (Array.isArray(filepath)) {
        for (let f in filepath) {
            if (typeof filepath[f] == 'string' && filepath[f].startsWith('/')) {
                filepath[f] = environment.API_URL + filepath[f];
            }
        }
    }
    return filepath;
}

export function imageExists(image_url) {
    var http = new XMLHttpRequest();
    http.onreadystatechange = function () {
        if (http.readyState === 4) {
            //if complete
            if (http.status === 200) {
                //check if "OK" (200)
                //success
                return true;
            } else {
                return false; //otherwise, some other code was returned
            }
        }
    };
    http.open('HEAD', image_url, false);

    // http.onloadend = function() {
    //     if(http.status == 404)
    //         throw new Error(image_url + ' replied 404');
    // }
    http.send();

    return http.status != 404;
}

export function blobToFile(theBlob: Blob): File {
    // , fileName:string
    var b: any = theBlob;
    //A Blob() is almost a File() - it's just missing the two properties below which we will add
    b.lastModifiedDate = new Date();
    //b.name = fileName;
    var file = new File([b], theBlob['name']);

    //Cast to a File() type
    return <File>file;
}

export function validatePhone(phone: string): boolean {
    var match = phone.match(/([0-9])/g);
    return match && match.length >= 9;
}

export function scrollToTop() {
    (function smoothscroll() {
        var currentScroll =
            document.documentElement.scrollTop || document.body.scrollTop;
        if (currentScroll > 0) {
            window.requestAnimationFrame(smoothscroll);
            window.scrollTo(0, currentScroll - currentScroll / 8);
        }
    })();
}

