export function scrollToBottom(myScrollContainer) {
    try {
        myScrollContainer.nativeElement.scrollTop =
            myScrollContainer.nativeElement.scrollHeight;
    } catch (err) { }
}

export function isDef(val) {
    return Array.isArray(val)
        ? val.every((el) => typeof el != 'undefined')
        : typeof val != 'undefined';
}

export function checkAffectedRows(res) {
    return isDef(res) && isDef(res.affectedRows) && res.affectedRows > 0;
}

export function validateEmail(email: string): boolean {
    var match = email.match(
        /(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:(2(5[0-5]|[0-4][0-9])|1[0-9][0-9]|[1-9]?[0-9]))\.){3}(?:(2(5[0-5]|[0-4][0-9])|1[0-9][0-9]|[1-9]?[0-9])|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])/g
    );
    return Array.isArray(match) && match.length > 0 && match[0] == email;
}

export function isArrNotEmpty(elem: any): boolean {
    return Array.isArray(elem) && elem.length > 0;
}

export function noImage(imageUrl: string, gender: string): string {
    if (imageUrl == '') {
        switch (gender) {
            case '1':
                imageUrl = '../assets/images/user_female.png';
                break;
            case '2':
                imageUrl = '../assets/images/user_male.png';
                break;
            default:
                imageUrl = '../assets/images/user_other.png';
                break;
        }
    }
    return imageUrl;
}

export function dateFormat(
    date: string = '',
    isDateTime: boolean = false
): string {
    var d = date == '' ? new Date(Date.now()) : new Date(date);
    if (!(d instanceof Date) || isNaN(d.getTime())) return 'INVALID DATE';

    let day = d.getDate() <= 9 ? '0' + d.getDate() : d.getDate();
    let month =
        d.getMonth() + 1 <= 9 ? '0' + (d.getMonth() + 1) : d.getMonth() + 1;
    let hours = d.getHours() <= 9 ? '0' + d.getHours() : d.getHours();
    let minuts = d.getMinutes() <= 9 ? '0' + d.getMinutes() : d.getMinutes();
    var returnDate = day + '/' + month + '/' + d.getFullYear();
    if (isDateTime) {
        returnDate += ' ' + hours + 'h' + minuts;
    }
    return returnDate;
}

export function getLocalStorage() {
    return JSON.parse(localStorage.getItem('currentUser')).user[0];
}

