export function destructureStrapiFormat(data) {
    let cleanData: any = [];
    let neddToBeArr = false;

    if (data && data.data) {
        neddToBeArr = Array.isArray(data.data);

        if (neddToBeArr) {
            data.data.forEach((el) => {
                if (el.id != undefined && el.attributes != undefined) {
                    const obj = { id: el.id, ...el.attributes };
                    // el = obj;
                    cleanData.push(obj);
                } else cleanData = data;
            });
        } else if (
            data.data.id != undefined &&
            data.data.attributes != undefined
        ) {
            const obj = { id: data.data.id, ...data.data.attributes };
            cleanData = obj;
        }
        return cleanData;
    } else {
        return data;
    }
}

export function extractStrapiDataAttributes(data, populate): any[] {
    let cleanData = destructureStrapiFormat(data);
    data = cleanData;

    if (populate) {
        let popDeep = []; // populate.filter((el) => el.split('.').length > 1)
        populate.forEach((el) => {
            if (el.split('.').length > 1) popDeep.push(el.split('.'));
        });

        if (Array.isArray(data)) {
            data.forEach((d) => {
                Object.keys(d).forEach((k) => {
                    d[k] = destructureStrapiFormat(d[k]);
                });

                popDeep.forEach((popArrKey) => {
                    if (
                        d[popArrKey[0]] != undefined &&
                        d[popArrKey[0]].length > 0
                    ) {
                        cleanAllDataStrapi(d[popArrKey[0]]);

                        if (
                            popArrKey.length > 1 &&
                            Array.isArray(d[popArrKey[0]]) &&
                            d[popArrKey[0]][0][popArrKey[1]] != undefined
                        ) {
                            d[popArrKey[0]].forEach((el) => {
                                cleanAllDataStrapi(el[popArrKey[1]]);
                                if (
                                    popArrKey.length > 2 &&
                                    Array.isArray(el[popArrKey[1]]) &&
                                    el[popArrKey[1]].length > 0 &&
                                    el[popArrKey[1]][0][popArrKey[2]] !=
                                    undefined
                                ) {
                                    el[popArrKey[1]].forEach((elem) => {
                                        cleanAllDataStrapi(elem[popArrKey[2]]);
                                    });
                                } else if (
                                    popArrKey.length > 1 &&
                                    d[popArrKey[0]][popArrKey[1]] != undefined
                                ) {
                                    cleanAllDataStrapi(
                                        el[popArrKey[1]][popArrKey[1]]
                                    );
                                }
                            });
                        } else if (
                            popArrKey.length > 1 &&
                            d[popArrKey[0]][popArrKey[1]] != undefined
                        ) {
                            cleanAllDataStrapi(d[popArrKey[0]][popArrKey[1]]);

                            if (
                                popArrKey.length > 2 &&
                                Array.isArray(d[popArrKey[0]][popArrKey[1]]) &&
                                d[popArrKey[0]][popArrKey[1]].length > 0 &&
                                d[popArrKey[0]][popArrKey[1]][0][
                                popArrKey[2]
                                ] != undefined
                            ) {
                                d[popArrKey[0]][popArrKey[1]].forEach(
                                    (elem) => {
                                        cleanAllDataStrapi(elem[popArrKey[2]]);
                                    }
                                );
                            } else if (
                                popArrKey.length > 1 &&
                                d[popArrKey[0]][popArrKey[1]] != undefined
                            ) {
                                cleanAllDataStrapi(
                                    d[popArrKey[0]][popArrKey[1]][popArrKey[1]]
                                );
                            }
                        }
                    }

                    if (
                        popArrKey.length > 2 &&
                        d[popArrKey[0]] != undefined &&
                        d[popArrKey[0]][popArrKey[1]] != undefined &&
                        d[popArrKey[0]][popArrKey[1]][popArrKey[2]] != undefined
                    ) {
                        cleanAllDataStrapi(d[popArrKey[0]][popArrKey[1]]);
                    }
                });
            });
        } else {
            Object.keys(data).forEach((k) => {
                data[k] = destructureStrapiFormat(data[k]);
            });

            popDeep.forEach((popArrKey) => {
                if (
                    data[popArrKey[0]] != undefined &&
                    data[popArrKey[0]].length > 0
                ) {
                    cleanAllDataStrapi(data[popArrKey[0]]);

                    if (
                        popArrKey.length > 1 &&
                        Array.isArray(data[popArrKey[0]]) &&
                        data[popArrKey[0]][0][popArrKey[1]] != undefined
                    ) {
                        data[popArrKey[0]].forEach((el) => {
                            cleanAllDataStrapi(el[popArrKey[1]]);
                            if (
                                popArrKey.length > 2 &&
                                Array.isArray(el[popArrKey[1]]) &&
                                el[popArrKey[1]].length > 0 &&
                                el[popArrKey[1]][0][popArrKey[2]] != undefined
                            ) {
                                el[popArrKey[1]].forEach((elem) => {
                                    cleanAllDataStrapi(elem[popArrKey[2]]);
                                });
                            } else if (
                                popArrKey.length > 1 &&
                                data[popArrKey[0]][popArrKey[1]] != undefined
                            ) {
                                cleanAllDataStrapi(
                                    el[popArrKey[1]][popArrKey[1]]
                                );
                            }
                        });
                    } else if (
                        popArrKey.length > 1 &&
                        data[popArrKey[0]][popArrKey[1]] != undefined
                    ) {
                        cleanAllDataStrapi(data[popArrKey[0]][popArrKey[1]]);

                        if (
                            popArrKey.length > 2 &&
                            Array.isArray(data[popArrKey[0]][popArrKey[1]]) &&
                            data[popArrKey[0]][popArrKey[1]].length > 0 &&
                            data[popArrKey[0]][popArrKey[1]][0][popArrKey[2]] !=
                            undefined
                        ) {
                            data[popArrKey[0]][popArrKey[1]].forEach((elem) => {
                                cleanAllDataStrapi(elem[popArrKey[2]]);
                            });
                        } else if (
                            popArrKey.length > 1 &&
                            data[popArrKey[0]][popArrKey[1]] != undefined
                        ) {
                            cleanAllDataStrapi(
                                data[popArrKey[0]][popArrKey[1]][popArrKey[1]]
                            );
                        }
                    }
                }

                if (
                    popArrKey.length > 2 &&
                    data[popArrKey[0]] != undefined &&
                    data[popArrKey[0]][popArrKey[1]] != undefined &&
                    data[popArrKey[0]][popArrKey[1]][popArrKey[2]] != undefined
                ) {
                    cleanAllDataStrapi(data[popArrKey[0]][popArrKey[1]]);
                }
            });
        }
    }

    if (!data) console.log('🛑 Failed to extract strapi data : ', data);

    return data;
}

export function cleanAllDataStrapi(ddd) {
    if (Array.isArray(ddd)) {
        ddd.forEach((d) => {
            Object.keys(d).forEach((k) => {
                d[k] = destructureStrapiFormat(d[k]);
            });
        });
    } else {
        Object.keys(ddd).forEach((k) => {
            ddd[k] = destructureStrapiFormat(ddd[k]);
        });
    }
}

// params => { populate: ['column','column'..] , query : "popular=true?&pagination%5Blimit%5D=2" }
export function fetchStrapiFormat(http, params): Promise<any> {
    // url param ================================
    let url = '';
    if (
        params.url != undefined &&
        params.url &&
        typeof params.url == 'string'
    ) {
        url = params.url;
    }

    const qArr = [];

    // filter param ======================
    let filterQuery = '';
    if (
        params.filter != undefined &&
        params.filter &&
        Object.keys(params.filter) &&
        Object.keys(params.filter).length > 0
    ) {
        const keys = Object.keys(params.filter);
        keys.forEach((k, i) => {
            filterQuery =
                (i > 0 ? '&' : '') +
                filterQuery +
                '[' +
                k +
                '][$eq]=' +
                params.filter[k];
        });
        filterQuery = 'filters' + filterQuery;
        qArr.push(filterQuery);
    }

    // populate param ================
    let populateQuery = '';
    let populateArray = [];
    if (
        params.populate != undefined &&
        params.populate &&
        Array.isArray(params.populate) &&
        params.populate.length > 0
    ) {
        const populateArr = [...params.populate];
        populateArray = populateArr;
        populateQuery = 'populate=' + populateArr.join('%2C');
        qArr.push(populateQuery);
    }

    // query param ===========================
    let query = '';
    if (
        params.query != undefined &&
        params.query &&
        typeof params.query == 'string' &&
        params.query.length > 0
    ) {
        query = params.query;
        qArr.push(query);
    }

    let fullUrl = `${environment.API_URL}${url}${qArr.length > 0 ? '?' : ''}${qArr.join('&')}`;



    const promise = new Promise(async (resolve, reject) => {
        const result = await http
            .get(fullUrl)
            .toPromise()
            .then(
                (res) => {
                    const cleanRes = extractStrapiDataAttributes(
                        res,
                        populateArray
                    );


                    resolve(cleanRes);
                },
                (err) => {
                    reject(err);
                }
            );
        return result || [];
    });

    return promise;
}
