import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import {
    faCheck,
    faFlagCheckered,
    faPersonRunning,
    faPersonToDoor,
} from '@fortawesome/pro-duotone-svg-icons';

@Component({
    selector: 'app-side-suivi-course-menu',
    templateUrl: './side-suivi-course-menu.component.html',
    styleUrls: ['./side-suivi-course-menu.component.scss'],
})
export class SideSuiviCourseMenuComponent implements OnChanges {

    faCheck = faCheck;
    faPersonToDoor = faPersonToDoor;
    faPersonRunning = faPersonRunning;
    faFlagCheckered = faFlagCheckered;


    @Input() modules;
    @Input() largeMode;
    @Input() currentModuleId;
    @Input() currentActId;
    @Input() totalDone;
    @Input() totalPercent;
    @Input() totalActivity;

    @Output() activityChange = new EventEmitter();
    @Output() pourcentCal = new EventEmitter();


    constructor() { }
    ngOnChanges(changes: SimpleChanges): void {
        if (this.modules && this.currentActId && this.currentModuleId && changes && (changes.currentActId || changes.currentModuleId)) {
            const currentModule = this.modules.find(el => el.module.id == this.currentModuleId)
            if (currentModule) {
                const currentActivite = currentModule.module.Activites.find(el => el.activite.id == this.currentActId)
            }
            // this.calcProgressWidth()
        }
    }

    FormatDate(timesecond) {
        var date = new Date(0);
        date.setSeconds(timesecond);
        var timeString = date.toISOString().substr(11, 8);
        while (timeString[0] == '0' || timeString[0] == ':') {
            timeString = timeString.substring(1);
        }
        return timeString;
    }

    returnState(act) {
        if (act.done) {
            return '✅';
        } else if (act.done == false && act.isActive) {
            return '🔓';
        } else {
            return '🔒';
        }
    }

    ChangeActivityRequest(act: any, mod: any) {
        this.activityChange.emit({ act, mod });
    }
    pourcentCallRequest() {
        this.pourcentCal.emit();
    }

    minFormat(time) {
        return time > 60
            ? (time / 60).toFixed(2).toString().replace('.', 'h')
            : time + ' min';
    }

    returnProgressWidth(time, timeMin, timeMax) {
        const timeMinSeconds: number = timeMin * 60
        const timeMaxSeconds: number = timeMax * 60

        if (
            timeMax != null &&
            timeMax != 0 &&
            timeMin != null &&
            timeMin != 0
        ) {
            // range : time => min => max

            const perMin =
                time < timeMinSeconds
                    ? ((100 * time) / timeMinSeconds).toFixed(1)
                    : 100;


            const perMax =
                time < timeMaxSeconds
                    ? ((100 * time) / timeMaxSeconds).toFixed(1)
                    : 100;
            const minWidth = ((100 * timeMinSeconds) / timeMaxSeconds).toFixed(1);

            return { perMin: perMin, minWidth: minWidth, perMax: perMax };
        } else if (
            timeMinSeconds != null &&
            timeMinSeconds != 0 &&
            (timeMaxSeconds == null || timeMaxSeconds == 0)
        ) {

            const perMin =
                time < timeMinSeconds
                    ? ((100 * time) / timeMinSeconds).toFixed(0)
                    : 100;
            return { perMin: perMin, minWidth: null, perMax: null };
        } else if (
            timeMaxSeconds != null &&
            timeMaxSeconds != 0 &&
            (timeMinSeconds == null || timeMinSeconds == 0)
        ) {

            const percentMin =
                time < timeMaxSeconds
                    ? ((100 * time) / timeMaxSeconds).toFixed(0)
                    : 100;

            return { perMin: percentMin, minWidth: null, perMax: null };
        } else if (
            (timeMaxSeconds == null || timeMaxSeconds == 0) &&
            (timeMinSeconds == null || timeMinSeconds == 0)
        ) {
            // only time
            return { perMin: 0, minWidth: null, perMax: null };
        }
    }


}
