import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-text-area',
  templateUrl: './text-area.component.html',
  styleUrls: ['./text-area.component.scss']
})
export class TextAreaComponent implements OnInit {

  @Input() showFileInput: boolean;
  @Input() showTitleInput: boolean;
  @Input() shortTextInput: boolean;
  @Input() minChar: number;


  public titleInputContent: string = "";
  public textInputContent: string = "";
  public fileInputContent: string = "";
  public goodToGo = false;

  constructor() { }

  ngOnInit(): void {
    this.refreshGoodToGo()
  }

  refreshGoodToGo() {
    if (this.textInputContent.length == 0) this.goodToGo = false

    if ((this.minChar == null || this.minChar == undefined) && this.textInputContent.length > 0) this.goodToGo = true;

    if (this.minChar != null && this.minChar > 0 && this.textInputContent.length >= this.minChar) this.goodToGo = true


  }



}
