import { Component, OnInit } from '@angular/core';
import { RoleService } from './Role.service';
import { faMinus, faMagnifyingGlassPlus } from '@fortawesome/pro-solid-svg-icons';
@Component({
  selector: 'app-teacher',
  templateUrl: './teacher.component.html',
  styleUrls: ['./teacher.component.scss']
})
export class TeacherComponent implements OnInit {
  faMinus = faMinus;
  faMagnifyingGlassPlus = faMagnifyingGlassPlus;
  public data: any;
  public theme: any = JSON.parse(localStorage.getItem('Theme')).theme;

  constructor(private content: RoleService) {
    this.content.getRoles().then((data: any) => {

      // data = data[0];

      var id_teacher = data.roles.find((el) => el.type.includes("enseignant")).id;
      this.content.getRole(id_teacher).then((teacher: any) => {
        // teacher = teacher[0];

        teacher.forEach((el) => {
          el.selected = false;
        })
        this.data = teacher;


      })
    });
  }

  ngOnInit(): void {
  }

  SeeMore(i) {
    this.data[i].selected = !this.data[i].selected;
  }

}
