import { Component, Input, OnInit, Output, EventEmitter, OnChanges, SimpleChanges, AfterViewInit, ViewChild, TemplateRef } from '@angular/core';
import {
    faArrowsMaximize,
    faArrowsMinimize,
    faChevronLeft,
    faChevronRight,
} from '@fortawesome/pro-solid-svg-icons';
import Glide, { } from '@glidejs/glide/dist/glide.modular.esm'

@Component({
    selector: 'app-suivi-activite',
    templateUrl: './suivi-activite.component.html',
    styleUrls: ['./suivi-activite.component.scss'],
})
export class SuiviActiviteComponent implements OnInit, OnChanges {

    // @ViewChild(NgxGlideComponent, { static: false }) ngxGlide: NgxGlideComponent;

    @ViewChild('chevronLeft', { read: TemplateRef }) chevronLeftTemplate: TemplateRef<any>;
    @ViewChild('chevronRight', { read: TemplateRef }) chevronRightTemplate: TemplateRef<any>;
    // @ViewChild('', { static: false }) rightChevronRef: any;


    @Input() data: any;
    @Input() moduleId: any;
    @Input() lgMode: boolean;
    @Input() startIndex: string;
    @Output() activityNav = new EventEmitter();
    @Output() activityContentNav = new EventEmitter();
    @Output() toggleFullScreen = new EventEmitter();

    public currentContentindex = 0;
    private reloadingSlider: boolean = false;
    private navLocked: boolean = false;
    private myGlide: Glide;

    faChevronLeft = faChevronLeft;
    faArrowsMaximize = faArrowsMaximize;
    faArrowsMinimize = faArrowsMinimize;
    faChevronRight = faChevronRight;

    constructor() {
        this.createGlide()
    }



    ngOnChanges(changes: SimpleChanges): void {
        // console.log('SUIVI ACTIVITE CONTENT CHANGE ', changes);

        // if (changes.moduleId || changes.data && changes.data.previousValue.activite.id != changes.data.currentValue.activite.id) {
        //     // reset du currentContentindex => a remplacer dés que le current content sera save en BO
        //     // this.currentContentindex = 0
        //     this.initContentIndex()
        // }

        if (changes && changes.data != undefined) {
            this.resetSlider()
        }

        if (changes && changes.startIndex != undefined && changes.startIndex.currentValue != undefined) {
            // this.resetSlider()
            this.initContentIndex(changes.startIndex.currentValue)
        }



    }

    ngOnInit(): void {
        this.initContentIndex(null)
    }

    initContentIndex(value) {
        console.log('value', value);
        console.log('initContentIndex startIndex', this.startIndex);
        const index = value ? value : this.startIndex
        if (index && index.length > 0) {
            const idx = parseInt(index)
            this.currentContentindex = idx
        } else this.currentContentindex = 0
    }


    contentNavHandler(direction: string, indexToGo: number) {
        const contentLength = this.data ? this.data.activite.content.length : 0

        if (this.navLocked) return;

        if (direction && (direction == '>' || direction == '<')) {

            this.myGlide.go(direction)

            this.navLocked = true;
            const timeout = setTimeout(() => { this.navLocked = false; }, 300)

            const nextindx = this.currentContentindex + (direction == '<' ? -1 : 1);
            this.currentContentindex = nextindx;
            this.navContentUpdateRequest(nextindx)


        } else if (indexToGo != null && indexToGo <= contentLength) {
            const arg = '=' + indexToGo.toString()
            this.myGlide.go(arg)

            this.navLocked = true;
            const timeout = setTimeout(() => { this.navLocked = false; }, 300)

            this.currentContentindex = indexToGo;
            this.navContentUpdateRequest(indexToGo)
        }
    }


    navContentUpdateRequest(newIndex) {
        const obj = { index: newIndex, actId: (this.data && this.data.activite && this.data.activite.id) ? this.data.activite.id : null, moduleId: this.moduleId ? this.moduleId : null }
        this.activityContentNav.emit(obj);
    }

    navActivityRequest(way) {
        this.activityNav.emit(way);
    }

    toggleFullScreenRequest() {
        this.toggleFullScreen.emit(null);
        this.resetSlider()
    }

    resetSlider() {
        if (this.myGlide) {
            this.myGlide.destroy()
        }
        this.createGlide()
    }

    createGlide() {

        console.log('createGlide', this.currentContentindex);
        setTimeout(() => {
            this.myGlide = new Glide('.glide')
            this.myGlide.mount()
            if (this.currentContentindex && this.currentContentindex > 0) {
                console.log('this.currentContentindex', this.currentContentindex);
                const arg = '=' + this.currentContentindex.toString()
                this.myGlide.go(arg)
            }
        }, 300) // 300ms = anim duration + 100 => .baseTransition 
    }


}
