import { Component, Input, OnInit } from '@angular/core';
import { NotifierService } from 'angular-notifier';


@Component({
  selector: 'app-eval-txt-a-trous',
  templateUrl: './eval-txt-a-trous.component.html',
  styleUrls: ['./eval-txt-a-trous.component.scss']
})
export class EvalTxtATrousComponent implements OnInit {


  constructor(public notifier: NotifierService) { }

  @Input() data: any;

  public formattedText: Array<{ isInput: boolean, value: string }> = []
  public formatError = false;

  ngOnInit(): void {
    this.formattedText = this.initTxtATrous()
  }

  initTxtATrous() {
    let txt = null;

    if (this.data && Array.isArray(this.data) && this.data.length > 0 && this.data[0].TexteATrous)
      txt = this.data[0].TexteATrous

    if (txt) {

      const formattedText: Array<{ isInput: boolean, value: string }> = []

      const firstSplit = txt.split('[')

      if (firstSplit[0].includes(']') || !firstSplit[1].includes(']')) {
        if (!this.formatError) this.notifier.notify("error", "Format incorrect : texte-a-trous ") // anti spam
        this.formatError = true;
        return
      }

      firstSplit.forEach(txtPiece => {
        if (txtPiece.includes(']')) {
          const secondSplit = txtPiece.split(']')
          

          if (secondSplit.length == 2) {
            formattedText.push({ isInput: true, value: secondSplit[0] })
            formattedText.push({ isInput: false, value: secondSplit[1] })
          } else {
            if (!this.formatError) this.notifier.notify("error", "Format incorrect : texte-a-trous ") // anti spam
            this.formatError = true;
            return
          }
        } else formattedText.push({ isInput: false, value: txtPiece })
      })

      return formattedText;
    } else return null
  }

}
