import { Component, OnInit } from '@angular/core';
import { UserService } from '../../../services/user.service';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { environment } from '../../../../environments/environment';
import { ProfileService } from './profile.service';
import { NotifierService } from 'angular-notifier';

import * as dayjs from 'dayjs';
import * as relativeTime from 'dayjs/plugin/relativeTime';
dayjs.extend(relativeTime);
import 'dayjs/locale/fr';
@Component({
    selector: 'app-profile',
    templateUrl: './profile.component.html',
    styleUrls: ['./profile.component.scss'],
})
export class ProfileComponent implements OnInit {
    private API_URL = environment.API_URL;
    isLoggedIn = this.userService.isLoggedIn();
    userData = this.userService.getUser();
    userOrders: any;
    public theme: any;
    public data: any;
    public selected: any;
    notifier: NotifierService;
    public load = false;
    public formation: any;
    recupLog : any;
    public dashboard : any;
    public activityLog : any;
    public startFormation = false;
    public sessionId : any;
    constructor(
        private userService: UserService,
        private http: HttpClient,
        private route: ActivatedRoute,
        private profileService: ProfileService,
        public router: Router,
        notifierService: NotifierService
    ) {
        this.theme = JSON.parse(localStorage.getItem('Theme')).theme;
        this.profileService.Image().then((user) => {
            this.userData["avatar"] = user.avatar.url
        })
        this.notifier = notifierService;
        this.profileService.getData().then((data: any) => {
         data.profilLists[5].publier = true
            for (let i = 0; i < data.profilLists.length; i++) {
                data.profilLists[i]['selected'] = false;
            }

            if (
                this.router.url.includes('#') &&
                data.profilLists.find((el) =>
                    el.menuLink.includes(this.router.url.split('#')[1])
                ).publier
            ) {
                var findkey = this.router.url.split('#')[1];
                data.profilLists[
                    Object.keys(data.profilLists).find(
                        (key) => data.profilLists[key].menuLink === findkey
                    )
                ]['selected'] = true;
            } else {
                data.profilLists[
                    Object.keys(data.profilLists).find(
                        (key) => data.profilLists[key].menuLink === 'overview'
                    )
                ]['selected'] = true;
            }
            this.data = data;

            this.selected = this.data.profilLists.find(
                (el) => el.selected == true
            );

        });


    }
    InitLog(e){
        this.recupLog = e;
    }

    async ngOnInit() {
        let save = await this.profileService.LatestCourse().then((data) => { return data });
        (save["dashboard"]) ? this.dashboard = save["dashboard"] : null;
        (save["activityLog"]) ? this.activityLog = save["activityLog"] : null;
        dayjs.locale('fr')
        const date = dayjs(new Date()).unix() * 1000;
        switch (Object.keys(save)[0]) {
         
            case 'sessionStart':
                let dateStart = new Date(save["sessionStart"].date_start).getTime();
                this.formation = save["sessionStart"].formation;
                this.sessionId = save["sessionStart"].id

                if (date < dateStart) {
                    this.formation["date_start"] = save["sessionStart"].date_start;

                }
                break;
            case 'sessionAny':
                this.formation = false;

                break
            case 'sessionProgress':
                this.formation = save["sessionProgress"].session.formation;
                this.sessionId = save["sessionProgress"].session.id
                break;

        }
        this.load = true;
    }

    notifyError() {
        this.notifier.notify(
            'info',
            'Cette fonctionnalité arrive bientôt ! 👍'
        );
    }

    toggleNavbar(e) {
        if (!this.data.profilLists[e].publier) {
            return;
        }

        for (let i = 0; i < this.data.profilLists.length; i++) {
            i == e
                ? (this.data.profilLists[i]['selected'] = true)
                : (this.data.profilLists[i]['selected'] = false);
        }
        this.findSelected();
    }

    logout() {
        this.userService.logOut();
        this.notifier.notify('success', 'On espère vous revoir 👋');
        this.router.navigate(['/']).then(() => {
            window.location.reload();
        });
    }

    findSelected() {
        this.selected = this.data.profilLists.find((el) => el.selected == true);
        this.router.navigate(['/dashboard'], { fragment: this.selected.menuLink })
    }
}
