import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-eval-vf',
  templateUrl: './eval-vf.component.html',
  styleUrls: ['./eval-vf.component.scss']
})
export class EvalVfComponent implements OnInit {
  @Input() data: any;

  // key = bonneReponse || mauvaiseReponse
  public selectedAnswerId = ""

  constructor() { }

  ngOnInit(): void {
    if (this.data) {
      this.initResponse(this.data)
    }

  }

  initResponse(content) {
    if (content.bonneReponse && content.mauvaiseReponse) {
      content["selected"] = ""
    }
  }

  toggleResponse(id) {
    if (this.data && this.data.selected != undefined) {
      this.data.selected = id
    } else {
      this.initResponse(this.data)
      this.data.selected = id
    }
  }

}
