import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../src/environments/environment';
import { fetchStrapiFormat } from '../../../helpers/functions';

@Injectable({
    providedIn: 'root',
})
export class FooterService {
    private API_URL = environment.API_URL;

    constructor(private http: HttpClient) {}

    getData() {
        // let url = `${this.API_URL}/footer`;
        return fetchStrapiFormat(this.http, {
            url: '/footer',
            populate: ['logo', 'widgets', 'socials', 'widgets.allItems','certifications'],
        });
        // this.http.get(url);
    }
}
