import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { fetchStrapiFormat } from 'src/app/helpers/functions';

@Injectable({
    providedIn: 'root'
})
export class RoleService {

    private API_URL= environment.API_URL;

    constructor(
        private http : HttpClient
    ) {}

    getRoles(){
        return fetchStrapiFormat(this.http, { url: '/users-permissions/roles'});

    }

    getRole(i){
        return fetchStrapiFormat(this.http, { url: '/users-permissions/byrole/'+i});

    }

}