import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { UserService } from '../../../services/user.service';
import { FormBuilder, Validators } from '@angular/forms';
import { environment } from '../../../../environments/environment';
import { NotifierService } from 'angular-notifier';
import { ProfileUpdateService } from './profile-update.service';
import { faCircleArrowLeft } from '@fortawesome/pro-duotone-svg-icons';
import { faAlarmPlus, faAlarmClock, faCoins, faCreditCard } from '@fortawesome/pro-regular-svg-icons';
import { faPencil, faKey, faCartShopping, faGears, faInboxOut, faQuestion } from '@fortawesome/pro-duotone-svg-icons';
import { AnimationOptions } from 'ngx-lottie';

@Component({
    selector: 'app-profile-update',
    templateUrl: './profile-update.component.html',
    styleUrls: ['./profile-update.component.scss']
})
export class ProfileUpdateComponent implements OnInit {
    faQuestion = faQuestion;
    faGears = faGears;
    faCartShopping = faCartShopping;
    faKey = faKey;
    faPencil = faPencil;
    faAlarmPlus = faAlarmPlus;
    faAlarmClock = faAlarmClock;
    faCoins = faCoins;
    faInboxOut = faInboxOut;
    faCreditCard = faCreditCard;
    faCircleArrowLeft = faCircleArrowLeft;
    private readonly notifier: NotifierService;
    private API_URL = environment.API_URL;
    isLoggedIn = this.userService.isLoggedIn();
    public Editprofil = false;
    public Editpassword = false;
    public Editachats = false;
    public Editparams = false;
    orders: any;
    public load = false;
    @Input() formation;
    @Input() startFormation;
    @Input() user;
    @Input() sessionId;
    page: number = 1;
    change = false;
    public theme = JSON.parse(localStorage.getItem('Theme')).theme;
    public ExistNewsLetter : any;
    updateForm = this.formBuilder.group({
        username: ['', Validators.required],
        // email: [''],
        phoneNumber: ['', Validators.required],
        twitter: [''],
        facebook: [''],
        linkedin: [''],
        instagram: [''],
        biography: [''],
        Profession: ['']
    });

    updatePasswordForm = this.formBuilder.group({
        password: ['', Validators.required],
        passwordCheck: ['', Validators.required],
    })

    options: AnimationOptions = {
        path: 'https://assets4.lottiefiles.com/packages/lf20_pHl01w.json',
    };

    get p() {
        return this.updatePasswordForm.controls;
    }

    get f() {
        return this.updateForm.controls;
    }
   

    constructor(
        private formBuilder: FormBuilder,
        private userService: UserService,
        private http: HttpClient,
        notifierService: NotifierService,
        private profileUpdateService: ProfileUpdateService
    ) {
        this.notifier = notifierService;
    }

    async ngOnInit() {
        this.orders = await this.profileUpdateService.getOrdersUser().then((res) => {
            return res;
        })

    }
    ChangePassword() {
        let checkData = this.updatePasswordForm.value;
        const { password, passwordCheck } = checkData;
        if (password != passwordCheck) {
            return this.notifier.notify('error', 'Mot de passes différents');
        }
        this.profileUpdateService
            .UpdateInfoProfil({
                data: {
                    password

                },
            }).subscribe((res) => {
                if (res) {
                    this.updatePasswordForm.reset();
                    this.notifier.notify('success', 'Mot de passe modifié avec succès!');

                } else {
                    this.notifier.notify('error', 'Votre mot de passe n\'a pas été édité 🤔');

                }

            });
    }
    changeAvatar(e) {
        let form = new FormData();
        form.append("files", e, e.name)
        form.append("fileInfo", JSON.stringify({ name: e.name, caption: e.name, alternativeText: e.name }))

        if (e.type.includes("png") || e.type.includes("jpeg") || e.type.includes("jpg")) {
            if (e.size <= 5000000) {
                this.profileUpdateService.UploadAvatar(form).subscribe((data) => {

                    this.profileUpdateService
                        .UpdateInfoProfil({
                            data: { avatar: data[0].id },
                        }).subscribe((success) => {
                            this.user.avatar = data[0].url;
                            this.notifier.notify('success', 'Avatar modifié avec succès !');
                        },
                        (err) => {
                            this.notifier.notify('error', 'Une erreur est survenue');
                        })
                },
                    (err) => {
                        console.log(err)
                    })
            } else {
                this.notifier.notify('error', 'Image trop volumineuse 🤷‍♂️');

            }
        } else {
            this.notifier.notify('error', 'Le format de ce fichier n\'est pas accepté 🤷‍♂️');
        }
    }
   async changeStatus(e){
       this.change = true;
        this.ExistNewsLetter = !this.ExistNewsLetter;
        const update =  await this.profileUpdateService.UpdateNewsLetter({ data : {email :this.user.email, authorize : this.ExistNewsLetter}}).toPromise();
    (this.ExistNewsLetter) ?  this.notifier.notify('success', 'Félicitation ! Vous êtes désormais abonné') : this.notifier.notify('info', 'Vous ne recevrez plus notre NewsLetter !');
    this.change = false;
}
    async SeeMyOrders() {
        this.Editachats = true;
    }

    compareDate(created, published) {
        return new Date(created) != new Date(published);
    }

    Back() {
        this.Editprofil = false;
        this.Editpassword = false;
        this.Editachats = false;
        this.Editparams = false;
    }
    SendInvoice(order){
        this.user["adresse"] = this.user.biography;
      const data = {user : this.user , order : order}

        this.profileUpdateService.SendInvoice({data : data}).subscribe((res)=>{
           if(res){
            this.notifier.notify('info', 'Votre facture vous a été adressée à ' + this.user.email);

           }else{
            this.notifier.notify('error', 'Erreur dans l\'envoi');

           }

        })
    }

    onSubmit() {
        let checkData = this.updateForm.value;
        const {  username, phoneNumber, Profession, biography, facebook, linkedin, twitter, instagram } = checkData;

        this.profileUpdateService
            .UpdateInfoProfil({
                data: {
                    username, phoneNumber, Profession, biography, facebook, linkedin, twitter, instagram

                },
            }).subscribe((res) => {
                if (res) {
                    var lms_user = JSON.parse(localStorage.getItem('lms_user'));
                    lms_user.user.Profession = Profession;
                    lms_user.user.username = username;
                    lms_user.user.phoneNumber = phoneNumber;
                    lms_user.user.biography = biography;
                    lms_user.user.facebook = facebook;
                    lms_user.user.linkedin = linkedin;
                    lms_user.user.twitter = twitter;
                    lms_user.user.instagram = instagram;
                    localStorage.setItem('lms_user', JSON.stringify(lms_user));
                    this.notifier.notify('success', 'Profil modifié avec succès!');
                } else {
                    this.notifier.notify('error', 'Votre profil n\'a pas été édité 🤔');
                }

            });


    }

   async notifyIncomingFeature() {
        this.ExistNewsLetter =  await this.profileUpdateService.ExistNewsLetter({ data : {email :this.user.email}}).toPromise();
        this.Editparams = true;

    }

}