export class ProfileAuthenticationService {
    private API_URL= environment.API_URL;

    constructor(
        private http : HttpClient
    ) {
        // this.theme = JSON.parse(localStorage.getItem('Theme')).theme;
    }

    getData(){
        return fetchStrapiFormat(this.http, { url: '/nav-bar-login', populate : ['loginLists ']});

    }
}