import { Component, Input, OnInit } from '@angular/core';
import { CommunauteService } from 'src/app/components/common/communaute/communaute.service';
import {
  faPen
} from '@fortawesome/pro-regular-svg-icons';
import {
  faCalendar
} from '@fortawesome/pro-duotone-svg-icons';
import { faCircleUser, faHeart } from '@fortawesome/pro-solid-svg-icons';
import { AnimationOptions } from 'ngx-lottie';
import { Router } from '@angular/router';
@Component({
  selector: 'app-forum',
  templateUrl: './forum.component.html',
  styleUrls: ['./forum.component.scss']
})
export class ForumComponent implements OnInit {
  @Input() slug;
  @Input() type;
  @Input() forum;
  @Input() id;
  @Input() user;

  faCalendar = faCalendar;
  faPen = faPen;
  faCircleUser = faCircleUser;
  faHeart = faHeart;
  post: any;
  pforum: number = 1;
  public theme =  JSON.parse(localStorage.getItem('Theme')).theme;

  constructor(private communauteService: CommunauteService, public router: Router) {
  
  }
  options: AnimationOptions = {
    path: 'https://assets9.lottiefiles.com/packages/lf20_dbaflzo1.json',
  };

  async ngOnInit() {
 
    (this.type && this.type == "forum" && this.id == null) ? this.forum = await this.communauteService.getForumByFormation(this.slug).toPromise() : null;

    (this.id) ? this.post = await this.communauteService.getQandAByFormation(this.id, this.slug).toPromise() : null;

  }
}
