import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';

let users = JSON.parse(localStorage.getItem('lms_user') || '{}');

@Injectable({
    providedIn: 'root',
})
export class UserService {
    private currentUserSubject: BehaviorSubject<any>;
    public currentUser: Observable<any>;

    constructor(private http: HttpClient, private router: Router) {
        
        const currentUserfind = JSON.parse(localStorage.getItem('lms_user') || '{}');
        
        this.currentUserSubject  =  new BehaviorSubject<any>(currentUserfind);
        this.currentUser = this.currentUserSubject.asObservable();

    }

    public get currentUserValue(): any {
        if(this.currentUserSubject == undefined) {
            return null as any;
        } else {
            return this.currentUserSubject.value;
        }
        
    }

    isLoggedIn() {
        return users && users.jwt
    }

    getUser(){
        
        return users && users.user
    }

    logOut(){
        localStorage.removeItem("lms_user");
    }


}