import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot } from '@angular/router';
import { CoursesService } from '../components/common/courses/courses.service';

@Injectable({
  providedIn: 'root'
})
export class NoDataGuard implements CanActivate {

  constructor(
    private router: Router,
    private courseService: CoursesService,
  ) { }



  async canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot) {

    let data: any = await this.courseService.getCount().toPromise();

    if (data.count == 0) {

      if (route.url[0].path === "nos-formations" || route.url[0].path.includes("formation")) {
        this.router.navigate(['/']);
      }
      return true;
    } else {
      return true;
    }




  }

}
