import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SpinnerHandlerService } from '../spinner/spinner-handler.service';

@Component({
  selector: 'app-page-loader',
  templateUrl: './page-loader.component.html',
  styleUrls: ['./page-loader.component.scss']
})
export class PageLoaderComponent implements OnInit {
  public pageLoaderActive: boolean = true;

  constructor(
      public spinnerHandler: SpinnerHandlerService,
      public router: Router
  ) {
      this.spinnerHandler.showSpinner.subscribe(this.showSpinner.bind(this));
  }

  showSpinner = (state: boolean): void => {
      state ? document.body.style.overflowY = "hidden" : document.body.style.overflowY = "scroll"
      this.pageLoaderActive = state;
  };

  ngOnInit(): void { }

}
