import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { faMoon, faSun, } from '@fortawesome/pro-duotone-svg-icons';

@Component({
    selector: 'app-dark-mode-switch',
    templateUrl: './dark-mode-switch.component.html',
    styleUrls: ['./dark-mode-switch.component.scss'],
})
export class DarkModeSwitchComponent implements OnInit {
    faSun = faSun;
    faMoon = faMoon;
    ref: ElementRef;

    @Input() lg: boolean;
    public darkMode: boolean;

    constructor() { }

    ngOnInit(): void {
       this.darkMode = JSON.parse(localStorage.getItem('darkMode'));

        if (this.darkMode != undefined && typeof this.darkMode == 'boolean') {

            this.darkMode 
                ? document.body.classList.add('dark') 
                : document.body.classList.remove('dark')
            ;
        } else {
            this.darkMode = false;
            localStorage.setItem('darkMode', this.darkMode.toString());
        }
    }

    toggleDarkMode() {
        const newMode = !this.darkMode;
        document.body.classList.toggle('dark');
        localStorage.setItem('darkMode', newMode.toString());
        this.darkMode = newMode;
    }
}
