import { Component, Input, OnInit } from '@angular/core';
import { Options } from "@angular-slider/ngx-slider";

@Component({
  selector: 'app-eval-graduation',
  templateUrl: './eval-graduation.component.html',
  styleUrls: ['./eval-graduation.component.scss']
})
export class EvalGraduationComponent implements OnInit {

  constructor() { }

  @Input() data: any;

  public value: number = 0;
  public options: Options = {};

  initRangeInput(data) {
    const newOptions = {
      floor: 0,
      ceil: 10,
      step: 1,
      showTicks: true
    }
    let midValue = 0

    if (data != undefined && data.note_max !== undefined && data.note_min !== undefined) {
      if (data.note_min) newOptions['floor'] = parseInt(data.note_min)
      if (data.note_max) {
        const ceil = parseInt(data.note_max)
        newOptions['ceil'] = ceil
        const ceil4midvalue = ceil - (newOptions['floor'] ? newOptions['floor'] : 0)
        midValue = parseInt((ceil4midvalue / 2).toFixed(0)) + (newOptions['floor'] ? newOptions['floor'] : 0)
      }
    }
    this.value = midValue
    this.options = newOptions


  }
  
  ngOnInit(): void {
    this.initRangeInput(this.data)
  }

}
