import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { jsPDF } from 'jspdf';
import { environment } from 'src/environments/environment';
import { UserService } from './user.service';
import * as dayjs from 'dayjs';
import * as relativeTime from 'dayjs/plugin/relativeTime';
dayjs.extend(relativeTime);
import 'dayjs/locale/fr';

@Injectable({
  providedIn: 'root'
})
export class ExportPDFService {
  private API_URL = environment.API_URL;
  private logo : any;

  constructor(private http: HttpClient, private userService: UserService
  ) {
    this.getBase64ImageFromUrl(JSON.parse(localStorage.getItem('Theme'))["logo"].url).then(result =>{ 
      this.logo = result
    })
  .catch(err => console.error(err));
   }

 async getBase64ImageFromUrl(imageUrl) {
    var res = await fetch(imageUrl);
    var blob = await res.blob();
  
    return new Promise((resolve, reject) => {
      var reader  = new FileReader();
      reader.addEventListener("load", function () {
          resolve(reader.result);
      }, false);
  
      reader.onerror = () => {
        return reject(this);
      };
      reader.readAsDataURL(blob);
    })
  }

  exportPDF(dataPdf) {

    const user = this.userService.getUser();
    const header = dataPdf.headers
    const data = dataPdf.data;
    const info_session = dataPdf.info_session
    const activityLog = dataPdf.activityLog
    const title = info_session.titre;
    const courseTime = this.FormatDate(activityLog.total_course_time)

    const img = this.logo;
    var pdf = new jsPDF('l', 'pt', 'a4');
    pdf.addImage(img, "PNG", 10, 10, 100, 80);
    dayjs.locale('fr')
    var html = "<table>";
    var th_head = "";
    var th_body = "";
    th_head += "<thead><tr>";
    header.forEach((head) => {

      th_head += "<th> &emsp; " + head + "  &emsp; </th>"
    })
    th_head += "</tr></thead>";
    html += th_head
    if (data && data.length > 0) {

      th_body += "<tbody>";
      data.forEach((d) => {
        th_body += "<tr>"
        th_body += "<td> &emsp; " + d.id + "  &emsp; </td>"
        th_body += "<td> &emsp; " + d.date_start + "  &emsp; </td>"
        th_body += "<td> &emsp; " + d.date_finish + "  &emsp; </td>"
        th_body += "<td> &emsp; " + d.pourcentage + "%  &emsp; </td>"
        th_body += "<td> &emsp; " + this.FormatDate(d.time) + "  &emsp; </td>"
        th_body += "</tr>"
      })
      th_body += "</tbody>";
    }
    
    html += th_body
    html += '</table>'
    pdf.setTextColor(11, 47, 170);
    pdf.setFontSize(22);
    pdf.text(title, 350, 60);
    pdf.setTextColor(0, 0, 0);
    pdf.setFont("italic");
    pdf.setFontSize(12);
    pdf.text(dayjs(info_session.date_start).format('DD/MM/YYYY') + " - " + dayjs(info_session.date_finish).format('DD/MM/YYYY'), 340, 75);
    pdf.setFont("normal");
    pdf.setFontSize(14);
    pdf.text('Apprenant : ' + user.username, 70, 125);
    pdf.text('Formation : ' + info_session.formation.title, 70, 145);
    pdf.text('Temps total : ' + courseTime, 70, 165);



    pdf.html(html,
      {
        x: 100,
        y: 200,
        width: 2000,
        windowWidth: 2000,
        margin: 5,
        callback: function (pdf) {
          pdf.save(title.replace(/ /g, "_") + "_" + user.username.replace(/ /g, "_") + ".pdf");
        }
      });

  }

  FormatDate(timesecond) {
    var date = new Date(0);
    date.setSeconds(timesecond);
    var timeString = date.toISOString().substr(11, 8);
    return timeString;
  }

}
