import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { NotifierService } from 'angular-notifier';
import { faStars, faBooks, faVolume } from '@fortawesome/pro-regular-svg-icons';
import { faArrowRightFromBracket, faFlagCheckered, faHeart, faCircleUser } from '@fortawesome/pro-solid-svg-icons';
import { faCirclePlay, faCalendar, faMessageDots } from '@fortawesome/pro-duotone-svg-icons';
import { CommunauteService } from '../communaute/communaute.service';
import { ModalService } from '../popup-form/popup-form.service';
@Component({
    selector: 'app-overview',
    templateUrl: './overview.component.html',
    styleUrls: ['./overview.component.scss'],
})
export class OverviewComponent implements OnInit {
    faMessageDots = faMessageDots;
    @Input() ProfilList;
    faCirclePlay = faCirclePlay;
    faCircleUser = faCircleUser;
    faFlagCheckered = faFlagCheckered
    faStars = faStars
    faBooks = faBooks 
    faVolume = faVolume
    faHeart = faHeart;
    faCalendar = faCalendar
    @Output() clickToggle = new EventEmitter<boolean>();
    public IndexProfil: any;
    private readonly notifier: NotifierService;
    public profilClean: any;
    public notPublish: any;
    public publish: any;
    public data: any;
    public community : any;
    public load = false;
    @Input() formation;
    @Input() startFormation;
    @Input() user;
    @Input() dashboard;
    @Input() sessionId;
    public theme = JSON.parse(localStorage.getItem('Theme')).theme;

    constructor(notifierService: NotifierService, private communauteService : CommunauteService, private modalService : ModalService
    ) {
        this.notifier = notifierService
       
    }
    RecupStat(){
        return (this.dashboard && this.dashboard['course']) ? this.dashboard : null;
    }
    async ngOnInit(): Promise<void> {
        var cleanList = [...this.ProfilList];

        for (var i = 0; i < cleanList.length; i++) {
            cleanList[i]['index'] = i;
        }
        this.community = await this.communauteService.getCommunityFormationPreview(this.formation.slug).toPromise()
        console.log('this.formation.slug', this.community)

        this.profilClean = cleanList.slice(1);
        this.IndexProfil = this.profilClean.find((el) => el.menuLink == "profil")
        this.publish = this.profilClean.filter((el) => el.publier == true);
        this.notPublish = this.profilClean.filter((el) => el.publier == false);
        this.load = true;

    }
    toggleNavbar(e) {
        this.clickToggle.emit(e);
    }
    notifyError() {
        this.notifier.notify('info', 'Cette fonctionnalité arrive bientôt ! 👍');
    }

    toggleWidget(i) {


        this.clickToggle.emit(i);

    }
    Pourcent(){
        return (this.dashboard && this.dashboard["course"]) ? Math.round((this.dashboard["course"]['total_done'] / this.dashboard["course"]['total_activities']) * 100) : 0;
    }
    FormatDate(timesecond) {
        var date = new Date(0);
        date.setSeconds(timesecond);
        var timeString = date.toISOString().substr(11, 8);
        return timeString;
    }

    // }

    AnalyseStatut(){
        if(!this.startFormation && this.dashboard && this.dashboard.course.total_done ==  this.dashboard.course.total_activities){
           return "Revoir"
        }else if(!this.startFormation &&  this.dashboard && this.dashboard.course.total_done !=  this.dashboard.course.total_activities){
            return "Reprendre"
        }else{
            return "Au travail !"
        }
      
    }
    modalAvisOpen(rateUser) {
        this.modalService.openAvisModal(rateUser);
      }
    async toggleRating(e) {
        if (e) {
     
                    if (!this.startFormation) {

                        const alreadyComment = this.community.avis.ratings.find((el) => el.user.id == this.user.id);
                        const comment = (alreadyComment) ? alreadyComment["comment"] : "";
                        const rate = (alreadyComment) ? { newRate: e, previousRate: alreadyComment["rate"] } : { newRate: e, previousRate: 0 };
                        const id = (alreadyComment) ? alreadyComment["id"] : 0;
                        const rateUser = { rate: rate, comment: comment, id: id, formation: this.formation.id, change: false };
                        
                        this.modalAvisOpen(rateUser);
                        // const index = this.data.ratings.findIndex((el)=> el.user.id == this.user.id);
                        // const ratesave =  this.modalService.watchRateUser();
                        // console.log('ratesave', ratesave)
                        // if(index){
                        //     this.community.avis.ratings[index].rate = ratesave["source"]["_value"]["rate"]["newRate"]
                        //     this.community.avis.ratings[index].comment = ratesave["source"]["_value"]["rate"]["newRate"]
                        // }else{
                        //     this.community.avis.ratings.push({rate : ratesave["source"]["_value"]["rate"]["newRate"], comment : ratesave["source"]["_value"]["rate"]["comment"]});

                        // }


                    } else {
                        this.notifier.notify("info", "Vous n\'avez pas commencé votre formation !")
                    }
               

          
        }

    }

}
