import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../src/environments/environment';
import { fetchStrapiFormat } from '../../../helpers/functions';

@Injectable({
    providedIn: 'root',
})
export class HomePageService {
    private API_URL = environment.API_URL;

    constructor(private http: HttpClient) {}

    getHome(){
        let url = `${this.API_URL}/accueil/order`;
        return this.http.get<any>(url);
    }

 
    getOrders() {
        return fetchStrapiFormat(this.http, { url: '/orders/count' });
    }

    getFormationsCount() {
        return fetchStrapiFormat(this.http, { url: '/formations/count' });
    }

    getRatings() {
        return fetchStrapiFormat(this.http, { url: '/ratings/count' });
    }


}
