import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute } from '@angular/router';
import { environment } from '../../../../../src/environments/environment';
import { BlogService } from '../../common/blog/blog.service';
import { fetchStrapiFormat } from 'src/app/helpers/functions';

@Component({
  selector: 'app-blog-details',
  templateUrl: './blog-details.component.html',
  styleUrls: ['./blog-details.component.scss']
})
export class BlogDetailsComponent implements OnInit {
  private API_URL= environment.API_URL;
  public slug: any;
  public data : any;
  term = '';
  public blogPostdata: any;
  public blogCategoriesdata: any;
  public blogTagsdata: any;

  constructor(
      private route: ActivatedRoute,
      private http: HttpClient,
  private content: BlogService
  ) {
      this.route.params.subscribe((param : any)=>{
     

          let ddd = fetchStrapiFormat(this.http, { url: '/blog-posts', filter : {slug :param.slug  }, populate : ['image','blog_category','blog_tag']});
          ddd.then(res => {
              this.data = res
          })
      });
  this.content.getData().then((blogPostdata: any) => {
          this.blogPostdata = blogPostdata;
      });
  this.content.getCategoriesData().then((blogCategoriesdata: any) => {
          this.blogCategoriesdata = blogCategoriesdata;
      });
  this.content.getTagsData().then((blogTagsdata: any) => {
          this.blogTagsdata = blogTagsdata;
      });
  }

  ngOnInit(): void {}

  classSearchApplied = false;
  togglSearcheClass() {
      this.classSearchApplied = !this.classSearchApplied;
  }


}
