import { Component, OnInit } from '@angular/core';
import { ModalService } from '../../common/popup-form/popup-form.service';
import { UserService } from 'src/app/services/user.service';
import { CoursesDetailsService } from '../../pages/courses-details/courses-details.service';
import { CommunauteService } from './communaute.service';
import { faBullhorn, faComments,faCowbellCirclePlus, faQuestion, faEye, faStar } from '@fortawesome/pro-duotone-svg-icons';
import { AnimationOptions } from 'ngx-lottie';
import { NotifierService } from 'angular-notifier';

@Component({
  selector: 'app-communaute',
  templateUrl: './communaute.component.html',
  styleUrls: ['./communaute.component.scss']
})
export class CommunauteComponent implements OnInit {
  faQuestion = faQuestion;
  faEye = faEye;
  faStar = faStar;
  public categsFormation: any;
  public load = false;
  public user = this.userService.getUser();
  buttonState: any;
  faBullhorn = faBullhorn;
  faCowbellCirclePlus = faCowbellCirclePlus;
  theme = JSON.parse(localStorage.getItem('Theme')).theme;
  faComments = faComments;
  options: AnimationOptions = {
    path: 'https://assets9.lottiefiles.com/packages/lf20_dbaflzo1.json',
  };
  private readonly notifier: NotifierService;


  constructor(
    private communauteService: CommunauteService,
    private userService: UserService,
    notifierService: NotifierService,

    private modalService: ModalService,
  ) {
    this.communauteService.Community().subscribe((res) => {
      this.categsFormation = res;
      this.load = true;
    })
    this.notifier = notifierService;

  }

  

  ngOnInit() {

  }


  FormateHour(e) {
    return Math.round(e / 60);
  }


  modalAvisOpen(rateUser) {
    this.modalService.openAvisModal(rateUser);
  }

  async toggleRating(e, formation) {
    if (formation.can) {
    const alreadyComment = (formation.ratings.length > 0 ) ? formation.ratings[0] : null;
        const comment = (alreadyComment) ? alreadyComment["comment"] : "";
    const rate = (alreadyComment) ? { newRate: e, previousRate: alreadyComment["rate"] } : { newRate: e, previousRate: 0 };
    const id = (alreadyComment) ? alreadyComment["id"] : 0;

    const rateUser = { rate: rate, comment: comment, id: id, formation: formation.id };
    this.modalAvisOpen(rateUser);
    }
    else {
      this.notifier.notify("info", "Vous n\'avez pas commencé votre formation !")
  }
  }
}
