import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import {
    faDownload,
    faFileExcel,
    faFilePdf,
    faFileCertificate,
    faClock,
    faFile,
    faHourglass,
    faCircleCheck
} from '@fortawesome/pro-duotone-svg-icons';
import { NotifierService } from 'angular-notifier';
import { ExportExcelService } from 'src/app/services/export-excel.service';
import { AnimationOptions } from 'ngx-lottie';
import { ExportPDFService } from 'src/app/services/export-pdf.service';

@Component({
    selector: 'app-files',
    templateUrl: './files.component.html',
    styleUrls: ['./files.component.scss'],
})
export class FilesComponent implements OnInit {
    public load: boolean = false;
    faFile = faFile;
    faHourglass = faHourglass;
    faCircleCheck = faCircleCheck;
    faDownload = faDownload;
    faClock = faClock;
    faFileCertificate = faFileCertificate;
    faFileExcel = faFileExcel;
    faFilePdf = faFilePdf;
    public selected: number = 2;
    @Input() activityLog;
    rapport: any;
    psessions : number = 1;
    pattestations : number = 1;
    dataForExcel = [];
    numberItem : any;
    @Input() user;
    @Output()  recupLog =  new EventEmitter<any>();
    @Input() recup: any;
    public theme = JSON.parse(localStorage.getItem('Theme')).theme;
    options: AnimationOptions = {
        path: 'https://assets4.lottiefiles.com/packages/lf20_QJbPLt.json',
    };

    notifier: NotifierService;

    constructor(
        public ete: ExportExcelService,
        notifierService: NotifierService,
        public pdf : ExportPDFService
    ) {
        this.notifier = notifierService;
    }

    FormatDate(timesecond) {
        var date = new Date(0);
        date.setSeconds(timesecond);
        var timeString = date.toISOString().substr(11, 8);
        return timeString;
    }

    async ngOnInit() {
        if (this.activityLog) {
            if(!this.recup){
             
                this.recup = await this.ete
                .SessionInfo({ data: Object.keys(this.activityLog) })
                .toPromise();
            
              this.recupLog.emit(this.recup);
            }
            this.numberItem = Object.keys(this.activityLog).length;
        }
        this.load = true;

    }


    Pourcent(el) {
        return this.activityLog[el]['total_done'] /
            this.activityLog[el]['total_activities']
            ? Math.round(
                  (this.activityLog[el]['total_done'] /
                      this.activityLog[el]['total_activities']) *
                      100
              )
            : 0;
    }

    DowloadAttestation(e,certification){
        const pourcent = this.Pourcent(e);
        if(pourcent < 100){
            this.notifier.notify("info","Vous devez finir votre formation avant de pouvoir télécharger votre attestation")
        }else{
            const formation = {title : this.recup[e].formation.title , category : this.recup[e].formation.formations_category.title, session :  this.recup[e].titre, date_start : this.recup[e].date_start, date_finish : this.recup[e].date_finish  }
            const data = {certification : {...certification}, user : {...this.user}, formation : formation}
          this.ete.CertificationSend({data : data}).subscribe((res)=>{
            if(res){
                this.notifier.notify('info', 'Votre certification vous a été adressée à ' + this.user.email);
    
               }else{
                this.notifier.notify('error', 'Erreur dans l\'envoi');
    
               }
          }, (error)=>{
        })
        this.notifier.notify('info', 'Votre certification vous a été adressée à ' + this.user.email);

        }
    }
    toggleList(id) {
        this.selected = id;
    }

    async exportToExcel(key) {
        this.rapport = await this.ete.RapportSession(key).toPromise();
        let rapport = [...this.rapport];

        rapport.forEach((row: any) => {
            this.dataForExcel.push(Object.values(row));
        });

        let reportData = {
            data: this.dataForExcel,
            activityLog: this.activityLog[key],
            info_session: this.recup[key],
            
            headers: [
                'Numéro',
                'Date de connexion',
                'Date de déconnexion',
                'Avancement',
                'Temps',
            ],
        };

        this.ete.exportExcel(reportData);
    }

   async exportToPDF(key){
        this.rapport = await this.ete.RapportSession(key).toPromise();
        
        let reportData = {
            data: this.rapport,
            activityLog: this.activityLog[key],
            info_session: this.recup[key],
            
            headers: [
                'Numéro',
                'Date de connexion',
                'Date de déconnexion',
                'Avancement',
                'Temps',
            ],
        };
        this.pdf.exportPDF(reportData);
    }
}
