import {
    Component,
    ElementRef,
    HostListener,
    OnInit,
    SimpleChange,
    ViewChild,
} from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, RouteConfigLoadEnd, Router } from '@angular/router';
import { environment } from '../../../../../src/environments/environment';
import { CoursesService } from '../../common/courses/courses.service';
import { NotifierService } from 'angular-notifier';
import { CourseCartService } from '../../../services/courseCart.service';
import { CoursesDetailsService } from './courses-details.service';
import { Course } from 'src/app/course';
import { faFileCertificate } from '@fortawesome/pro-solid-svg-icons';
import { faCoins, faGraduationCap } from '@fortawesome/pro-regular-svg-icons';
import {
    faUserGraduate,
    faPaperPlane,
    faRefresh,
    faCircleCheck,
    faDotCircle,
    faCirclePlay,
    faFlagCheckered,
    faAlarmClock,
    faBooks,
    faCalendarDays,
    faEarthAmericas,
    faCommentDots,
    faLockKeyhole,
    faMoneyBill1,
    faCircleDollar,
    faClock,
    faPhoneIncoming
} from '@fortawesome/pro-duotone-svg-icons';
import { ModalService } from '../../common/popup-form/popup-form.service';
import { UserService } from 'src/app/services/user.service';
@Component({
    selector: 'app-courses-details',
    templateUrl: './courses-details.component.html',
    styleUrls: ['./courses-details.component.scss'],
})
export class CoursesDetailsComponent implements OnInit {
    faClock = faClock;
    faMoneyBill1 = faMoneyBill1;
    faLockKeyhole = faLockKeyhole;
    faCommentDots = faCommentDots;
    faEarthAmericas = faEarthAmericas;
    faCalendarDays = faCalendarDays;
    faCircleDollar = faCircleDollar;
    faPhoneIncoming = faPhoneIncoming;
    faBooks = faBooks;
    faAlarmClock = faAlarmClock;
    faUserGraduate = faUserGraduate;
    faFileCertificate = faFileCertificate;
    faGraduationCap = faGraduationCap;
    faCoins = faCoins;
    faRefresh = faRefresh;
    faPaperPlane = faPaperPlane;
    faCircleCheck = faCircleCheck;
    faDotCircle = faDotCircle;
    faCirclePlay = faCirclePlay;
    faFlagCheckered = faFlagCheckered;
    public theme: any = JSON.parse(localStorage.getItem('Theme')).theme;
    public logo: any = JSON.parse(localStorage.getItem('Theme')).logo.url;

    public buttonState: any;
    public sessions: Array<any> = [];
    public slug: any;
    public data: any;
    public courseData: any;
    public suggest: any;
    private URL = environment.URL;
    private readonly notifier: NotifierService;
    private modules: Array<any> = [];
    cart: Array<any> = [];
    public creditOpen = false;
    public videoPlay = false;
    public load = false;
    public showCTA = false;
    public objectifs = "";
    public user = this.userService.getUser();
    eRef: any;

    constructor(
        private route: ActivatedRoute,
        private http: HttpClient,
        private router: Router,
        private content: CoursesService,
        private cartService: CourseCartService,
        private modalService: ModalService,
        private userService: UserService,
        private coursesDetailsService: CoursesDetailsService,
        notifierService: NotifierService
    ) {
        this.load = false;
        this.cart = this.getCart();
        this.route.params.subscribe((param: any) => {
            this.content.getData(param.slug).subscribe((res) => {
                const data = res[0];
                if (data) {

                    this.objectifs = data.objectifs ? data.objectifs : ""
                    this.showCTA = data.showCTA ? data.showCTA : null
                    this.sessions = (data.sessions) ? data.sessions : [];
                    this.coursesDetailsService
                        .getProgrammeExist(data.id)
                        .subscribe((res) => {
                            if (
                                res &&
                                res['Modules'] &&
                                Array.isArray(res['Modules']) &&
                                res['Modules'][0] &&
                                res['Modules'][0].module != undefined
                                // &&                             res['Modules'][0].module
                            ) {
                                this.modules = res['Modules'];
                            }

                            this.coursesDetailsService
                                .getRatingsForCourseDetail(data.id)
                                .subscribe((res: any) => {
                                    data["ratings"] = res.ratings;
                                    data["result_final"] = res.result_final;
                                    data["totalRatings"] = res.totalRatings;

                                    const starAverage = [...res.starAverage];
                                    const starCount = [...res.starCount];

                                    data['starAverage'] = starAverage;
                                    data['starCount'] = starCount;
                                 
                                    if(data.formations_category){
                                        this.coursesDetailsService
                                        .getSuggestion(data.formations_category.id)
                                        .subscribe((res) => {
                                            if (res) this.suggest = res;

                                        });
                                    }
                             



                                });
                            (!this.user) ? this.load = true : null;
                        });
                } else {
                    this.router.navigate(['**'])
                }
                this.data = data;
                if (this.user && this.user.id) {
                    this.coursesDetailsService.getDetailCourse(this.data.id).subscribe((back) => {
                        this.buttonState = back;
                        this.load = true;

                    });
                }

                // this.coursesDetailsService
                //     .getTeacherDetails(data.enseignant.id)
                //     .subscribe((res) => {
                //         if (res) data.enseignant['details'] = res;
                //     });
            });
        });

        this.notifier = notifierService;
    }

    CheckNaN(e) {
        return isNaN(e) ? 0 : e;
    }
    toggleVideo() {
        if (
            !this.videoPlay &&
            this.data.video_media &&
            this.data.video_media.url
        )
            this.videoPlay = true;
    }

    FormateHour(e) {
        return Math.round(e / 60);
    }

    openModal() {

        this.modalService.openProspectModal();
    }

    openContactModal() {

        this.modalService.openEmailModal();
    }


    UseCredit(content) {
        this.addToCart(content);
        this.openModal();
    }


    getCart() {
        const cartTotal = this.cartService.getItems();
        return cartTotal;
    }

    isInCart(id) {
        const cart = this.cart.findIndex((el) => el.id == id);
        return cart != -1;
    }



    addToCart(course: Course) {
        this.cartService.addToCart(course);
        this.notifier.notify(
            'success',
            'Cette formation a été ajoutée au panier !'
        );
    }
    removeFromCart(course: Course) {
        this.cartService.deleteFromCart(course.id);

        this.notifier.notify(
            'info',
            'Cette formation a été rétirée du panier !'
        );
    }

    AvisSelect(e) {
        this.toggleRating(e);
    }

    modalAvisOpen(rateUser) {
        this.modalService.openAvisModal(rateUser);
    }

    async toggleRating(e) {
        if (e) {
            if (this.user && this.user.id != undefined) {

                if (this.buttonState && this.buttonState.etat) {
                    if (this.buttonState.start && this.buttonState.start != false) {

                        const alreadyComment = this.data.ratings.find((el) => el.user.id == this.user.id);
                        const comment = (alreadyComment) ? alreadyComment["comment"] : "";
                        const rate = (alreadyComment) ? { newRate: e, previousRate: alreadyComment["rate"] } : { newRate: e, previousRate: 0 };
                        const id = (alreadyComment) ? alreadyComment["id"] : 0;
                        const rateUser = { rate: rate, comment: comment, id: id, formation: this.data.id, change: false };
                        this.modalAvisOpen(rateUser);
              

                    } else {
                        this.notifier.notify("info", "Vous n\'avez pas commencé votre formation !")
                    }
                } else {
                    this.UseCredit(this.data);
                    this.notifier.notify("info", "Inscrivez vous à cette formation pour pouvoir laisser votre avis ⭐ ");
                }

            } else {
                this.UseCredit(this.data);
                this.notifier.notify("info", "Inscrivez vous à cette formation pour pouvoir laisser votre avis ⭐");
            }
        }

    }

    ngOnInit() {

    }
}
