import { PipeTransform, Pipe } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { DomSanitizer } from '@angular/platform-browser';
import { Observable } from "rxjs/internal/Observable";


@Pipe({ name: "image" })
export class ImagePipe implements PipeTransform {
  defaultImage = JSON.parse(localStorage.getItem('Theme'));

  constructor(
    private http: HttpClient,
    private _sanitizer: DomSanitizer
  ) { }


  /**
   * This pipe just check if image url exist before displaying them and returns image source or default image source if not found
   * @param url 
   * @returns url:string
   */
  async transform(url: string) {
    if (url === undefined) return this.defaultImage.default_avatar.url;

    try {
      new URL(url);
      return url;
    } catch (error) {
      return this.defaultImage.default_avatar.url;
    }

  }
}