import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../src/environments/environment';
import { fetchStrapiFormat } from '../../../helpers/functions';
@Injectable({
    providedIn: 'root'
})
export class ContactUsService {

    private API_URL= environment.API_URL;

    constructor(
        private http : HttpClient
    ) {}

    getData(){
        return fetchStrapiFormat(this.http, { url: '/contact-page', populate: ['contactinfo'] });
    
    }

    getDataNav(){
    
        return fetchStrapiFormat(this.http, { url: '/nav-bar-contact', populate: ['contactLists'] });
 
    }
}