import { Component, OnInit, Input } from '@angular/core';
import { NotifierService } from 'angular-notifier';
import { CourseCartService } from '../../../../services/courseCart.service';
import { Course } from 'src/app/course';
import {
    faTimer,
    faBookOpenCover,
    faScreenUsers,
} from '@fortawesome/pro-solid-svg-icons';
import { extractStrapiDataAttributes } from '../../../../helpers/functions';
import { CoursesService } from 'src/app/components/common/courses/courses.service';
import { ModalService } from '../../../common/popup-form/popup-form.service'
import { AnimationOptions } from 'ngx-lottie';

@Component({
    selector: 'app-homepage-banner',
    templateUrl: './homepage-banner.component.html',
    styleUrls: ['./homepage-banner.component.scss'],
})
export class HomepageBannerComponent implements OnInit {
    faTimer = faTimer;
    faBookOpenCover = faBookOpenCover;
    faScreenUsers = faScreenUsers;
    public receivedData;
    public theme: any;
    public data: any;
    public courseData = [];
    private readonly notifier: NotifierService;
    sunIcon: AnimationOptions = {
        path: 'https://assets8.lottiefiles.com/packages/lf20_5i5k8eh3.json',
    };

    moonIcon: AnimationOptions = {
        path: 'https://assets6.lottiefiles.com/packages/lf20_btkj8xsi.json',
    };


    constructor(
        private content: CoursesService,
        private cartService: CourseCartService,
        private modalService: ModalService,
        notifierService: NotifierService
    ) {
      
        this.notifier = notifierService;
    }

    addToCart(course: Course) {
        this.cartService.addToCart(course);
        this.notifier.notify(
            'success',
            'Cette formation a été ajoutée au panier !'
        );
    }

    open() {
        this.modalService.openCategoriesModal();
    }

    
    ngOnInit(): void {

        this.theme = JSON.parse(localStorage.getItem('Theme')).theme;
        this.data = JSON.parse(this.data);

        if(this.data){
  
        
             var count = 2;
             if(this.data.TopFormation1){
                 count -= 1;
                 this.courseData.push(this.data.TopFormation1);
             } 
             if(this.data.TopFormation2){
                 count -= 1;
                 this.courseData.push(this.data.TopFormation2);
             } 
             if(count > 0 || count ==2 ){
                 this.content.getFormationCards(count).then((courseData: any) => {
                     this.courseData.push(...courseData);
                 });
             }
         
  
          }
    }

    bgImage = [
        {
            img: 'assets/img/gray-bg.jpg',
        },
    ];
}
