import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanDeactivate, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';


import {  Renderer2, RendererFactory2 } from '@angular/core';
import {  Subject } from 'rxjs';
import { BsModalService } from 'ngx-bootstrap/modal';
import { ConfirmLeaveCourseComponent } from '../components/common/confirm-leave-course/confirm-leave-course.component';
export interface ComponentCanDeactivate {
    canDeactivate: () => boolean | Observable<boolean>;
}




@Injectable({
  providedIn: 'root'
})
export class LeavingSuiviGuard implements CanDeactivate<unknown> {

  private renderer: Renderer2;



  constructor(
    private modalService: BsModalService,
    rendererFactory: RendererFactory2
) {
    this.renderer = rendererFactory.createRenderer(null, null);
}


  canDeactivate(
    component: ComponentCanDeactivate,
    currentRoute: ActivatedRouteSnapshot,
    currentState: RouterStateSnapshot,
    nextState?: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
  
      
             if (!component.canDeactivate()) {
            const subject = new Subject<boolean>();

            const modal = this.modalService.show(ConfirmLeaveCourseComponent, {
                class: 'modal-dialog-cust',
            });
            modal.content.subject = subject;

            return subject.asObservable();
        }

  }
  
}
