import { HttpClient } from '@angular/common/http';
import {
    Component,
    ElementRef,
    EventEmitter,
    HostListener,
    Input,
    OnInit,
    Output,
    SimpleChange,
    ViewChild,
} from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { faPaperPlane, faTimes } from '@fortawesome/pro-regular-svg-icons';
import { faPersonToDoor } from '@fortawesome/pro-duotone-svg-icons';
import { NotifierService } from 'angular-notifier';
import { CourseCartService } from 'src/app/services/courseCart.service';
import { environment } from 'src/environments/environment';
import { UserService } from 'src/app/services/user.service';

import { Observable } from 'rxjs';

import { ModalService } from './popup-form.service';
import { CategoriesService } from '../top-categories/categories.service';

@Component({
    selector: 'app-popup-form',
    templateUrl: './popup-form.component.html',
    styleUrls: ['./popup-form.component.scss'],
})
export class PopupFormComponent implements OnInit {
    @Output() modal = new EventEmitter<boolean>();
    faPaperPlane = faPaperPlane;
    faTimes = faTimes;
    public rateUser : any;
    faPersonToDoor = faPersonToDoor;
    public open = false;
    public dataCateg = null;
    public text: String;
    public isChecked: boolean = false;
    private API_URL = environment.API_URL;
    public ref: ElementRef;
    notifier: NotifierService;
    public user : any;
    professions = {};
    SubmitLoad = false;
    prospectForm = this.formBuilder.group({
        name: ['', Validators.required],
        category: [null, ''],
        profession: ['', Validators.required],
        phone: ['', Validators.required],
        email: ['', Validators.required],
        message: ['', ''],
        check: [this.isChecked, ''],
    });

    avisForm = this.formBuilder.group({
        rate : [0,Validators.required],
        comment : ['']
    })

    display: Observable<'open' | 'close'>;
    type: Observable<
        | 'prospect'
        | 'categories'
        | 'email'
        | 'teacher'
        | 'faq'
        | 'newsletter'
        | 'areYouSure'
        | 'avis'
    >;

    constructor(
        private courseCartService: CourseCartService,
        notifierService: NotifierService,
        private formBuilder: FormBuilder,
        private http: HttpClient,
        private router: Router,
        private userService: UserService,
        private categoriesService: CategoriesService,
        private modalService: ModalService
    ) {
        this.modalService.setFormations(
            JSON.parse(localStorage.getItem('items'))
        );
        this.user = this.userService.getUser();
        if (this.user) {
            if (this.user.email)
                this.prospectForm.controls['email'].setValue(this.user.email);
            if (this.user.name)
                this.prospectForm.controls['name'].setValue(this.user.username);
            if (this.user.phoneNumber)
                this.prospectForm.controls['phone'].setValue(this.user.phoneNumber);
            if(this.user.username)
                this.prospectForm.controls['name'].setValue(this.user.username);

        }
        this.notifier = notifierService;
        this.categoriesService.professionProspect().then((data)=>{ 
        console.log(data, "daaaaaaatageted");
        
           this.professions = data;
        })
    }
    ngOnChanges(changes: SimpleChange) {
    }



    ngOnInit(): void {
        this.SubmitLoad = false;
        this.rateUser = this.modalService.watchRateUser();
        this.display = this.modalService.watchDisplay();
        this.type = this.modalService.watchType();
        console.log("data", this.professions);


        this.categoriesService
            .getAllCoursesCategories()
            .subscribe((dataCateg: any) => {
                this.dataCateg = dataCateg;
            });
    }

    returnSubtitle(type: any) {
        switch (type) {
            case 'areYouSure':
                return 'Votre progression sera sauvegardée ✅';
                break;
            case 'avis':
                if(  this.rateUser["source"]["_value"]["rate"]["previousRate"] != 0){
                   return ( this.rateUser["source"]["_value"]["rate"]["previousRate"] > this.rateUser["source"]["_value"]["rate"]["newRate"]) ?
                    'Quelque chose vous a déplu ? Vous vous apprêtez à  passer votre note de ' + this.rateUser["source"]["_value"]["rate"]["previousRate"] + ' à ' + this.rateUser["source"]["_value"]["rate"]["newRate"] + '  😔' :
                    'La formation vous a plus d\'avantage ? Vous vous apprêtez à passer votre note  à ' + this.rateUser["source"]["_value"]["rate"]["newRate"] + ' 🤗' ;
                }else{
                    return 'Votre avis compte ! Il nous permet d\'améliorer sans cesse nos formations ✍️'
                }
               
            default:
                return 'Laissez-nous vos coordonnées pour être rappelé rapidement ✍️';
                break;
        }
    }

    leaveCourse() {}

    returnTitle(type: any) {
        switch (type) {
            case 'avis': 
            return 'Une note ? Un commentaire ?'
            break;
            case 'prospect':
                return 'Cette formation vous intéresse ?';
                break;
            case 'categories':
                return 'Les formations indemnisées vous intéressent ?';
                break;
            case 'email':
                return "On s'appelle ?";
                break;
            case 'teacher':
                return 'Veuillez remplir ce formulaire';
                break;
            case 'faq':
                return 'Une question ? On vous écoute';
                break;

            case 'newsletter':
                return 'Toutes les dernières informations directement par email ';
                break;
            case 'areYouSure':
                return 'Voulez-vous vraiment quitter le cours ?';
                break;
            default:
                break;
        }
    }

    returnIcon(type: any) {
        switch (type) {
            case 'areYouSure':
                return '⚠️';
                break;
            case 'avis' :
                return '⭐';
                break;
            default:
                return '👋';
                break;
        }
    }

    onDeleteItem(courseID: any) {
        this.courseCartService.deleteFromCart(courseID);
        this.notifier.notify(
            'info',
            'Cette formation a été retirée de vos choix !'
        );
        this.modalService.setFormations(
            JSON.parse(localStorage.getItem('items'))
        );
    }

    close() {
        this.modalService.closeModal();
        this.SubmitLoad = false;
    }

    onChangeProfession(e) {
        this.prospectForm.controls['profession'].setValue(e);
    }

    onChangeCateg(e) {
        this.prospectForm.controls['category'].setValue(e.toString());
    }

    analyseForm(type: any) {
        const value = this.type['source']['_value'];
        switch (value) {
            case 'prospect':
                return (
                    this.prospectForm.value['email'].includes('@') &&
                    this.prospectForm.value['name'].length > 2 &&
                    this.prospectForm.value['profession'] != 'Sélectionner' &&
                    this.prospectForm.value['phone'].length > 9 &&
                    this.prospectForm.value['check']
                );
                break;
            case 'categories':
                return (
                    this.prospectForm.value['email'].includes('@') &&
                    this.prospectForm.value['category'] != 'Sélectionner' &&
                    this.prospectForm.value['phone'].length > 9 &&
                    this.prospectForm.value['check']
                );
                break;
            case 'email':
                return (
                    this.prospectForm.value['email'].includes('@') &&
                    this.prospectForm.value['phone'].length > 9 &&
                    this.prospectForm.value['check']
                );
                break;
            case 'teacher':
                return (
                    this.prospectForm.value['email'].includes('@') &&
                    this.prospectForm.value['name'].length > 2 &&
                    this.prospectForm.value['profession'] != 'Sélectionner' &&
                    this.prospectForm.value['phone'].length > 9 &&
                    this.prospectForm.value['check']
                );
                break;
            case 'faq':
                return (
                    this.prospectForm.value['email'].includes('@') &&
                    this.prospectForm.value['message'].length > 10 &&
                    this.prospectForm.value['phone'].length > 9 &&
                    this.prospectForm.value['check']
                );
            case 'newsletter':
                return (
                    this.prospectForm.value['email'].includes('@') &&
                    this.prospectForm.value['check']
                );
            case 'avis':
                    return (
                        this.rateUser["source"]["_value"]["rate"]["newRate"] > 0 &&
                        this.rateUser["source"]["_value"]["comment"] &&
                        this.rateUser["source"]["_value"]["comment"].length > 6
                    );
             
            default:
                break;
        }
    }

    async  onSubmitAvis() {
        
        this.SubmitLoad = true;

        const id_rating =  (this.rateUser["source"]["_value"]["id"] != 0) ? this.rateUser["source"]["_value"]["id"] : 0;
        let url = `${this.API_URL}/ratings/MyRating/${id_rating}`;
        let data  = {rate : this.rateUser["source"]["_value"]["rate"]["newRate"], comment :  this.rateUser["source"]["_value"]["comment"], formation : this.rateUser["source"]["_value"]["formation"] }
        return this.http.put(url,{ data: data }).subscribe((res)=>{

            if(this.rateUser["source"]["_value"]["id"] != 0){
               this.notifier.notify("success", "Avis modifié !")
              
            }else{
                this.notifier.notify("success", "Avis ajouté ! ")
                           }
            this.close();
            window.location.reload();
        });

    }
  async  onSubmit() {
    this.SubmitLoad = true;
        if (
            this.analyseForm(this.type) && (
            this.type['source']['_value'] == 'prospect' ||
            this.type['source']['_value'] == 'categories'  )

        ) {
            var content = this.prospectForm.value;
            content.formations = [];
            content.profession = this.professions[content.profession];
            var price = {}
            this.modalService.watchFormations().forEach((el) => {
                price[el.id] = el.price;
                content.formations.push(el.id);
            });

            if(this.user){
                let url = `${this.API_URL}/orders/create`;
                content.formations.forEach((formation) => {
                    var info_order = { payement : "en attente", with : "Crédits", user : this.user.id, totalPrice : price[formation], formation: formation }
                    this.http.post<any>(url, { data: info_order }).subscribe(
                        (response) => {
             
                        },
                        (e) => {
                           
                        }
                    );
                })
                this.notifier.notify(
                    'success',
                    'Message envoyé, à très vite !'
                );
                this.modalService.watchFormations().forEach((el) => {
                    this.courseCartService.deleteFromCart(el.id);
                });
                setTimeout(() => {
                    this.prospectForm = this.formBuilder.group({
                        name: ['', Validators.required],
                        category: ['', Validators.required],
                        profession: ['', Validators.required],
                        phone: ['', Validators.required],
                        email: ['', Validators.required],
                        message: ['', ''],
                        check: [this.isChecked, ''],
                    });
                    this.close();
                }, 1000);
            }else{
        
                    let url = `${this.API_URL}/users-permissions/addprospect`;
                    content.Status = "NonAssigne";
                    this.http.post<any>(url, { data: content }).subscribe(
                        (response) => {
                            if(response  && response.id == null){
                                this.notifier.notify(
                                    'info',
                                    'Un compte existe déjà avec cet email 🤔'
                                );
                              
                            }else{
                                this.notifier.notify(
                                    'success',
                                    'Message envoyé, à très vite !'
                                );
                                this.modalService.watchFormations().forEach((el) => {
                                    this.courseCartService.deleteFromCart(el.id);
                                });
                            }
                           
                           
                            setTimeout(() => {
                                this.prospectForm = this.formBuilder.group({
                                    name: ['', Validators.required],
                                    category: ['', Validators.required],
                                    profession: ['', Validators.required],
                                    phone: ['', Validators.required],
                                    email: ['', Validators.required],
                                    message: ['', ''],
                                    check: [this.isChecked, ''],
                                });
                                this.close();
                            }, 1000);
                        },
                        (e) => {
                            this.notifier.notify('error', "Erreur dans l'envoie..");
                        }
                    );
             
              
            }
         
           
        } else if (
            this.analyseForm(this.type) &&
            this.type['source']['_value'] == 'newsletter'
        ) {
            var email = this.prospectForm.value['email'];
 
                let url = `${this.API_URL}/newsletter/exist/`;
           
                this.http.post<any>(url, { data: { email: email } }).subscribe(
                    (response) => {
                        if(response && response.id == null){
                            this.notifier.notify(
                                'info',
                                'Vous êtes déjà inscrit à la newsletter !'
                            );
                          
                        }else{
                            this.notifier.notify(
                                'success',
                                'Vous êtes désormais abonné à notre newsletter !'
                            );
                        }
                       
    
                        setTimeout(() => {
                            this.prospectForm = this.formBuilder.group({
                                name: ['', Validators.required],
                                category: ['', Validators.required],
                                profession: ['', Validators.required],
                                phone: ['', Validators.required],
                                email: ['', Validators.required],
                                message: ['', ''],
                                check: [this.isChecked, ''],
                            });
                            this.close();
                        }, 1000);
                    },
                    (e) => {
                        this.notifier.notify('error', "Erreur dans l'envoie..");
                    }
                );
          
          
        } else if (
            this.analyseForm(this.type) && (
            this.type['source']['_value'] == 'email' || this.type['source']['_value'] == 'faq' || this.type['source']['_value'] == 'teacher')
        ) {
            let url = `${this.API_URL}/contacts`;
            var email = this.prospectForm.value['email'];
            var phone = this.prospectForm.value['phone'];
            var message = this.prospectForm.value['message'];
            var name = this.prospectForm.value['name'];
            this.http
                .post<any>(url, {
                    data: {name : name, email: email, phoneNumber: phone, message: message },
                })
                .subscribe(
                    (response) => {
                        this.notifier.notify(
                            'success',
                            'Message envoyé, à très vite !'
                        );

                        setTimeout(() => {
                            this.prospectForm = this.formBuilder.group({
                                name: ['', Validators.required],
                                category: ['', Validators.required],
                                profession: ['', Validators.required],
                                phone: ['', Validators.required],
                                email: ['', Validators.required],
                                message: ['', ''],
                                check: [this.isChecked, ''],
                            });
                            this.close();
                        }, 1000);
                    },
                    (e) => {
                        this.notifier.notify('error', "Erreur dans l'envoie..");
                    }
                );
        }
     
    }

    changed(evt) {
        this.isChecked = evt.target.checked;
        if (this.isChecked) {
            this.notifier.notify('info', 'Vos données seront protégées 👍');
        }
    }

    @HostListener('document:mousedown', ['$event'])
    clickout(event: any) {
        const dis: any = this.modalService.watchDisplay();
        if (
            dis &&
            dis.source &&
            dis.source._value &&
            dis.source._value == 'open'
        ) {
            const clickInside = this.ref.nativeElement.contains(event.target);
            !clickInside ? this.close() : null;
        }
    }

    @ViewChild('modal', { static: false }) set target(target: ElementRef) {
        if (target) this.ref = target;
    }
}
