import { Component, OnInit } from '@angular/core';
import { Router, NavigationCancel, NavigationEnd } from '@angular/router';
import {
    Location,
    LocationStrategy,
    PathLocationStrategy,
} from '@angular/common';
import { filter } from 'rxjs/operators';
import { ThemeService } from './services/theme.service';
declare let $: any;

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss'],
    providers: [
        Location,
        {
            provide: LocationStrategy,
            useClass: PathLocationStrategy,
        },
    ],
})
export class AppComponent {
    location: any;
    routerSubscription: any;
    public setTheme: any;
    public init = false;
    public rootCss: any;
    public cssColors: any;

    constructor(public router: Router, public themeService: ThemeService) {
        this.rootCss = document.querySelector(':root');
    }

    async ngOnInit() {
        this.setTheme = await this.themeService.Theme().toPromise();
        localStorage.setItem('Theme', JSON.stringify(this.setTheme));

        this.cssColors = Object.entries(await this.themeService.Colors().toPromise());
        this.cssColors.shift();

        for (const [key, value] of this.cssColors) {
            var rgbColor = this.hexToRgb(value);
            let newColor = rgbColor?.r + ' ' + rgbColor?.g + ' ' + rgbColor?.b

            this.rootCss.style.setProperty(`--${key}`, newColor)
        }

        document.body.classList.add(this.setTheme.theme);

        this.init = true;

        this.recallJsFuntions();
    }

    IsLogin() {
        return this.router.url.includes('connecter');
    }

    recallJsFuntions() {
        this.routerSubscription = this.router.events
            .pipe(
                filter(
                    (event) =>
                        event instanceof NavigationEnd ||
                        event instanceof NavigationCancel
                )
            )
            .subscribe((event) => {
                this.location = this.router.url;
                if (!(event instanceof NavigationEnd)) {
                    return;
                }
                window.scrollTo(0, 0);
            });
    }

    toggleModal(type: string, data: any) { }


    hexToRgb(hex) {
        var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
        return result ? {
            r: parseInt(result[1], 16),
            g: parseInt(result[2], 16),
            b: parseInt(result[3], 16)
        } : null;
    }
}
