import { Injectable } from '@angular/core';
import { CoursesDetailsService } from '../components/pages/courses-details/courses-details.service';
import { Course } from '../course';
@Injectable({
    providedIn: 'root',
})
export class CourseCartService {
    items: Course[] = (localStorage.getItem('items') != null) ? JSON.parse(localStorage.getItem('items')) : [];


    constructor(private courseDetail : CoursesDetailsService) {
        if(this.items){
            this.items.forEach( (el)=>{
                courseDetail.courseExist(el.id).subscribe((dd)=>{
                   if(dd == false){
                       this.deleteFromCart(el.id);
                   }
                })
            })
        }
    }


    /**
     * 
     * @param course 
     * @param quantity 
     * Add to cart item
     */
    addToCart(course: Course, quantity: number = 1) {
        const check_index = this.items.findIndex(
            (item) => item.id === course.id
        );
        if (check_index !== -1) {
            this.items[check_index].quantity;
        } else {
            this.items.push({ ...course, quantity });
        }

        localStorage.setItem('items', JSON.stringify(this.items));

    }

    /**
     * 
     * @returns get total
     */
    getTotal() {
        const total = this.items.reduce((acc, el) => {
            acc += el.price * el.quantity;
            return acc;
        }, 0);
        return ((total * 100) / 100).toFixed(2);
    }

    /**
     * 
     * @returns get cart items
     */
    getItems() {
        return this.items;
    }

    /**
     * 
     * @param courseID 
     * delete item from cart
     */
    deleteFromCart(courseID: any) {
        const items = this.items.filter((item) => item.id === courseID);
        const index = this.items.indexOf(items[0]);
        if (index > -1) {
            this.items.splice(index, 1);
        }
        localStorage.setItem('items', JSON.stringify(this.items));
    }

    /**
     * 
     * @returns clear all cart items
     */
    clearCart() {
        this.items = [];
        localStorage.removeItem('items');

        return this.items;
    }
}