import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import * as moment from 'moment';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { ProfileService } from '../../pages/profile/profile.service';
import {
    faArrowRightFromBracket,
    faFlagCheckered,
    faCowbellCirclePlus,
} from '@fortawesome/pro-solid-svg-icons';
import { faCirclePlay } from '@fortawesome/pro-duotone-svg-icons';
import * as dayjs from 'dayjs';
import { AnimationOptions } from 'ngx-lottie';
@Component({
    selector: 'app-view-courses',
    templateUrl: './view-courses.component.html',
    styleUrls: ['./view-courses.component.scss'],
})
export class ViewCoursesComponent implements OnInit {

    faCirclePlay = faCirclePlay;
    faFlagCheckered = faFlagCheckered;
    faCowbellCirclePlus = faCowbellCirclePlus;

    public theme = JSON.parse(localStorage.getItem('Theme')).theme;


    private template: ElementRef;
    private template1: ElementRef;
    private template2: ElementRef;
    public checkSession: any;


    @Input() activityLog;

    options: AnimationOptions = {
        path: 'https://assets9.lottiefiles.com/packages/lf20_dbaflzo1.json',
    };

    @ViewChild('template', { static: false }) set target(target: ElementRef) {
        if (target) this.template = target;
    }
    @ViewChild('template1', { static: false }) set target2(
        target2: ElementRef
    ) {
        if (target2) this.template1 = target2;
    }
    @ViewChild('template2', { static: false }) set target3(
        target3: ElementRef
    ) {
        if (target3) this.template2 = target3;
    }

    load = false;
    sessions: any;
    order_sessions = {
        progress: [],
        todo: {
            open: [],
            close: [],
        },
        end: {
            open: [],
            close: [],
        },
    };
    restSession = [];
    restSessionId = [];

    constructor(private profileService: ProfileService) { }

    ngAfterViewInit() {
        if (this.template2) {
            this.template2.nativeElement
                .querySelector('div.owl-theme > div.owl-nav > div.owl-prev')
                .classList.add('arrow_change');
            this.template2.nativeElement
                .querySelector('div.owl-theme > div.owl-nav > div.owl-next')
                .classList.add('arrow_change');
            this.template2.nativeElement
                .querySelector('div.owl-theme > div.owl-nav')
                .classList.add('owlnav_change');
            this.template2.nativeElement
                .querySelector('div.owl-theme > div.owl-stage-outer')
                .classList.add('owlstageouter_change');
        }
        if (this.template1) {
            this.template1.nativeElement
                .querySelector('div.owl-theme > div.owl-nav > div.owl-prev')
                .classList.add('arrow_change');
            this.template1.nativeElement
                .querySelector('div.owl-theme > div.owl-nav > div.owl-next')
                .classList.add('arrow_change');
            this.template1.nativeElement
                .querySelector('div.owl-theme > div.owl-nav')
                .classList.add('owlnav_change');
            this.template1.nativeElement
                .querySelector('div.owl-theme > div.owl-stage-outer')
                .classList.add('owlstageouter_change');
        }
        if (this.template) {
            this.template.nativeElement
                .querySelector('div.owl-theme > div.owl-nav > div.owl-prev')
                .classList.add('arrow_change');
            this.template.nativeElement
                .querySelector('div.owl-theme > div.owl-nav > div.owl-next')
                .classList.add('arrow_change');
            this.template.nativeElement
                .querySelector('div.owl-theme > div.owl-nav')
                .classList.add('owlnav_change');
            this.template.nativeElement
                .querySelector('div.owl-theme > div.owl-stage-outer')
                .classList.add('owlstageouter_change');
        }
    }
    async ngOnInit() {
        this.load = false;
        dayjs.locale('fr');
        const date_now = dayjs(new Date()).unix() * 1000;
        const recup = await this.profileService
            .getSessions()
            .then((data: any) => {
                if (data && data.sessions) {
                    this.checkSession = data.sessions.length;
                    data.sessions.forEach((session) => {
                        if(session.formation){
                            let dateStart = new Date(session.date_start).getTime();
                            let dateEnd = new Date(session.date_finish).getTime();
                            if (dateEnd < date_now) {
                                this.order_sessions.end.close.push(session);
                            } else {
                                if (dateStart > date_now) {
                                    this.order_sessions.todo.close.push(session);
                                } else {
                                    this.restSession.push(session);
                                    this.restSessionId.push(session.id);
                                }
                            }
                        }else{
                            this.checkSession -= 1;
                        }
              
                    });
                    this.load = true;
                }
            });

        if (this.restSession && this.restSession.length > 0) {
            this.restSession.forEach((session) => {
                if (
                    this.activityLog &&
                    this.activityLog[session.id] &&
                    this.activityLog[session.id].total_course_time > 0
                ) {
                    session['totalActivities'] =
                        this.activityLog[session.id].total_activities;

                    session['totalDone'] =
                        this.activityLog[session.id].total_done;
                    session['totalTime'] =
                        this.activityLog[session.id].total_course_time;

                    this.activityLog[session.id].finish
                        ? this.order_sessions.end.open.push(session)
                        : this.order_sessions.progress.push(session);
                } else {
                    this.order_sessions.todo.open.push(session);
                }
            });
            this.load = true;
        }
    }
    advisorSlides: OwlOptions = {
        loop: true,
        nav: false,
        dots: false,
        margin: 30,
        rtl: true,
        autoplay: false,
        autoplayHoverPause: true,
        responsive: {
            0: {
                items: 1,
            },
            640: {
                items: 2,
            },
            768: {
                items: 2,
            },
            1280: {
                items: 3,
            },
        },
    };

    Pourcent(total_done, total_activities) {
        return Math.round((total_done / total_activities) * 100);
    }
    FormatDate(timesecond) {
        var date = new Date(0);
        date.setSeconds(timesecond);
        var timeString = date.toISOString().substr(11, 8);
        return timeString;
    }
}
