import { Injectable } from '@angular/core';

import { BehaviorSubject, Observable } from 'rxjs';
class rateUser {
    rate : any;
    comment: string;
    id : number
    formation : number
    change : boolean
    constructor( rate : any,   comment: string,   id: number, formation : number, change: boolean){
        this.rate = rate;
        this.comment = comment;
        this.id = id;
        this.formation = formation;
        this.change = change;
    }
}

@Injectable({
    providedIn: 'root',
})

export class ModalService {
    public rateUser = new rateUser(0,"",0, 0,false);
    public rateUSerParams =  new BehaviorSubject<rateUser>(this.rateUser);

    private display: BehaviorSubject<'open' | 'close'> = new BehaviorSubject(
        'close'
    );
    private type: BehaviorSubject<'prospect' | 'categories' | 'email' | 'teacher' | 'faq' | 'newsletter' | 'areYouSure' | 'avis'> = new BehaviorSubject(
        'email'
    );
    private formations : any;

    watchDisplay(): Observable<'open' | 'close'> {
        return this.display.asObservable();
    }
    watchType(): Observable<'prospect' | 'categories' | 'email' | 'teacher' | 'faq' | 'newsletter' | 'areYouSure' | 'avis'> {
        return this.type.asObservable();
    }
    watchFormations(){
        return this.formations;
    }
    watchRateUser() : Observable<rateUser>{
        return this.rateUSerParams.asObservable();
    }
    setRateUser(RateUser){
        this.rateUSerParams.next(RateUser);
    }

   setFormations(formations){
      this.formations = formations;
    }
    openTeacherModal() {
        this.type.next('teacher');
        this.display.next('open');
    }
    openCategoriesModal() {
        this.type.next('categories');
        this.display.next('open');
    }
    openEmailModal() {
        this.type.next('email');
        this.display.next('open');
    }
    openProspectModal() {
        this.type.next('prospect');
        this.display.next('open');
        this.formations = JSON.parse(localStorage.getItem('items'));
    }
    openFaqModal() {
        this.type.next('faq');
        this.display.next('open');
    }
    openNewsletterModal() {
        this.type.next('newsletter');
        this.display.next('open');
    }
    openAreUSureModal() {
        this.type.next('areYouSure');
        this.display.next('open');
    }

    openAvisModal(rateUser) {
        this.rateUSerParams.next(rateUser);
        this.type.next('avis');
        this.display.next('open');
    }

    closeModal() {
        this.display.next('close');
    }
}
