import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute } from '@angular/router';
import { environment } from '../../../../../src/environments/environment';
import { fetchStrapiFormat } from 'src/app/helpers/functions';

@Component({
    selector: 'app-blog-tag-search',
    templateUrl: './blog-tag-search.component.html',
    styleUrls: ['./blog-tag-search.component.scss']
})
export class BlogTagSearchComponent implements OnInit {

    private API_URL = environment.API_URL;
    public slug: any;
    public data: any;

    constructor(
        private http: HttpClient,
        private route: ActivatedRoute
    ) {
        this.route.params.subscribe((param: any) => {
            let ddd = fetchStrapiFormat(this.http, { url: '/blog-tags', filter : {slug :param.slug  }, populate : ['blog_posts','blog_posts.image','blog_posts.blog_category']});

            ddd.then((res) => {
   
                this.data = res;
            });
        });
    }

    ngOnInit(): void {}

    blogGrid: number = 1;

}