import {
    Component,
    ElementRef,
    HostListener,
    Input,
    OnInit,
    ViewChild,
} from '@angular/core';
import {
    faTimer,
    faBookOpenCover,
    faScreenUsers,
    faGraduationCap,
    faStar,
    faPenNib,
    faTimes,
    faCirclePlay,
    faFlagCheckered
} from '@fortawesome/pro-regular-svg-icons';
import { faEye } from '@fortawesome/pro-duotone-svg-icons';
import { CourseCartService } from '../../../services/courseCart.service';
import { Course } from 'src/app/course';
import { NotifierService } from 'angular-notifier';
import { Router } from '@angular/router';
import { ModalService } from '../../common/popup-form/popup-form.service';
import { UserService } from 'src/app/services/user.service';
import { CoursesDetailsService } from '../../pages/courses-details/courses-details.service';

@Component({
    selector: 'app-course-card-cust',
    templateUrl: './course-card-cust.component.html',
    styleUrls: ['./course-card-cust.component.scss'],
})
export class CourseCardCustComponent implements OnInit {
    @Input() content = null;

    @Input() isFirstChildHomepage = false;

    faTimer = faTimer;
    faBookOpenCover = faBookOpenCover;
    faStar = faStar;
    faEye = faEye;
    faTimes = faTimes;
    faPenNib = faPenNib;
    faCirclePlay = faCirclePlay;
    faFlagCheckered = faFlagCheckered;
    faGraduationCap = faGraduationCap;
    faScreenUsers = faScreenUsers;
    rating = 0;
    showRatings = true;
    cart: Array<any> = [];
    nbReview = 0;
    public theme;
    public creditOpen = false;
    public load = false;
    private readonly notifier: NotifierService;
    public target: boolean = false;
    public ref: ElementRef;
    buttonState: any;
    bannerFormation: any = JSON.parse(localStorage.getItem('Theme')).bannerFormation;
    public user = this.userService.getUser();
    constructor(
        private cartService: CourseCartService,
        notifierService: NotifierService,
        private userService: UserService,
        private coursesDetailsService: CoursesDetailsService,
        private router: Router,
        private modalService: ModalService,

    ) {
        this.theme = JSON.parse(localStorage.getItem('Theme')).theme;
        this.cart = this.getCart();
        this.notifier = notifierService;

    }

    async ngOnInit() {
        
        if (this.content) {
           
            if (this.userService.isLoggedIn()) {
                this.buttonState = await this.coursesDetailsService.getDetailCourse(this.content.id).toPromise();
            }

        }
        this.load = true;

    }

    Fixed(e){
        return e.toFixed(0);
    }

    open() {
        this.modalService.openProspectModal();
    }

    getCart() {
        const cartTotal = this.cartService.getItems();
        return cartTotal;
    }

    isInCart(id) {
        const cart = this.cart.findIndex((el) => el.id == id);
        return cart != -1;
    }

   
    NaviguateCourse(slug) {
        this.router.navigate(['/suivi', slug, this.buttonState.session])
    }
    removeFromCart(course: Course) {
        this.cartService.deleteFromCart(course.id);
        this.notifier.notify(
            'success',
            'Cette formation a été rétirée du panier !'
        );
    }
    addToCart(course: Course, modal) {
        if (!this.isInCart(course.id)) {
            this.cartService.addToCart(course);
            this.getCart();
            this.notifier.notify(
                'success',
                'Cette formation a été ajoutée au panier !'
            );
        }
        if (modal) {
            this.open();

        }
    }


    ChangeTarget() {
        this.target = true;
        setTimeout(() => {
            this.target = false;
        }, 1000)
    }

    navigateToDetails(slug: string) {
        if (!this.target) {
            this.router.navigate(['/formation/' + slug ]);
            this.target = false;
        }
    }

    changeModal(e: any) {
        if (e) {
            this.creditOpen = false;
        }
    }
    modalAvisOpen(rateUser) {
        this.modalService.openAvisModal(rateUser);
    }

    async toggleRating(e) {
        if (e) {
            if (this.user && this.user.id != undefined) {

                if (this.buttonState && this.buttonState.etat) {
                    if (this.buttonState.start && this.buttonState.start != false) {
                        const alreadyComment = await this.coursesDetailsService.GetUserRate(this.content.id).toPromise();
                        const comment = (alreadyComment) ? alreadyComment["comment"] : "";
                        const rate = (alreadyComment) ? { newRate: e, previousRate: alreadyComment["rate"] } : { newRate: e, previousRate: 0 };
                        const id = (alreadyComment) ? alreadyComment["id"] : 0;
                        const rateUser = { rate: rate, comment: comment, id: id, formation: this.content.id };
                        this.modalAvisOpen(rateUser);

                    } else {
                        this.notifier.notify("info", "Vous n\'avez pas commencé votre formation !")
                    }
                } else {
                    this.creditOpen = true;
                    this.addToCart(this.content, true)
                    this.open();
                    this.notifier.notify("info", "Inscrivez vous à cette formation pour pouvoir laisser votre avis ⭐ ");
                }

            } else {
                this.creditOpen = true;
                this.addToCart(this.content, true)
                this.open();

                this.notifier.notify("info", "Inscrivez vous à cette formation pour pouvoir laisser votre avis ⭐");
            }
        }

    }
}
