import { Component, Input, OnInit } from '@angular/core';
import {
    faCirclePlay,
    faCowbellCirclePlus,
    faAlarmClock,
    faHourglass,
    faHandPointRight
} from '@fortawesome/pro-duotone-svg-icons';
import { AnimationOptions } from 'ngx-lottie';

@Component({
    selector: 'app-course-card-dashboard',
    templateUrl: './course-card-dashboard.component.html',
    styleUrls: ['./course-card-dashboard.component.scss'],
})
export class CourseCardDashboardComponent implements OnInit {
    @Input() formation: any;
    @Input() pourcent: any;
    @Input() status: any;
    @Input() carousel: any;
    @Input() totalTimeCourse: any;
    @Input() iconType: any;
    @Input() startDate: any;
    @Input() sessionId : any;
    public theme = JSON.parse(localStorage.getItem('Theme')).theme;


    public strokeColor: any;
    faCowbellCirclePlus = faCowbellCirclePlus;
    faCirclePlay = faCirclePlay;
    faAlarmClock = faAlarmClock;
    faHourglass = faHourglass;
    faHandPointRight = faHandPointRight;

    options: AnimationOptions = {
        path: 'https://assets9.lottiefiles.com/packages/lf20_dbaflzo1.json',
    };

    constructor() {
        this.strokeColor = JSON.parse(localStorage.getItem('Theme'))
            ? `${JSON.parse(localStorage.getItem('Theme')).mainColor}`
            : '#FFFFFF';
    }

    ngOnInit(): void {
      console.log('this.theme', this.theme)

     }

    FormateHour(e) {
        return Math.round(e / 60);
    }

    FormatDate(timesecond) {
        var date = new Date(0);
        date.setSeconds(timesecond);
        var timeString = date.toISOString().substr(11, 8);
        return timeString;
    }
}
