import { HttpClient } from '@angular/common/http';
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'banner'
})
export class BannerPipe implements PipeTransform {
  defaultImage = JSON.parse(localStorage.getItem('Theme'));

  constructor(
    private http: HttpClient,
  ) { }


  /**
   * This pipe just check if image url exist before displaying them and returns image source or default image source if not found
   * @param url 
   * @returns url:string
   */
  async transform(url: string) {
    if (url === undefined) return this.defaultImage.bannerFormation.url;

    try {
      new URL(url);
      return url;
    } catch (error) {
      return this.defaultImage.bannerFormation.url;
    }

  }

}
