import { Component, OnInit } from '@angular/core';
import { CoursesService } from '../../common/courses/courses.service';
import { NotifierService } from 'angular-notifier';
import { CourseCartService } from '../../../services/courseCart.service';
import { Course } from 'src/app/course';
import { CategoriesService } from '../../common/top-categories/categories.service';
import { Router, ActivatedRoute } from '@angular/router';

import {
    faTimer,
    faBookOpenCover,
    faScreenUsers,
    faEye,
    faGraduationCap,
} from '@fortawesome/pro-regular-svg-icons';
@Component({
    selector: 'app-formations',
    templateUrl: './formations.component.html',
    styleUrls: ['./formations.component.scss'],
})
export class FormationsComponent implements OnInit {
    // fa section ===============================
    faTimer = faTimer;
    faGraduationCap = faGraduationCap;
    faBookOpenCover = faBookOpenCover;
    faScreenUsers = faScreenUsers;
    faEye = faEye;
    public theme: any;

    // var needed ===============================
    cart: Array<any> = [];
    categories: Array<any> = [];
    selectedCategId: number = 0;
    selectedCategIndex: number = 0;
    nbFormationDisplayed: number = 0;
    sortOptId: string = '0';
    sortOpts: Array<any> = [
        { id: 0, name: 'Default' },
        // { id: 1, name: 'Popularité' },
        { id: 2, name: 'Récents' },
        { id: 3, name: 'Prix: croissant' },
        { id: 4, name: 'Prix: décroissant' },
    ];
    allCategObj: any = {
        HexCode: '#333333',
        courses: [],
        created_at: '2022-01-05T05:12:08.542Z',
        icon: '📖',
        id: 0,
        published_at: '2022-01-05T05:12:10.544Z',
        slug: 'all',
        title: 'Toutes les formations',
        updated_at: '2022-03-04T12:58:51.716Z',
    };

    private readonly notifier: NotifierService;
    public data: Array<any> = [];
    public sortedData: Array<any> = [];

    constructor(
        private content: CoursesService,
        private cartService: CourseCartService,
        private categoriesService: CategoriesService,
        notifierService: NotifierService,
        private _route: ActivatedRoute,
        public _router: Router
    ) {
        this.theme = JSON.parse(localStorage.getItem('Theme')).theme;
        
        // handle categories change from # in URL
        _router.events.subscribe((val) => {
            if (_router.url.includes('#')) {
                var findkey = this._router.url.split('#')[1];

                if (findkey.length > 0) {
                    // look for matching categorie to retreive index
                    const categIndx = this.categories.findIndex(
                        (el) => el.slug == findkey
                    );

                    if (
                        categIndx != -1 &&
                        categIndx != this.selectedCategIndex
                    ) {
                        this.selectedCategId = this.categories[categIndx].id;
                        this.selectedCategIndex = categIndx;
                        this.fetchCourses((err, res) => {
                            this.sortedData = res;
                            this.nbFormationDisplayed = res.length;
                        });
                    }
                }
            }
        });

        if (this.categories.length == 0) {
            this.fetchAllCategs((err, resCateg) => {
                this.categories = resCateg;

                if (_router.url.includes('#')) {
                    var findkey = this._router.url.split('#')[1];

                    if (findkey.length > 0) {
                        const categIndx = resCateg.findIndex(
                            (el) => el.slug == findkey
                        );
                        if (categIndx != -1) {
                            const categId = resCateg[categIndx].id;
                            this.selectedCategId = categId;
                            this.selectedCategIndex = categIndx;
                            if (this.sortedData.length == 0) {
                                this.fetchCourses((err, res) => {
                                    this.sortedData = res;
                                    this.nbFormationDisplayed = res.length;
                                });
                            }
                        }
                    }
                } else {
                    this.fetchCourses((err, res) => {
                        this.sortedData = res;
                        this.nbFormationDisplayed = res.length;
                    });
                }
            });
        }

        this.notifier = notifierService;
    }

    // Return all categories and add the #all fake categorie
    fetchAllCategs(callback) {
        this.categoriesService
            .getAllCoursesCategories()
            .subscribe((dataCateg: any) => {
                const sortedCategories = dataCateg.sort((a, b) => {
                    return b.formation_count - a.formation_count;
                });

                sortedCategories.unshift(this.allCategObj);

                return callback(null, sortedCategories);
            });
    }

    // fetch all the courses needed depending on the categorie selected
    fetchCourses(callback) {
        if (this.selectedCategId == 0) {
            if (this.data.length == 0) {
                this.content.getFormationCards(0).then((data: any) => {
                    this.data = data;
                    return callback(null, this.sortFilter(data));
                });
            } else {
                return callback(null, this.sortFilter(this.data));
            }
        } else {
            const categCourses =
                this.data.length > 0
                    ? this.data.filter(
                        (el) =>
                            el.formations_category?.id == this.selectedCategId
                    )
                    : null;

            if (
                categCourses &&
                categCourses.length ==
                this.categories[this.selectedCategIndex].formation_count
            ) {
                return callback(null, categCourses);
            } else {
                this.categoriesService
                    .getAllCoursesByCategories(this.selectedCategId)
                    .subscribe((data: any) => {
                        return callback(null, this.sortFilter(data));
                    });
            }
        }
    }

    toggleNavbar(index) {
        this.selectedCategId = this.categories[index].id;
        this.sortedData = this.sortFilter(this.sortedData);
    }

    toggleSortOpt(e) {
        this.sortOptId = e.target.value;

        this.sortedData = this.sortFilter(this.sortedData);
    }

    addToCart(course: Course) {
        this.cartService.addToCart(course);
        this.notifier.notify(
            'success',
            'Cette formation a été ajoutée au panier !'
        );
    }

    //Return sorted data depending on sortOptId
    sortFilter(data) {
        let newSortedData = [...data];

        switch (this.sortOptId) {
            case '0':
                newSortedData.sort((a, b) => {
                    return a.id - b.id;
                });
                break;

            case '1':
                newSortedData.sort((a, b) => {
                    return b.courses_orders.length - a.courses_orders.length;
                });
                break;

            case '2':
                newSortedData.sort((a, b) => {
                    let c = new Date(a.published_at).getTime();
                    let d = new Date(b.published_at).getTime();
                    return d - c;
                });
                break;

            case '3':
                newSortedData.sort((a, b) => {
                    return a.price - b.price;
                });
                break;
            case '4':
                newSortedData.sort((a, b) => {
                    return b.price - a.price;
                });
                break;

            default:
                break;
        }

        return newSortedData;
    }

    getCart() {
        const cartTotal = this.cartService.getItems();
        return cartTotal;
    }

    isInCart(id) {
        const cart = this.cart.findIndex((el) => el.id == id);
        return cart != -1;
    }

    ngOnInit(): void { }

    coursesGrid: number = 1;
}
