import { Component, OnInit } from '@angular/core';

import { ConditionsService } from './conditions.service';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { NotifierService } from 'angular-notifier';
import { Router } from '@angular/router';
import { ModalService } from '../../common/popup-form/popup-form.service';

@Component({
    selector: 'app-conditions',
    templateUrl: './conditions.component.html',
    styleUrls: ['./conditions.component.scss']
})
export class ConditionsComponent implements OnInit {
    private readonly notifier: NotifierService;
    private API_URL = environment.API_URL;
    public data: any;
    public selected: any;
    public theme: any;

    constructor(
        private content: ConditionsService,
        public notifierService: NotifierService,
        private router: Router
    ) {
        this.theme = JSON.parse(localStorage.getItem('Theme')).theme;
        this.notifier = notifierService;
        this.content.getData().then((data: any) => {
            for (let i = 0; i < data.conditionsLists.length; i++) {
                data.conditionsLists[i]['selected'] = false;
            }
            this.data = data;

            if (this.router.url.includes('#') && data.conditionsLists.find((el) => el.menuLink.includes(this.router.url.split('#')[1])).publier) {
                var findkey = this.router.url.split('#')[1];
                data.conditionsLists[
                    Object.keys(data.conditionsLists).find(
                        (key) => data.conditionsLists[key].menuLink === findkey
                    )
                ]['selected'] = true;
            } else {

                data.conditionsLists[
                    Object.keys(data.conditionsLists).find(
                        (key) => data.conditionsLists[key].menuLink === 'politique-qualite'
                    )
                ]['selected'] = true;
            }
            this.selected = this.data.conditionsLists.find((el) => el.selected == true);
        });
    }

    ngOnInit(): void {
    }
    toggleNavbar(e) {
        if (!this.data.conditionsLists[e].publier) {
            return;
        }

        for (let i = 0; i < this.data.conditionsLists.length; i++) {
            i == e
                ? (this.data.conditionsLists[i]['selected'] = true)
                : (this.data.conditionsLists[i]['selected'] = false);
        }
        this.findSelected();
    }

    notifyError() {
        this.notifier.notify(
            'info',
            'Cette fonctionnalité arrive bientôt ! 👍'
        );
    }

    findSelected() {
        this.selected = this.data.conditionsLists.find(
            (el) => el.selected == true
        );
    }
}
