import { Component, Input, OnInit } from '@angular/core';
import { ThemeService } from 'src/app/services/theme.service';
import { SpinnerHandlerService } from '../spinner/spinner-handler.service';

@Component({
  selector: 'app-background',
  templateUrl: './background.component.html',
  styleUrls: ['./background.component.scss']
})

export class BackgroundComponent implements OnInit {

  public bgActive = false;
  protected colors: any;
  public mainColor: string;
  public secondColor: string;
  public backgroundType: string;


  constructor(
    private spinnerHandler: SpinnerHandlerService,
    private themeService: ThemeService
  ) {
    this.spinnerHandler.showSpinner.subscribe(this.showSpinner.bind(this));
  }

  async ngOnInit() {
    this.colors = await this.themeService.Theme().toPromise();
    this.backgroundType = this.colors.background;

    let rgbMainColor = this.hexToRgb(this.colors.mainColor);
    let newMainColor = rgbMainColor.r + ' ' + rgbMainColor.g + ' ' + rgbMainColor.b

    let rgbSecondColor = this.hexToRgb(this.colors.secondColor);
    let newSecondColor = rgbSecondColor.r + ' ' + rgbSecondColor.g + ' ' + rgbSecondColor.b

    this.mainColor = `rgb(${newMainColor})`;
    this.secondColor = `rgb(${newSecondColor})`;
  }

  showSpinner = (state: boolean): void => {
    this.bgActive = !state;
  };

  hexToRgb(hex) {
    var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result ? {
      r: parseInt(result[1], 16),
      g: parseInt(result[2], 16),
      b: parseInt(result[3], 16)
    } : null;
  }
}
