import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../src/environments/environment';
import { fetchStrapiFormat } from 'src/app/helpers/functions';

@Injectable({
    providedIn: 'root'
})
export class CommunauteService {

    private API_URL = environment.API_URL;

    constructor(
        private http: HttpClient
    ) { }

    Community() {
        let url = this.API_URL + "/session/community";
        return this.http.get(url);
    }

    CanAccessFormationCommunity(slug) {
        let url = this.API_URL + "/users-permissions/formation/community/" + slug;
        return this.http.get(url);
    }

        AnnonceCommunity(slug){
            let url = this.API_URL+"/annonces/formation/"+slug;
            return this.http.get(url);
        }
        getForumByFormation(slug){
            let url = this.API_URL+"/qa/getforumbyformation/"+slug;
            return this.http.get(url);
        }

        getQandAByFormation(id,slug){
            let url = this.API_URL+"/qa/getbyformation/"+id + "/"+slug;
            return this.http.get(url);
        }

        getAvisCommunity(slug){
            let url = this.API_URL+"/ratings/communityavis/"+slug;
            return this.http.get(url);
        }

        getCommunityFormationPreview(slug){
            let url = this.API_URL+"/community/formation/"+slug;
            return this.http.get(url);
        }
}