import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../src/environments/environment';
import { fetchStrapiFormat } from 'src/app/helpers/functions';

@Injectable({
    providedIn: 'root'
})
export class BlogService {

    private API_URL= environment.API_URL;

    constructor(
        private http : HttpClient
    ) {}

    getData(){
        return fetchStrapiFormat(this.http, { url: '/blog-posts', query : '_sort=id:DESC', populate : ['image','blog_category','blog_tag']});

    }
    getCategoriesData(){
        return fetchStrapiFormat(this.http, { url: '/blog-categories', query : '_sort=id:DESC', populate : ['blog_posts']});

    }
    getTagsData(){
        return fetchStrapiFormat(this.http, { url: '/blog-tags', query : '_sort=id:DESC', populate : ['blog_posts']});

    }
    getBlogData(){
        return fetchStrapiFormat(this.http, { url: '/blog', populate : ['image','blog_category','blog_tag']});

    }

}