import { Component, Input, OnInit, ViewChild, OnChanges, SimpleChanges } from '@angular/core';

@Component({
  selector: 'app-iframe',
  templateUrl: './iframe.component.html',
  styleUrls: ['./iframe.component.scss']
})
export class IframeComponent implements OnInit, OnChanges {

  @ViewChild('Iframe', { static: false }) IframeRef: any;

  @Input() lgMode: boolean;
  @Input() isDisplayed: string;
  @Input() content: any;

  public iframeType = null;
  public sourceUrl = null;

  // specific to type YouTube
  public youTubeParamsUrl = null;

  constructor() { }


  ngOnInit(): void {
  }

  ngOnChanges(changes: SimpleChanges): void {

    if (changes && changes.content && changes.content.currentValue && changes.content.currentValue.lienUrl) this.sourceUrl = changes.content.currentValue.lienUrl
    this.iframeType = this.getIframeType()

    if (changes.isDisplayed && changes.isDisplayed.currentValue == false && changes.isDisplayed.previousValue == true) {
      this.IframeRef.nativeElement.contentWindow.postMessage('{"event":"command","func":"' + 'stopVideo' + '","args":""}', '*');
    }

  }

  getIframeType() {
    // return this.source.changingThisBreaksApplicationSecurity.includes('youtube') ? 'iframe-yt' : 'default'  
    const componentid = this.content.__component.split('.')[1]
    if (componentid) {
      switch (componentid) {
        case 'iframe-youtube':
          this.youTubeParamsUrl = this.extractYTParams()
          return 'iframe-youtube'
          break;
        default:
          return 'default'
          break;
      }
    }
  }




  extractYTParams() {

    const addParam = (paramString: string, param: string): string => {
      let newString = paramString
      if (!newString.includes('?', 0) && newString.length == 0) {
        newString = '?' + param
      } else {
        newString = newString + "&" + param
      }
      return newString
    }
    // ?autoplay=1&end=6568&start=567

    const dateToMs = (date) => {

      const timestamp = date.toString().split(".")

      if (timestamp.length == 2) timestamp.unshift('00')

      const timeObj = {
        seconds: timestamp[2] != '00' ? parseInt(timestamp[2]) : null,
        minutes: timestamp[1] != '00' ? parseInt(timestamp[1]) : null,
        hours: timestamp[0] != '00' ? parseInt(timestamp[0]) : null,
      }


      let milliseconds = 0;
      if (timeObj.seconds) milliseconds += timeObj.seconds
      if (timeObj.minutes) milliseconds += timeObj.minutes * 60
      if (timeObj.hours) milliseconds += timeObj.hours * 3600

      const msDate = milliseconds.toFixed(0)

      return msDate;
    }

    let paramsString = "";

    const content = this.content;

    if (content.debut != undefined && content.debut) paramsString = addParam(paramsString, "start=" + dateToMs(content.debut))
    if (content.fin != undefined && content.fin) paramsString = addParam(paramsString, "end=" + dateToMs(content.fin))
    if (content.lecture_auto != undefined && content.lecture_auto) paramsString = addParam(paramsString, "autoplay=1")
    // if (content.son_active != undefined && content.son_active) paramsString = addParam(paramsString, "")

    return this.content.lienUrl + paramsString;
  }


}
