import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AnimationOptions } from 'ngx-lottie';
import { CommunauteService } from 'src/app/components/common/communaute/communaute.service';
import { faMessageDots } from '@fortawesome/pro-solid-svg-icons';
import { faCalendar } from '@fortawesome/pro-duotone-svg-icons';
@Component({
  selector: 'app-annonces',
  templateUrl: './annonces.component.html',
  styleUrls: ['./annonces.component.scss']
})
export class AnnoncesComponent implements OnInit {
  faCalendar = faCalendar;
  faMessageDots = faMessageDots;
  @Input() slug: string;
  @Input() annonces;
  @Input() type;
  @Input() id;
  @Input() user;

  pannonces : number = 1;
  public load = false;
  constructor(private communauteService : CommunauteService, public router: Router) {
   
   }
   options: AnimationOptions = {
    path: 'https://assets7.lottiefiles.com/packages/lf20_r2nu21mo.json',
  };

  async ngOnInit() {
   this.load = false;
    (this.type && this.type == "annonces" && this.id == null ) ? this.annonces  = await this.communauteService.AnnonceCommunity(this.slug).toPromise() : null;
    this.load = true;

  }
}
