import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { PopupService } from '@ng-bootstrap/ng-bootstrap/util/popup';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { ModalService } from '../../../common/popup-form/popup-form.service';

@Component({
  selector: 'app-carousel-home',
  templateUrl: './carousel-home.component.html',
  styleUrls: ['./carousel-home.component.scss']
})
export class CarouselHomeComponent implements OnInit {
  private template: ElementRef;
  public theme: any;
  advisorSlides: OwlOptions = {
    loop: true,
    nav: false,
    dots: true,
    margin: 0,
    rtl: true,
    autoplay: true,
    autoplayHoverPause: false,
    responsive: {
      0: {
        items: 1,
      }
    },
  };

  public data: any;

  @ViewChild('template', { static: false }) set target(target: ElementRef) {
    if (target) this.template = target;
  }

  constructor(private modal: ModalService) { 

  }

  async ngOnInit() {
    this.data = JSON.parse(this.data);
    this.theme = JSON.parse(localStorage.getItem('Theme')).theme;
  }

  openModal() {
    this.modal.openEmailModal();
  }

}
