import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot, UrlTree ,Router} from '@angular/router';

import { NotifierService } from 'angular-notifier';
import { ProfileService } from '../components/pages/profile/profile.service';
import { UserService } from '../services/user.service';
import {CommunauteService} from '../components/common/communaute/communaute.service'
import * as dayjs from 'dayjs';
import * as relativeTime from 'dayjs/plugin/relativeTime';
dayjs.extend(relativeTime);
import 'dayjs/locale/fr';

@Injectable({
  providedIn: 'root'
})
export class CommunityGuard implements CanActivate {
     
  constructor(
    public notifier: NotifierService,
    private router: Router,
    private userService: UserService,
    private communauteService: CommunauteService,
) {

}


 async canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot)
    // : Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree
     {
    const currentUser = this.userService.getUser();
    const formation = route.params.slug;

    if (currentUser) {
      if(formation){
    
      const can = await this.communauteService.CanAccessFormationCommunity(formation).toPromise();
      if (can['etat']) {

       return true;
      } else {
        this.notifier.notify('error', can['msg']);
        this.router.navigate(['/formation/' + can['slug']]);
        return false;
      }
    }else{
      this.router.navigate(['/dashboard']);
      this.notifier.notify('error', "Vous n'avez pas accès à cette formation");

      return false;

    }

    } else {
      this.userService.logOut();
      this.router.navigate(['/portail']);
      return false;
    }
  }


  isConnected() {
    return this.userService.isLoggedIn();
  }
  
}
