import { Component, ComponentFactoryResolver, ComponentRef, Input, OnInit, ViewChild, ViewContainerRef, ViewEncapsulation } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { AdDirective } from 'src/app/ad.directive';
import { FaqComponent } from '../faq/faq.component';
import { CarouselHomeComponent } from './carousel-home/carousel-home.component';
import { CategoriesComponent } from './categories/categories.component';
import { HomepageAboutComponent } from './homepage-about/homepage-about.component';
import { HomepageBannerComponent } from './homepage-banner/homepage-banner.component';
import { HomePageStatsComponent } from './homepage-stats/homepage-stats.component';
import { HomePageService } from './homepage.service';

@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class HomepageComponent implements OnInit {
  public htmlStr = "";
  public notFound = false;
  public load = false;

  @ViewChild('view', { read: ViewContainerRef }) view: ViewContainerRef;
  public tabs: any;
  constructor(private HomePageService: HomePageService, private resolver: ComponentFactoryResolver) { }

  async ngOnInit() {
    const orders = await this.HomePageService.getHome().toPromise();
    this.view.clear();
    if (orders && orders.Structure && orders.Structure.length > 0) {
      orders.Structure.forEach(order => {
        const component = order.__component.split(".")[1];
        this.SwitchStructure(component, order);
      })
      this.load = true;
    } else {
      this.notFound = true;
    }

  }


  SwitchStructure(e, order) {
    switch (e) {
      case 'banniere':
        this.loadTabs(HomepageBannerComponent, JSON.stringify(order), "data");

        break;
      case 'carousel':
        this.loadTabs(CarouselHomeComponent, JSON.stringify(order.carousel), "data");
        break;
      case 'categories':
        this.loadTabs(CategoriesComponent, JSON.stringify(order), "categs");

        break;
      case 'about':
        this.loadTabs(HomepageAboutComponent, JSON.stringify(order), "data");


        break;
      case 'reassurance':
        this.loadTabs(HomePageStatsComponent, JSON.stringify(order), "data");


        break;
      case 'faq':
        this.loadTabs(FaqComponent, JSON.stringify(order), "title");


        break
    }
  }

  loadTabs(tabComponent: any, tabData, key) {
    this.view.clear();
    setTimeout(() => {
      let componentFactory =
        this.resolver.resolveComponentFactory(tabComponent);
      let componentRef: ComponentRef<typeof tabComponent> =
        this.view.createComponent(componentFactory);
      componentRef.instance[key] = tabData;
    }, 0);
  }

}
