import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomepageComponent } from './components/pages/homepage/homepage.component';
import { ContactUsComponent } from './components/pages/contact-us/contact-us.component';
import { ProfileAuthenticationComponent } from './components/pages/profile-authentication/profile-authentication.component';
import { ErrorComponent } from './components/pages/error/error.component';
// import { CoursesStyleFourComponent } from './components/pages/courses-style-four/courses-style-four.component';
import { FormationsComponent } from './components/pages/formations/formations.component';
import { ProfileComponent } from './components/pages/profile/profile.component';
import { EventsComponent } from './components/pages/events/events.component';
import { EventsDetailsComponent } from './components/pages/events-details/events-details.component';
import { BlogDetailsComponent } from './components/pages/blog-details/blog-details.component';
import { BlogTagSearchComponent } from './components/pages/blog-tag-search/blog-tag-search.component';
import { CoursesDetailsComponent } from './components/pages/courses-details/courses-details.component';
import { ProfileUpdateComponent } from './components/common/profile-update/profile-update.component';
import { CoursesCheckoutComponent } from './components/pages/courses-checkout/courses-checkout.component';
import { CoursesCartComponent } from './components/pages/courses-cart/courses-cart.component';
import { RessourcesComponent } from './components/pages/ressources/ressources.component';
import { SuiviFormationComponent } from './components/pages/suivi-formation/suivi-formation.component';

import { AuthGuard } from './guard/auth.guard';
import { AuthNotGuard } from './guard/auth-not.guard';
import { NoDataGuard } from './guard/no-data.guard';
import { AuthSuiviGuard } from './guard/auth-suivi.guard';
import { LeavingSuiviGuard } from './guard/leaving-suivi.guard';
import { CommunityGuard } from './guard/community.guard';

import { CategoriesComponent } from './components/pages/homepage/categories/categories.component';
import { BlogCategoriesSearchComponent } from './components/pages/blog-categories-search/blog-categories-search.component';
import { ConditionsComponent } from './components/pages/conditions/conditions.component';
import { CommunityComponent } from './components/pages/community/community.component';
import { ForumComponent } from './components/pages/community/forum/forum.component';
import { AvisComponent } from './components/pages/community/avis/avis.component';
import { AnnoncesComponent } from './components/pages/community/annonces/annonces.component';

const routes: Routes = [
    { path: '', component: HomepageComponent }, // ok
    {
        path: 'nos-formations',
        component: FormationsComponent,
        canActivate: [NoDataGuard],
    }, // ok
    { path: 'ressources', component: RessourcesComponent }, // ok
    { path: 'tag/:slug', component: BlogTagSearchComponent }, // ok
    {
        path: 'portail',
        component: ProfileAuthenticationComponent, // ok
        canActivate: [AuthNotGuard],
    },
    {
        path: 'suivi/:slug/:id_session',
        component: SuiviFormationComponent,
        canActivate: [AuthSuiviGuard],
        canDeactivate: [LeavingSuiviGuard],
    },
        {
        path: 'suivi/:slug',
        component: SuiviFormationComponent,
        canActivate: [AuthSuiviGuard],
        canDeactivate: [LeavingSuiviGuard],
    },
    {
        path: 'communaute/:slug',
        component: CommunityComponent,
        canActivate: [CommunityGuard],
    },
    {
        path: 'communaute/:slug/:type',
        component: CommunityComponent,
        canActivate: [CommunityGuard],
    },
    {
        path: 'communaute/:slug/:type/:id',
        component: CommunityComponent,
        canActivate: [CommunityGuard],
    },
  

    { path: 'blog/:slug', component: BlogDetailsComponent }, // ok
    { path: 'contact', component: ContactUsComponent }, // ok
    { path: 'formation/:slug', component: CoursesDetailsComponent }, // ok
    { path: 'choix', component: CoursesCartComponent }, // ok
    { path: 'achat', component: CoursesCheckoutComponent }, // ok
    {
        path: 'dashboard',
        component: ProfileComponent,
        canActivate: [AuthGuard],
    }, // ok

    // ================================

    { path: 'conditions', component: ConditionsComponent }, // to remake

    {
        path: 'edition-profile',
        component: ProfileUpdateComponent,
        canActivate: [AuthGuard],
    }, // to keep 4 component

    // A KEEP POUR FAIRE EVENEMENTS ==============================================
    { path: 'events', component: EventsComponent }, // to keep  4 page
    { path: 'single-events', component: EventsDetailsComponent }, // to keep 4 page
    { path: 'categories', component: CategoriesComponent },
    { path: 'categorie/:slug', component: BlogCategoriesSearchComponent },
    { path: '**', component: ErrorComponent }, // 404
];

@NgModule({
    imports: [RouterModule.forRoot(routes, { useHash: true })],
    exports: [RouterModule],
})
export class AppRoutingModule {}
