import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CommunauteService } from '../../common/communaute/communaute.service';
import {
  faGridHorizontal
} from '@fortawesome/pro-regular-svg-icons';

import { faStars, faBooks, faVolume } from '@fortawesome/pro-regular-svg-icons';
import { UserService } from 'src/app/services/user.service';
@Component({
  selector: 'app-community',
  templateUrl: './community.component.html',
  styleUrls: ['./community.component.scss']
})

export class CommunityComponent implements OnInit {
  public logo: any = JSON.parse(localStorage.getItem('Theme')).logo.url;
  public theme = JSON.parse(localStorage.getItem('Theme')).theme;
  public user = this.userService.getUser();
  public forum: any;
  slug: any;
  selected: any;
  type: any;
  id: any;
  faGridHorizontal = faGridHorizontal;
  formation: any;
  typeAccept = ["annonces", "forum", "avis"]
  community: any;
  public menuData;


  constructor(private communauteService: CommunauteService,
    private route: ActivatedRoute,
    private router: Router,
    private userService: UserService
  ) {
    this.route.params.subscribe((param: any) => {
      this.type = param.type;
      this.slug = param.slug;
      this.id = param.id;
      this.formation = this.slug.replace(/-/g, " ");

      this.menuData = [
        {
          menuName: 'Avis',
          menuLink: '/communaute/' + this.slug + '/avis',
          buttonIcon: '⭐',
          icon: faStars,
          selected: this.router.url.includes('avis')
        },
        {
          menuName: 'Forum',
          menuLink: '/communaute/' + this.slug + '/forum',
          buttonIcon: '📚',
          icon: faBooks,
          selected: this.router.url.includes('forum')
        },
        {
          menuName: 'Annonce',
          menuLink: '/communaute/' + this.slug + '/annonces',
          buttonIcon: '🚀',
          icon: faVolume,
          selected: this.router.url.includes('annonces')
        }
      ]

      this.selected = this.menuData.find((el) => el.selected);

      if(!this.type || (this.type && !this.typeAccept.find((el) => el == this.type))){
        this.router.navigate(['/communaute/' + this.slug +"/forum"]);
        this.type = "forum";
      }



    })
  }

  async ngOnInit() {
   // (this.type == null) ? this.community = await this.communauteService.getCommunityFormationPreview(this.slug).toPromise() : null;
  }


  toggleNavbar(e) {
    for (let i = 0; i < this.menuData.length; i++) {
      i == e
        ? (this.menuData[i]['selected'] = true)
        : (this.menuData[i]['selected'] = false);
    }

    this.findSelected();
  }

  findSelected() {
    this.selected = this.menuData.find((el) => el.selected);

    if (this.selected == undefined) {
      return this.selected = this.menuData[1]
    } else {
      return this.selected;
    }
  }
}
