import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../src/environments/environment';
import { fetchStrapiFormat } from '../../../helpers/functions';
@Injectable({
    providedIn: 'root',
})
export class ProfileUpdateService {
    private API_URL = environment.API_URL;

    constructor(private http: HttpClient) {}

    UploadAvatar(data){
        let url = this.API_URL+"/upload";
        return this.http.post(url,data);
    }

    SendInvoice(data){
        let url = this.API_URL+"/users-permissions/user/sendInvoice";
        return this.http.post(url,data);
    }

    UpdateInfoProfil(data) {
        let url = this.API_URL+"/users-permissions/user/UpdateInfoProfil";
        return this.http.post(url,data);
    }

    getOrdersUser() {
        return fetchStrapiFormat(this.http, { url: '/orders/user' });
    }

    ExistNewsLetter(data){
        let url = this.API_URL+"/newsletter/register";
        return this.http.post(url,data);
    }
  
    UpdateNewsLetter(data){
        let url = this.API_URL+"/newsletter/updateauthorize";
        return this.http.post(url,data);
    }
}
