import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NotifierService } from 'angular-notifier';

import { RessourcesService } from './ressources.service';

@Component({
    selector: 'app-ressources',
    templateUrl: './ressources.component.html',
    styleUrls: ['./ressources.component.scss'],
})
export class RessourcesComponent implements OnInit {
    public data: any;
    private readonly notifier: NotifierService;
    public theme: any;

    public selected: any;
    constructor(
        private ressources: RessourcesService,
        private _route: ActivatedRoute,
        public _router: Router,
        private notifierService: NotifierService,
    ) {
        this.theme = JSON.parse(localStorage.getItem('Theme')).theme;
        this.notifier = notifierService;
        this.ressources.getData().then((data: any) => {
            for (let i = 0; i < data.ressourcesLists.length; i++) {
                data.ressourcesLists[i]['selected'] = false;
            }
            this.data = data;
            if (
                this._router.url.includes('#') &&
                data.ressourcesLists.find((el) =>
                    el.menuLink.includes(this._router.url.split('#')[1])
                ).publier
            ) {
                var findkey = this._router.url.split('#')[1];
                data.ressourcesLists[
                    Object.keys(data.ressourcesLists).find(
                        (key) => data.ressourcesLists[key].menuLink === findkey
                    )
                ]['selected'] = true;
            } else {
                data.ressourcesLists[
                    Object.keys(data.ressourcesLists).find(
                        (key) => data.ressourcesLists[key].menuLink === 'blog'
                    )
                ]['selected'] = true;
            }
            this.selected = this.data.ressourcesLists.find(
                (el) => el.selected == true
            );
        });
    }
    ngAfterViewInit() {}
    ngOnInit(): void {}

    notifyError() {
        this.notifier.notify(
            'info',
            'Cette fonctionnalité arrive bientôt ! 👍'
        );
    }

    toggleNavbar(e) {
        if (!this.data.ressourcesLists[e].publier) {
            return;
        }

        for (let i = 0; i < this.data.ressourcesLists.length; i++) {
            i == e
                ? (this.data.ressourcesLists[i]['selected'] = true)
                : (this.data.ressourcesLists[i]['selected'] = false);
        }
        this.findSelected();
    }

    findSelected() {
        this.selected = this.data.ressourcesLists.find(
            (el) => el.selected == true
        );
    }
}
