import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute } from '@angular/router';
import { environment } from '../../../../../src/environments/environment';
import { fetchStrapiFormat } from 'src/app/helpers/functions';

@Component({
    selector: 'app-blog-categories-search',
    templateUrl: './blog-categories-search.component.html',
    styleUrls: ['./blog-categories-search.component.scss']
})
export class BlogCategoriesSearchComponent implements OnInit {

    public slug: any;
    public data: any;

    constructor(
        private http: HttpClient,
        private route: ActivatedRoute
    ) {
        this.route.params.subscribe((param: any) => {
            let ddd = fetchStrapiFormat(this.http, { url: '/blog-categories', filter : {slug :param.slug  }, populate : ['blog_posts.image','blog_posts','blog_tag']});   
            ddd.then((res) => {
                this.data = res;
            });
        });
    }

    ngOnInit(): void {}

    blogGrid: number = 1;
}