import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot, UrlTree ,Router} from '@angular/router';

import { NotifierService } from 'angular-notifier';
import { ProfileService } from '../components/pages/profile/profile.service';
import { UserService } from '../services/user.service';

import { Observable } from 'rxjs';
import * as dayjs from 'dayjs';
import * as relativeTime from 'dayjs/plugin/relativeTime';
dayjs.extend(relativeTime);
import 'dayjs/locale/fr';

@Injectable({
  providedIn: 'root'
})
export class AuthSuiviGuard implements CanActivate {

     
  constructor(
    public notifier: NotifierService,
    private router: Router,
    private userService: UserService,
    private profileService: ProfileService,
) {

}


 async canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot)
    // : Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree
     {
    const currentUser = this.userService.getUser();
    const formation = route.params.slug;
    const id_session = route.params.id_session;

    if (currentUser ) {
      if(formation && id_session ){
        const can = await this.profileService.CanAccesSession(formation,id_session).toPromise();
    
        if (can['etat']) {
          const del = await this.profileService.GetTimePourcentCourse(can.session.id).toPromise();
  
          dayjs.locale('fr')
          const date_tz = dayjs().format()
  
          let log = {
            session: can.session.id,
  
            publishedAt: date_tz
          }
  
  
          if (del) {
  
            if (del["logs"] && del["logs"]["activites"] && del["logs"]["module"]) {
  
              if (del["logs"]["totalActivity"] != del["logs"]["totalDone"]) {
                const ll = [{
                  pourcentage: del["logs"]["pourcentage"],
                  time: del["logs"]["time"],
                  date_created: date_tz,
                  module: del["logs"]["module"].id,
                  activites: del["logs"]["activites"].id,
                  totalActivity: del["logs"]["totalActivity"],
                  totalDone: del["logs"]["totalDone"]
                }]
                log["logs"] = ll;
                const postLog = await this.profileService.PostLogSuivi({ data: log }).toPromise();
  
                localStorage.setItem('log', JSON.stringify({ id: postLog["id"], logs: log["logs"] }));
              } else {
                const logs = (Array.isArray(del['logs'])) ? del['logs'] : [del['logs']];
                localStorage.setItem('log', JSON.stringify({ id: del["id"], logs: logs }));
              }
  
            } else {
              localStorage.setItem('log', JSON.stringify({ id: del["id"] }));
  
            }
  
          } else {
            const postLog = await this.profileService.PostLogSuivi({ data: log }).toPromise();
  
            localStorage.setItem('log', JSON.stringify({ id: postLog["id"] }));
  
          }
  
  
  
          this.notifier.notify('success', 'Au travail 💪');
          localStorage.setItem('session', JSON.stringify(can.session));
  
          return true;
        } else {
          this.notifier.notify('error', can['msg']);
          if (can["slug"]) {
            this.router.navigate(['/formation/' + can['slug']]);
          } else {
            this.router.navigate(['/dashboard']);
          }
  
          return false;
        }
  
      }else{
        this.router.navigate(['/dashboard']);
        this.notifier.notify('error', "Oups.. Cet endroit n'existe pas");
        return false;
      }

    } else {
      this.userService.logOut();
      this.router.navigate(['/portail']);
      return false;
    }
  }


  isConnected() {
    return this.userService.isLoggedIn();
  }

}
