import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { Data } from '@angular/router';
import {
    faCircleInfo,
    faXmarkLarge,
    faArrowUpRightFromSquare,
} from '@fortawesome/pro-regular-svg-icons';
import { IMasonryGalleryImage } from 'ngx-masonry-gallery';

@Component({
    selector: 'app-eval-question',
    templateUrl: './eval-question.component.html',
    styleUrls: ['./eval-question.component.scss'],
})
export class EvalQuestionComponent implements OnInit {

    @Input() public data: any;

    @ViewChild('template', { static: false }) set target(target: ElementRef) {
        if (target) this.template = target;
    }


    private template: ElementRef;
    public faCircleInfo = faCircleInfo;
    public faXmarkLarge = faXmarkLarge;
    public faArrowUpRightFromSquare = faArrowUpRightFromSquare;
    public modalOpen: boolean = false;
    public isLeavingModal: boolean = false;
    public mediaUrlsArray: Array<any> = [];

    // public advisorSlides: OwlOptions = {
    //     loop: true,
    //     nav: false,
    //     dots: true,
    //     margin: 30,
    //     rtl: true,
    //     autoplay: false,
    //     responsive: {
    //         0: {
    //             items: 1,
    //         },
    //     },
    // };




    constructor() {
    }



    ngOnInit(): void {

        if (this.data && this.data.media && Array.isArray(this.data.media) && this.data.media.length > 0)
            this.createGallery(this.data.media)

    }

    private createGallery(medias: Array<any>) {
        const urlsArray = []
        medias.forEach(el => {
            if (el && el.url) {
                try {
                    new URL(el.url)
                    urlsArray.push(el.url)
                } catch (error) { }
            }
        })

        this.mediaUrlsArray = urlsArray
    }

    public get images(): IMasonryGalleryImage[] {
        return this.mediaUrlsArray.map(m => <IMasonryGalleryImage>{
            imageUrl: m
        });
    }



    public leaveModal() {
        this.isLeavingModal = true;
        setTimeout(() => {
            this.modalOpen = false;
            this.isLeavingModal = false;
        }, 150);
    }
}
