import { Component, HostListener, OnInit, OnDestroy } from '@angular/core';
import { faAngleDown } from '@fortawesome/pro-light-svg-icons';
import { DomSanitizer } from '@angular/platform-browser';
import { CoursesDetailsService } from '../../pages/courses-details/courses-details.service';
import { CoursesService } from '../../common/courses/courses.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { ModalService } from '../../common/popup-form/popup-form.service';
import { ComponentCanDeactivate } from '../../../guard/leaving-suivi.guard';
import {
    faPaperPlane,
    faRefresh,
    faCircleCheck,
    faDotCircle,
    faPersonToDoor,
    faTimes,
    faSchool,
} from '@fortawesome/pro-duotone-svg-icons';
import {
    faArrowAltRight,
    faArrowAltLeft,
    faArrowsMinimize,
} from '@fortawesome/pro-solid-svg-icons';
import { SuiviFormationService } from './suivi-formation.service';
import { Observable, Subject } from 'rxjs';
import { ProfileService } from '../profile/profile.service';
import { AnimationOptions } from 'ngx-lottie';
import dayjs from 'dayjs';
import * as relativeTime from 'dayjs/plugin/relativeTime';
dayjs.extend(relativeTime);
import 'dayjs/locale/fr';
@Component({
    selector: 'app-suivi-formation',
    templateUrl: './suivi-formation.component.html',
    styleUrls: ['./suivi-formation.component.scss'],
})
export class SuiviFormationComponent
    implements ComponentCanDeactivate, OnInit, OnDestroy {
    @HostListener('window:beforeunload')
    canDeactivate(): Observable<boolean> | boolean {

        if (this.InVieille == false) {
            this.LeaveCourse();
        }
        return false;
    }

    @HostListener('mousemove', ['$event'])
    onMousemove(event: MouseEvent) {
        clearTimeout(this.userActivity);
        this.setTimeout();
    }

    setTimeout() {
        this.userActivity = setTimeout(
            () => this.userInactive.next(undefined),
            1800000
        );
    }

    faTimes = faTimes;
    faSchool = faSchool;
    faArrowAltRight = faArrowAltRight;
    faArrowAltLeft = faArrowAltLeft;
    faRefresh = faRefresh;

    faPersonToDoor = faPersonToDoor;
    faArrowsMinimize = faArrowsMinimize;

    faPaperPlane = faPaperPlane;
    faCircleCheck = faCircleCheck;
    faDotCircle = faDotCircle;
    dynamicRate: number;
    faAngleDown = faAngleDown;

    userActivity;
    userInactive: Subject<any> = new Subject();
    public InVieille = false;
    private readonly notifier: NotifierService;
    public data: any;
    public slug: any;
    public modules: Array<any> = [];
    public session: any;
    public suggest: any;
    public log: any;
    public mobile: boolean = false;
    public openLeavModal: boolean = false;

    public currentActivity = null;
    public currentActivityContent = null;
    public currentActId: any;
    public currentActIndex: any;

    public currentModule = null;
    public currentModuleId: any;
    public currentModuleIndex: any;

    public menuOpen = true;
    public interval: any;
    public load = false;
    public totalActivity: number;
    public totalDone: number;
    public courseReviewAverage: number;
    public nothingToDisplay: boolean = false;
    public totalPercent: number;
    public totalTime: number;
    public displayWelcomeAnimation: boolean = true;
    public displayWelcomeAnimationClose: boolean = false;
    public logo = null;
    public progressText = ''
    public newReview: { rate: number; comment: string } = {
        rate: null,
        comment: null,
    };

    private logger_activity = {
        activite: '',
        session: '',
        done: false,
        content: '',
        time: 0,
        isActive: false,
        module: '',
        content_index: 0
    };

    sticky = false;
    // activeModule = this.sanitizer.bypassSecurityTrustResourceUrl(
    //     this.sections[0].modules[0].src
    // );

    options: AnimationOptions = {
        path: 'https://assets9.lottiefiles.com/packages/lf20_wslkt81r.json', // download the JSON version of animation in your project directory and add the path to it like ./assets/animations/example.json
    };

    constructor(
        notifierService: NotifierService,
        private route: ActivatedRoute,
        private sanitizer: DomSanitizer,
        private coursesDetailsService: CoursesDetailsService,
        private content: CoursesService,
        private router: Router,
        private modalService: ModalService,
        private suiviformationS: SuiviFormationService,
        private profileService: ProfileService
    ) {


        this.log = JSON.parse(localStorage.getItem('log'));
        this.session = JSON.parse(localStorage.getItem('session'));
        this.logo = JSON.parse(localStorage.getItem('Theme')).logo.url;

        const delay = setTimeout(() => {
            this.displayWelcomeAnimationClose = true;
            const secndDelay = setTimeout(() => {
                this.displayWelcomeAnimation = false;
                this.displayWelcomeAnimationClose = false;
            }, 1000);
        }, 3400);

        this.setTimeout();

        this.userInactive.subscribe(async () => {
            if (this.InVieille == false) {
                this.SaveLogSuivi();
                const update = await this.suiviformationS
                    .UpdateLog(this.logger_activity['id'], {
                        data: this.logger_activity,
                    })
                    .toPromise();
            }
            this.InVieille = true;
        });

        this.notifier = notifierService;

        // remove tab course if desktop
        this.route.params.subscribe((param: any) => {
            this.content.getData(param.slug).subscribe((res) => {
                const data = res[0];
                this.data = data;
                if (this.data.length == 0) {
                    this.notifier.notify(
                        'error',
                        "Cette formation n'existe pas !"
                    );
                    this.router.navigate(['/']);
                }

                if (data.Modules.length > 0 && data.Modules[0] && data.Modules[0].module && data.Modules[0].module.id) {

                    this.coursesDetailsService
                        .getProgrammeSuivi(data.id, this.session.id)
                        .subscribe((res: any) => {
                            const bool =
                                res &&
                                res['Modules'] &&
                                Array.isArray(res['Modules']) &&
                                res['Modules'][0] &&
                                res['Modules'][0].module != undefined;

                            if (bool) {
                                this.modules = res.Modules;
                                this.initCourseProgression(this.modules);
                                this.pourcentCal()
                            } else console.log('⚠️ Error.. modules not found');
                        });

                } else {
                    this.nothingToDisplay = true;

                }

                this.coursesDetailsService
                    .getRatingsForCourseDetail(data.id)
                    .subscribe((res: any) => {
                        const starAverage = [...res.starAverage];
                        const starCount = [...res.starCount];
                        data['starAverage'] = starAverage;
                        data['starCount'] = starCount;
                    });
            });
        });
    }

    @HostListener('window:scroll', ['$event'])
    handleScroll() {
        const windowScroll = window.pageYOffset;
        if (windowScroll >= 48) {
            this.sticky = true;
        } else {
            this.sticky = false;
        }
    }


    async navContentActHandler(obj) {
        const newLog = { ...this.logger_activity };

        if (obj.actId && obj.moduleId && newLog.activite == obj.actId) {
            newLog.content_index = obj.index.toString()
            this.logger_activity = newLog

        }

        const update = await this.suiviformationS
            .UpdateLog(newLog['id'], {
                data: newLog,
            })
            .toPromise();

    }

    closeModal() {
        this.openLeavModal = false;
    }

    openModalLeave() {
        this.openLeavModal = true;
    }

    async SaveLogSuivi() {

        this.pourcentCal();

        dayjs.locale('fr')
        const date_tz = dayjs().format()

        let new_log = {
            pourcentage: this.totalPercent,
            time: this.totalTime,
            module: this.currentModuleId,
            activites: this.currentActId,
            totalActivity: this.totalActivity,
            totalDone: this.totalDone,
            date_created: date_tz,
        };

        (this.log.logs) ? this.log.logs.push(new_log) : this.log["logs"] = [new_log];
        const log = await this.profileService.UpdateLogInSuivi(this.log.id, {
            data: {
                logs: this.log.logs
            },
        })
            .toPromise();

    }

    async LeaveCourse() {
        if (!this.nothingToDisplay) {
            this.SaveLogSuivi();
            const update = await this.suiviformationS
                .UpdateLog(this.logger_activity['id'], {
                    data: this.logger_activity,
                })
                .toPromise();
        }

    }

    async fetchActContentAndFillData(moduleId, currentActId, activityLoadedId, activityLoaded) {

        if (currentActId && moduleId) {
            this.currentActId = currentActId;
            this.currentModuleId = moduleId;


            let foundAct = null;

            let target = this.modules.find(
                (module) => moduleId == module.module.id
            );
            this.currentModule = target;

            target =
                target && target.module.Activites
                    ? target.module.Activites.find(
                        (el) => el.activite.id == currentActId
                    )
                    : null;

            if (target) {
                foundAct = target;
                let fetchedContent = {};

                if (currentActId != activityLoadedId) {
                    let res = await this.coursesDetailsService
                        .getActiviteContent(currentActId, this.data.id, this.session.id)
                        .toPromise();
                    if (
                        res &&
                        res['contenu'] != undefined &&
                        Array.isArray(res['contenu']) && res['contenu'].length > 0
                    ) {
                        foundAct.activite['content'] = res['contenu'];
                        fetchedContent = res['contenu'];
                    }
                } else {
                    const content = activityLoaded?.activite?.content;
                    foundAct.activite['content'] = content;
                    fetchedContent = content;
                }


                this.suiviformationS
                    .FindLog(currentActId, moduleId, this.session.id)
                    .then(async (data) => {
                        console.log('data', data);
                        if (data == false) {

                            console.log('this.logger_activity ');

                            this.logger_activity = {
                                activite: currentActId,
                                session: this.session.id,
                                done: false,
                                content: JSON.stringify(
                                    fetchedContent
                                ),
                                time: 0,
                                isActive: true,
                                module: moduleId,
                                content_index: 0
                            };

                            const save = await this.suiviformationS
                                .SaveLog({ data: this.logger_activity })
                                .toPromise();
                            this.logger_activity['id'] = save['id'];
                        } else {

                            console.log('this.logger_activity ');

                            if (data.content_index == null) data.content_index = '0'

                            this.logger_activity = data;

                            console.log('this.logger_activity', this.logger_activity);
                            const update = await this.suiviformationS
                                .UpdateLog(this.logger_activity['id'], {
                                    data: { isActive: true },
                                })
                                .toPromise();

                            this.logger_activity.session =
                                this.session.id;
                            this.logger_activity.module = moduleId;
                            this.logger_activity.activite =
                                currentActId;
                            this.logger_activity.isActive = true;
                        }
                        this.load = true;
                        this.currentActivity = foundAct;

                    }).catch(err => {
                        console.log('err FindLog', err);
                        this.currentActivity = foundAct;

                    });


                this.currentActivity = foundAct;

            } else console.log('⚠️ Error.. activite content not found');


        }
    }

    async navActHandler(direction) {
        this.SaveLogSuivi();

        const update = await this.suiviformationS
            .UpdateLog(this.logger_activity['id'], {
                data: this.logger_activity,
            })
            .toPromise();


        console.log('update', update);

        const actId = this.currentActId;
        const modId = this.currentModuleId;

        const foundModIndx = this.modules.findIndex(
            (el) => el.module.id == modId
        );
        const foundActIndx = this.modules[
            foundModIndx
        ].module.Activites.findIndex((el) => el.activite.id == actId);

        if (
            this.modules[foundModIndx].module.Activites[
            foundActIndx + direction
            ] != undefined &&
            this.modules[foundModIndx].module.Activites[
                foundActIndx + direction
            ].activite
        ) {
            // activite in same module
            const foundActId =
                this.modules[foundModIndx].module.Activites[
                    foundActIndx + direction
                ].activite.id;
            const can = this.CanChangeActivity(
                this.currentModule,
                this.currentActivity,
                foundActId,
                modId
            );
            if (can.change) {
                if (foundActId) {
                    if (can.msg && can.msg.length > 0) this.notifier.notify('success', can.msg);
                    this.logger_activity.isActive = false;
                    this.modules[foundModIndx].module.Activites[
                        foundActIndx
                    ].activite.isActive = false;
                    this.logger_activity.done = true;

                    const update = await this.suiviformationS
                        .UpdateLog(this.logger_activity['id'], {
                            data: this.logger_activity,
                        })
                        .toPromise();


                    console.log('update', update);

                    this.modules[foundModIndx].module.Activites[
                        foundActIndx + direction
                    ].activite.isActive = true;

                    this.fetchActContentAndFillData(modId, foundActId, this.currentActId, this.currentActivity);
                } else console.log('Error Act Id not found...');
            } else {
                if (can.msg && can.msg.length > 0) this.notifier.notify('info', can.msg);
            }
        } else if (
            this.modules[foundModIndx + direction] != undefined &&
            this.modules[foundModIndx + direction].module
        ) {


            // activite in next module
            if (
                this.modules[foundModIndx + direction].module.Activites &&
                this.modules[foundModIndx + direction].module.Activites[0] &&
                this.modules[foundModIndx + direction].module.Activites[0]
                    .activite
            ) {
                const moduleId =
                    this.modules[foundModIndx + direction].module.id;
                const newActIndx =
                    direction == -1
                        ? this.modules[foundModIndx + direction].module
                            .Activites.length - 1
                        : 0;
                const foundActId =
                    this.modules[foundModIndx + direction].module.Activites[
                        newActIndx
                    ].activite.id;

                this.modules[foundModIndx + direction].module;
                const can = this.CanChangeActivity(
                    this.currentModule,
                    this.currentActivity,
                    foundActId,
                    moduleId
                );
                if (can.change) {
                    if (foundActId && moduleId) {
                        if (can.msg && can.msg.length > 0) this.notifier.notify('success', can.msg);
                        this.modules[foundModIndx].module.open = false;
                        this.modules[foundModIndx + direction].module.open =
                            true;
                        this.logger_activity.isActive = false;
                        this.modules[foundModIndx].module.Activites[
                            newActIndx
                        ].activite.isActive = false;
                        const update = await this.suiviformationS
                            .UpdateLog(this.logger_activity['id'], {
                                data: this.logger_activity,
                            })
                            .toPromise();
                        this.modules[foundModIndx + direction].module.Activites[
                            newActIndx
                        ].activite.isActive = true;
                        this.fetchActContentAndFillData(moduleId, foundActId, this.currentActId, this.currentActivity);
                    } else console.log('Error Act Id not found...');
                } else {
                    if (can.msg && can.msg.length > 0) this.notifier.notify('info', can.msg);
                }
            }


        }
    }

    initCourseProgression(modules: Array<any>) {
        this.suiviformationS.LatestLog(this.session.id).then((data) => {
            if (data != false) {
                this.currentActId = data.activite.id;
                this.currentModuleId = data.module.id;
            } else {
                if (modules[0] && modules[0].module) {
                    this.currentModuleId = modules[0].module.id;
                    if (
                        modules[0].module.Activites != undefined &&
                        modules[0].module.Activites != undefined &&
                        Array.isArray(modules[0].module.Activites) &&
                        modules[0].module.Activites[0] &&
                        modules[0].module.Activites[0].activite != undefined &&
                        modules[0].module.Activites[0].activite.id != undefined
                    ) {
                        this.currentActId =
                            modules[0].module.Activites[0].activite.id;
                    }
                }
            }

            for (var i = 0; i < this.modules.length; i++) {
                if (this.modules[i].module) {
                    if (this.modules[i].module.id == this.currentModuleId) {
                        this.modules[i].module.open = true;
                    } else {
                        this.modules[i].module.open = false;
                    }
                }
            }
            this.fetchActContentAndFillData(
                this.currentModuleId,
                this.currentActId, null, null
            );
        });
    }

    toggleStarReview(rate) {
        this.newReview.rate = rate;
    }

    openModal() {
        this.modalService.openProspectModal();
    }


    toggleFullScreen() {
        this.menuOpen = !this.menuOpen;
    }

    getCourseReviewAverage(ratings) {
        let totalRate: number = 0;

        ratings.forEach((el) => {
            totalRate = totalRate + el.rate;
        });
        const result = ratings.length > 0 ? totalRate / ratings.length : 0;

        this.courseReviewAverage = parseInt(result.toFixed(0));
    }

    async EventVieille(e) {
        if (e) {
            this.InVieille = false;
        }
    }

    returnState(act) {
        if (act.done) {
            return '✅';
        } else if (act.done == false && act.isActive) {
            return '🔓';
        } else {
            return '🔒';
        }
    }

    FormatDate(timesecond) {
        var date = new Date(0);
        date.setSeconds(timesecond);
        var timeString = date.toISOString().substr(11, 8);
        return timeString;
    }

    CheckDoneStatut(currentAct, modIndex, ActIndex) {
        if (!this.modules[modIndex].module.Activites[ActIndex].activite.done) {
            if (
                parseInt(currentAct.activite.tempsMin) > 0 &&
                parseInt(currentAct.activite.tempsMin) * 60 <=
                this.modules[modIndex].module.Activites[ActIndex].activite
                    .time
            ) {
                this.modules[modIndex].module.Activites[
                    ActIndex
                ].activite.done = true;
                this.logger_activity.done = true;
                this.modules[modIndex].module.done.push(
                    this.modules[modIndex].module.Activites[ActIndex].id
                );
            } else if (
                parseInt(currentAct.activite.tempsMin) == 0 &&
                parseInt(currentAct.activite.tempsMax) > 0
                //  &&
                // parseInt(currentAct.activite.tempsMax) * 60 <=
                //     this.modules[modIndex].module.Activites[ActIndex].activite
                //         .time
            ) {
                this.modules[modIndex].module.Activites[
                    ActIndex
                ].activite.done = true;
                this.logger_activity.done = true;
                this.modules[modIndex].module.done.push(
                    this.modules[modIndex].module.Activites[ActIndex].id
                );
                //this.navActHandler(1);
            } else if (
                parseInt(currentAct.activite.tempsMin) == 0 &&
                parseInt(currentAct.activite.tempsMax) == 0
            ) {
                this.modules[modIndex].module.Activites[
                    ActIndex
                ].activite.done = true;
                this.logger_activity.done = true;
                this.modules[modIndex].module.done.push(
                    this.modules[modIndex].module.Activites[ActIndex].id
                );
            }
        }
    }

    async ngOnInit() {

        this.pourcentCal()
        if (this.nothingToDisplay == false) {

            if (window.innerWidth <= 980) {
                // 768px portrait
                this.mobile = true;
            }

            this.interval = setInterval(() => {
                if (this.InVieille == false) {
                    this.logger_activity['time']++;
                    if (this.currentModuleId) {
                        const modindex = this.modules.findIndex(
                            (el) => el.module.id == this.currentModuleId
                        );

                        this.modules[modindex].module.time++;
                        if (this.currentActId) {
                            const actindex = this.modules[
                                modindex
                            ].module.Activites.findIndex(
                                (el) => el.activite.id == this.currentActId
                            );
                            this.modules[modindex].module.Activites[actindex]
                                .activite.time++;

                            this.CheckDoneStatut(
                                this.currentActivity,
                                modindex,
                                actindex
                            );
                        }
                    }
                }
            }, 1000);
        }
    }

    ngOnDestroy() {
        if (this.interval) {
            clearInterval(this.interval);
        }
        this.openModalLeave();
    }


    CanChangeActivity(
        currentModule,
        currentActivity,
        acttargetId,
        modtargetId
    ) {

        const tempsMin = parseInt(currentActivity.activite.tempsMin)
        const tempsMax = parseInt(currentActivity.activite.tempsMax)



        const actLength = currentModule.module.Activites.length
        const lastActofModuleId = currentModule.module.Activites[actLength - 1].activite.id
        const isLastActofmodule = currentActivity.activite.id == lastActofModuleId

        const targetNotInSameModule = currentModule.module.id != modtargetId

        // module ciblé
        const modTarget =
            currentModule.module.id != modtargetId
                ? this.modules.find((el) => el.module.id == modtargetId)
                : currentModule;

        // index module ciblé
        const indexmod = modTarget
            ? this.modules.findIndex((el) => el.module.id == modtargetId)
            : this.modules.findIndex(
                (el) => el.module.id == currentModule.module.id
            );

        // activité  ciblé
        const ActTarget = this.modules[indexmod].module.Activites.find(
            (el) => el.activite.id == acttargetId
        );

        // si tempsMin & tempsMax définis
        if (
            tempsMin > 0 &&
            tempsMax > 0
        ) {
            // si le temps passé est inférieur au tempsMax 
            if (
                tempsMax * 60 >
                currentActivity.activite.time
            ) {

                // si le temps passé est inférieur au tempsMin et activité non fini
                if (
                    tempsMin * 60 >
                    currentActivity.activite.time &&
                    !ActTarget.activite.done
                ) {
                    // calcul de la difference de temps 
                    let diff = Math.round(
                        (tempsMin * 60 -
                            currentActivity.activite.time) /
                        60
                    );
                    return {
                        change: false,
                        msg:
                            'Encore ' +
                            diff +
                            ' min(s) à passer dans cette activité 🦾',
                    };
                } else {
                    let diff = Math.round(
                        (tempsMax * 60 -
                            currentActivity.activite.time) /
                        60
                    );
                    const returnTarget = this.canGoToTarget(
                        modTarget,
                        ActTarget
                    );

                    if (returnTarget.change)
                        return diff < 45 ? {
                            change: true,
                            msg:
                                ' Attention plus que ' +
                                diff +
                                ' min(s) pour suivre cette activité ⌛',
                        } : {
                            change: true,
                            msg:
                                '',
                        };
                    else return returnTarget;
                }
            } else {
                const returnTarget = this.canGoToTarget(
                    modTarget,
                    ActTarget
                );
                if (returnTarget.change)
                    return targetNotInSameModule ? this.canChangeModule(currentModule, modTarget, ActTarget) : {
                        change: true,
                        msg:
                            '',
                    }
                else return returnTarget
            }
        } else if (
            tempsMin > 0 &&
            tempsMax == 0
        ) {
            if (
                tempsMin * 60 >
                currentActivity.activite.time &&
                !ActTarget.activite.done
            ) {
                let diff = Math.round(
                    (tempsMin * 60 -
                        currentActivity.activite.time) /
                    60
                );
                return {
                    change: false,
                    msg:
                        'Encore ' +
                        diff +
                        " min(s) à passer dans l'activité 🦾",
                };
            } else {
                const returnTarget = this.canGoToTarget(
                    modTarget,
                    ActTarget
                );

                if (returnTarget.change)
                    return targetNotInSameModule ? this.canChangeModule(currentModule, modTarget, ActTarget) : {
                        change: true,
                        msg:
                            '',
                    }
                else return returnTarget
            }
        } else if (
            tempsMin == 0 &&
            tempsMax > 0
        ) {
            if (
                tempsMax * 60 >
                currentActivity.activite.time
            ) {
                const diff = Math.round(
                    (tempsMax * 60 -
                        currentActivity.activite.time) /
                    60
                );

                const perc = ((currentActivity.activite.time * 100) / (tempsMax * 60))

                const returnTarget = this.canGoToTarget(
                    modTarget,
                    ActTarget
                );

                if (returnTarget.change)
                    return perc > 80 ? {
                        change: true,
                        msg:
                            ' Attention plus que ' +
                            diff +
                            ' min(s) pour suivre cette activité ⌛',
                    } : {
                        change: true,
                        msg:
                            '',
                    };
                else return returnTarget

            } else {
                return targetNotInSameModule ? this.canChangeModule(currentModule, modTarget, ActTarget) : {
                    change: true,
                    msg:
                        '',
                }
            }
        } else if (
            tempsMin == 0 &&
            tempsMax == 0
        ) {
            // is last activity of module

            const returnTarget = this.canGoToTarget(
                modTarget,
                ActTarget
            );

            if (returnTarget.change)
                return targetNotInSameModule ? this.canChangeModule(currentModule, modTarget, ActTarget) : {
                    change: true,
                    msg:
                        '',
                }
            else return returnTarget

        }
    }

    canGoToTarget(modTarget, ActTarget) {

        if (modTarget) {
            if (
                parseInt(modTarget.module.tempsMax) > 0 &&
                parseInt(modTarget.module.tempsMax) * 60 <
                parseInt(modTarget.module.time)
            ) {
                return {
                    change: false,
                    msg: 'Le temps maximun accordé pour ce module est écoulé 👍 ',
                };
            } else if (
                parseInt(ActTarget.activite.tempsMax) > 0 &&
                parseInt(ActTarget.activite.tempsMax) * 60 <
                parseInt(ActTarget.activite.time)
            ) {
                return {
                    change: false,
                    msg: 'Le temps maximun accordé pour cette activité est écoulé 👍 ',
                };
            } else {
                return { change: true, msg: '' };
                // return { change: true, msg: '✔️ À la suite ' };
            }
        } else {
            if (
                parseInt(ActTarget.activite.tempsMax) > 0 &&
                parseInt(ActTarget.activite.tempsMax) * 60 <
                parseInt(ActTarget.activite.time)
            ) {
                return {
                    change: false,
                    msg: 'Le temps maximun accordé pour cette activité est écoulé👍 ',
                };
            } else {
                return { change: true, msg: '' };
                // return { change: true, msg: '✔️ À la suite ' };
            }
        }
    }

    canChangeModule(currentModule, modTarget, ActTarget) {
        if (modTarget) {

            const tempsMin = parseInt(currentModule.module.tempsMin)
            const tempsMax = parseInt(currentModule.module.tempsMax)

            // si tempsMin et tempsMax définins
            if (
                tempsMin > 0 &&
                tempsMax > 0
            ) {

                // si le temps passé dans le module est inferieur au tempsMax
                if (
                    tempsMax * 60 >
                    currentModule.module.time
                ) {

                    // si le temps passé dans le module est inferieur au tempsMin
                    if (
                        tempsMin * 60 >
                        currentModule.module.time && !ActTarget.activite.done
                    ) {

                        let diff = Math.round(
                            (tempsMin * 60 -
                                currentModule.module.time) /
                            60
                        );
                        return {
                            change: false,
                            msg:
                                'Encore ' +
                                diff +
                                ' min(s) à passer dans le module 🦾',
                        };
                    } else {
                        let diff = Math.round(
                            (tempsMax * 60 -
                                currentModule.module.time) /
                            60
                        );
                        const returnTarget = this.canGoToTarget(
                            modTarget,
                            ActTarget
                        );
                        if (returnTarget.change)
                            return diff < 45 ? {
                                change: true,
                                msg:
                                    ' Attention plus que ' +
                                    diff +
                                    ' min(s) pour suivre ce module ⌛',
                            } : {
                                change: true,
                                msg:
                                    '',
                            }
                        else return returnTarget;
                    }
                } else {
                    return this.canGoToTarget(modTarget, ActTarget);
                }
            } else if (
                tempsMin == 0 &&
                tempsMax > 0
            ) {
                if (
                    tempsMax * 60 >
                    currentModule.module.time
                ) {
                    let diff = Math.round(
                        (tempsMax * 60 -
                            currentModule.module.time) /
                        60
                    );
                    const returnTarget = this.canGoToTarget(
                        modTarget,
                        ActTarget
                    );
                    if (returnTarget.change)
                        return {
                            change: true,
                            msg:
                                ' Attention plus que ' +
                                diff +
                                ' min(s) pour suivre ce module ⌛',
                        };
                    else return returnTarget;
                } else {
                    return this.canGoToTarget(modTarget, ActTarget);
                }
            } else if (
                tempsMin > 0 &&
                tempsMax == 0
            ) {
                if (
                    tempsMin * 60 >
                    currentModule.module.time
                ) {
                    let diff = Math.round(
                        (tempsMin * 60 -
                            currentModule.module.time) /
                        60
                    );
                    return {
                        change: false,
                        msg:
                            'Encore ' +
                            diff +
                            ' min(s) à passer dans le module 🦾',
                    };
                } else {
                    //done module
                    return this.canGoToTarget(modTarget, ActTarget);
                }
            } else if (
                tempsMin == 0 &&
                tempsMax == 0
            ) {
                return this.canGoToTarget(modTarget, ActTarget);
            }
        } else {
            return this.canGoToTarget(modTarget, ActTarget);
        }
    }

    async ChangeActivity(data) {
        console.log('yo');

        if (data && data.act && data.mod) {

            const mod = data.mod;
            const act = data.act;
            const actId = act.activite.id;

            const activityLoadedId = this.currentActId
            const activityLoaded = this.currentActivity

            const isTheSame = activityLoadedId == actId && mod == this.currentModuleId
            if (!isTheSame) {

                const update = await this.suiviformationS
                    .UpdateLog(this.logger_activity['id'], {
                        data: this.logger_activity,
                    })
                    .toPromise();

                const can = this.CanChangeActivity(
                    this.currentModule,
                    this.currentActivity,
                    actId,
                    mod
                );




                if (can.change) {
                    this.SaveLogSuivi();

                    if (can.msg && can.msg.length > 0) this.notifier.notify('warn', can.msg);
                    this.currentActId = actId;
                    this.currentModuleId = mod;
                    const TargetIndexMod = this.modules.findIndex(
                        (el) => el.module.id == mod
                    );
                    const TargetIndexAct = this.modules[
                        TargetIndexMod
                    ].module.Activites.findIndex(
                        (el) => el.activite.id == actId
                    );
                    const CurrentIndexMod = this.modules.findIndex(
                        (el) => el.module.id == this.currentModuleId
                    );
                    const CurrentIndexAct = this.modules[
                        CurrentIndexMod
                    ].module.Activites.findIndex(
                        (el) => el.activite.id == this.currentActId
                    );
                    this.currentModule = this.modules[TargetIndexMod];

                    await this.fetchActContentAndFillData(mod, actId, activityLoadedId, activityLoaded);
                    // this.currentActivity =
                    //     this.modules[TargetIndexMod].module.Activites[
                    //     CurrentIndexAct
                    //     ];
                    this.modules[CurrentIndexMod].module.Activites[
                        CurrentIndexAct
                    ].activite.isActive = false;
                    this.logger_activity.isActive = false;

                    const update = await this.suiviformationS
                        .UpdateLog(this.logger_activity['id'], {
                            data: this.logger_activity,
                        })
                        .toPromise();
                    this.modules[TargetIndexMod].module.Activites[
                        TargetIndexAct
                    ].activite.isActive = true;


                } else {
                    if (can.msg && can.msg.length > 0) this.notifier.notify('info', can.msg);
                }
            }
        }
    }

    pourcentCal() {

        let total_done = 0;
        let total_activity = 0;
        let totalTime = 0;


        this.modules.forEach((el) => {
            totalTime += el.module.time;
            total_done += el.module.done.length;
            total_activity += el.module.Activites.length;
        });

        const progressTxt = total_done + ' / ' + total_activity
        const calculatedPerc = (isNaN(Math.round((total_done / total_activity) * 100))) ? 0 : Math.round((total_done / total_activity) * 100);

        this.totalTime = totalTime
        this.progressText = progressTxt
        this.totalActivity = total_activity
        this.totalDone = total_done
        this.totalPercent = calculatedPerc



    }
}
