import { Component, OnInit,Input } from '@angular/core';
import { CommunauteService } from 'src/app/components/common/communaute/communaute.service';
import {
  faPen
} from '@fortawesome/pro-regular-svg-icons';
import { AnimationOptions } from 'ngx-lottie';
import { Router } from '@angular/router';
import { ModalService } from 'src/app/components/common/popup-form/popup-form.service';
import { CoursesDetailsService } from '../../courses-details/courses-details.service';
import { NotifierService } from 'angular-notifier';
@Component({
  selector: 'app-avis',
  templateUrl: './avis.component.html',
  styleUrls: ['./avis.component.scss']
})
export class AvisComponent implements OnInit {
  @Input() slug;
  @Input() type;
  @Input() data;
  @Input() id;
  @Input() user;
  faPen = faPen
  options: AnimationOptions = {
    path: 'https://assets7.lottiefiles.com/packages/lf20_r2nu21mo.json',
  };
  state : any;
  private readonly notifier: NotifierService;

  pavis : number = 1;
  constructor(private communauteService : CommunauteService,
    notifierService: NotifierService,
     private coursesDetailsService: CoursesDetailsService, public router: Router, private modalService: ModalService,
  ) {         this.notifier = notifierService;
  }

 async ngOnInit() {
    (this.type && this.type == "avis" && this.id == null ) ? this.data  = await this.communauteService.getAvisCommunity(this.slug).toPromise() : null;
    this.state = await this.coursesDetailsService.getDetailCourse(this.data.formation).toPromise();

  }

  modalAvisOpen(rateUser) {
    this.modalService.openAvisModal(rateUser);
  }
async toggleRating(e) {
    if (e) {
 
                if (this.state.start && this.state.start != false) {

                    const alreadyComment = this.data.ratings.find((el) => el.user.id == this.user.id);
                    const comment = (alreadyComment) ? alreadyComment["comment"] : "";
                    const rate = (alreadyComment) ? { newRate: e, previousRate: alreadyComment["rate"] } : { newRate: e, previousRate: 0 };
                    const id = (alreadyComment) ? alreadyComment["id"] : 0;
                    const rateUser = { rate: rate, comment: comment, id: id, formation: this.data.formation, change: false };
                    
                    this.modalAvisOpen(rateUser);
                 
                } else {
                    this.notifier.notify("info", "Vous n\'avez pas commencé votre formation !")
                }
           

      
    }

}

}
