import { Component, OnInit } from '@angular/core';
import { RoleService } from '../teacher/Role.service';
import { faMinus, faMagnifyingGlassPlus } from '@fortawesome/pro-solid-svg-icons';
@Component({
  selector: 'app-equipe',
  templateUrl: './equipe.component.html',
  styleUrls: ['./equipe.component.scss']
})
export class EquipeComponent implements OnInit {
  faMagnifyingGlassPlus = faMagnifyingGlassPlus;
  faMinus = faMinus;
  public data: any;
  public theme: any = JSON.parse(localStorage.getItem('Theme')).theme;

  constructor(private content : RoleService) { 
    console.log(this.theme)
    this.content.getRoles().then((data: any) => {
      // data = data[0];
    var id_equipe =  data.roles.find((el)=>el.type.includes("backoff")).id;
    
      this.content.getRole(id_equipe).then((equipe : any)=>{
        // equipe = equipe[0];
        equipe.forEach((el)=>{
          el.selected = false;
        })
        this.data = equipe;
  
      })
  });
  }

  ngOnInit(): void {
  }

  SeeMore(i){
    this.data[i].selected = !this.data[i].selected;
  }

}
