import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-eval-qcm',
  templateUrl: './eval-qcm.component.html',
  styleUrls: ['./eval-qcm.component.scss']
})
export class EvalQcmComponent implements OnInit {

  @Input() data: any;

  constructor() { }

  ngOnInit(): void {
    this.initResponse(this.data)
  }

  initResponse(content) {
    const contentCopy = [...content]    

    if (contentCopy && Array.isArray(contentCopy) && contentCopy.length > 0) {
      contentCopy.forEach(c => c["selected"] = false)
      this.data = contentCopy
    }    
    
  }

  toggleResponse(id) {
    // if (this.data && this.data.selected != undefined) {
    //   this.data.selected = id
    // } else {
    //   this.initResponse(this.data)
    //   this.data.selected = id
    // }
  }


}
