import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import {
  faPersonToDoor,
  faSchool,
  faTimes,
  faHand
} from '@fortawesome/pro-duotone-svg-icons';
import { BsModalRef } from 'ngx-bootstrap/modal';
@Component({
  selector: 'app-veille-course',
  templateUrl: './veille-course.component.html',
  styleUrls: ['./veille-course.component.scss']
})
export class VeilleCourseComponent implements OnInit {
  faTimes = faTimes;
  faSchool = faSchool;
  faHand = faHand;
  faPersonToDoor = faPersonToDoor;
  buttonclick = false;
  constructor() { }
  @Output() changeVieille = new EventEmitter<boolean>();

  ngOnInit(): void {}



  public onConfirm(e): void {
    this.buttonclick = true;
    this.changeVieille.emit(e);
    this.buttonclick = false;
  }
}


