import { Component, OnInit } from '@angular/core';
import { BlogService } from '../../common/blog/blog.service';
import { ModalService } from '../../common/popup-form/popup-form.service';
import { faSquareEnvelope } from '@fortawesome/pro-duotone-svg-icons';

@Component({
    selector: 'app-blogpage',
    templateUrl: './blogpage.component.html',
    styleUrls: ['./blogpage.component.scss'],
})
export class BlogpageComponent implements OnInit {
    faSquareEnvelope = faSquareEnvelope;
    
    public data: any;
    public blog: any;
    public theme: any;
 

    constructor(
        private content: BlogService, 
        private modalService: ModalService
        ) {
            
        this.content.getData().then((data: any) => {
            this.data = data;
        });

        this.content.getBlogData().then((data: any) => {
            this.blog = data;
        })

    }

    blogGrid: number = 1;

    ngOnInit(): void {
        this.theme = JSON.parse(localStorage.getItem('Theme')).theme;
    }

    openModal(){
        this.modalService.openNewsletterModal()
    }
}
