import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { NotifierService } from 'angular-notifier';
import { environment } from '../../../../environments/environment';
import { ProfileAuthenticationService } from './profil-authentication.service';
import { faEye } from '@fortawesome/pro-duotone-svg-icons';
import { faEyeSlash } from '@fortawesome/pro-duotone-svg-icons';
import { CategoriesService } from '../../common/top-categories/categories.service';

@Component({
    selector: 'app-profile-authentication',
    templateUrl: './profile-authentication.component.html',
    styleUrls: ['./profile-authentication.component.scss'],
})
export class ProfileAuthenticationComponent implements OnInit {
    faEye = faEye;
    faEyeSlash = faEyeSlash;
    professions = {};
    private API_URL = environment.API_URL;
    private readonly notifier: NotifierService;
    public viewPassword = false;
    public viewPassword2 = false;
    public register = false;
    public fortgotPassword = false;
    public selected: any;
    public data: any;
    public LoginClick = false;
    public RegisterClick = false;
    public ForgotClick = false;
    public theme = JSON.parse(localStorage.getItem('Theme')).theme;

    recupForm = this.formBuilder.group({
        email: ['', Validators.required],
    });

    logForm = this.formBuilder.group({
        identifier: ['', Validators.required],
        password: ['', [Validators.required, Validators.minLength(6)]],
    });

    regForm = this.formBuilder.group({
        username: ['', Validators.required],
        email: ['', [Validators.required]],
        phoneNumber: ['', [Validators.required]],
        Profession: ['', Validators.required],
        password: ['', [Validators.required, Validators.minLength(6)]],
    });

    constructor(
        private profileauthentication: ProfileAuthenticationService,
        private formBuilder: FormBuilder,
        private router: Router,
        private http: HttpClient,
        notifierService: NotifierService,
        private categoriesService : CategoriesService
    ) {
        this.categoriesService.professionProspect().then((data)=>{ 
            this.professions = data.professions;
         })
        router.events.subscribe((val) => {});
        this.notifier = notifierService;
        this.profileauthentication.getData().then((data: any) => {
            for (let i = 0; i < data.loginLists.length; i++) {
                data.loginLists[i]['selected'] = false;
            }
            this.data = data;
            if (this.router.url.includes('#')) {
                var findkey = this.router.url.split('#')[1];
                data.loginLists[
                    Object.keys(data.loginLists).find(
                        (key) => data.loginLists[key].menuLink === findkey
                    )
                ]['selected'] = true;
            } else {
                data.loginLists[
                    Object.keys(data.loginLists).find(
                        (key) =>
                            data.loginLists[key].menuLink === 'se-connecter'
                    )
                ]['selected'] = true;
            }
            this.selected = this.router.url.includes('#')
                ? this.data.loginLists.find((el) =>
                      this.router.url.includes(el.menuLink)
                  )
                : this.data.loginLists.find((el) => el.selected == true);
        });
    }

    ngOnInit(): void {}

    notifyError() {
        this.notifier.notify(
            'info',
            'Cette fonctionnalité arrive bientôt ! 👍'
        );
    }

    /**
     * User Login
     */

    toggleRegisterMode(e) {
        if (e.includes('connecter')) {
            this.register = false;
        } else {
            this.register = true;
        }
    }

    onChangeProfession(e) {
        this.regForm.controls['Profession'].setValue(e);
    }

    toggleNavbar(e) {
        if (!this.data.loginLists[e].publier) {
            return;
        }
        
        this.viewPassword = false;
        this.viewPassword2 = false;
        this.fortgotPassword = false;
        for (let i = 0; i < this.data.loginLists.length; i++) {
            i == e
                ? (this.data.loginLists[i]['selected'] = true)
                : (this.data.loginLists[i]['selected'] = false);
        }
        this.findSelected();
    }

    findSelected() {
        this.selected = this.data.loginLists.find((el) => el.selected == true);
    }
    onSendRecup() {
        this.ForgotClick = true;
        let recupData = this.recupForm.value;
        const { email } = recupData;
        let url = `${this.API_URL}/users-permissions/forgetpassword/${email}`;

        this.ForgotClick = false;
        this.http.get<any>(url).subscribe(
            (response) => {
                this.notifier.notify(
                    'success',
                    'Un email vous a été envoyé ✉️ '
                );
                this.toggleNavbar(0);
            },
            (e) => {
                this.notifier.notify('error', e.error.error.message);
                this.ForgotClick = false;
            }
        );
    }
    onLogin(): void {
        this.LoginClick = true;
        let userData = this.logForm.value;
        const { identifier, password } = userData;
        let url = `${this.API_URL}/auth/local?locale=fr`;
        this.http
            .post<any>(url, {
                identifier,
                password,
            })
            .subscribe(
                (response) => {
                    localStorage.setItem('lms_user', JSON.stringify(response));

                    this.notifier.notify(
                        'success',
                        'Bienvenue ' + response.user.username + ' 👋 '
                    );
                    setInterval(() => {
                        this.LoginClick = false;

                        window.location.reload();
                    }, 1000);
                },
                (e) => {
                    this.notifier.notify('error', e);
                    this.LoginClick = false;
                }
            );
    }

    onRegister(): void {
        this.RegisterClick = true;
        
        let userData = this.regForm.value;
        const { username, email, password, phoneNumber, Profession} = userData;
        let url = `${this.API_URL}/auth/local/register`;
        this.http.post<any>(url, userData).subscribe(
            (response) => {
                this.regForm.reset();

                this.notifier.notify(
                    'success',
                    'Fécilitation ! 🎉 ' + username + ', connecter vous à Efia'
                );
                setInterval(() => {
                    this.toggleNavbar(0);
                }, 1500);
                this.RegisterClick = false;
            },
            (e) => {
                this.notifier.notify('error', e.error.error.message);
                this.RegisterClick = false;
            }
        );
    }
}
