import {
    Component,
    ElementRef,
    EventEmitter,
    HostListener,
    Input,
    OnChanges,
    OnInit,
    Output,
    QueryList,
    SimpleChanges,
    ViewChild,
    ViewChildren,
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CoursesService } from 'src/app/components/common/courses/courses.service';
import { ModalService } from 'src/app/components/common/popup-form/popup-form.service';
import { CourseCartService } from 'src/app/services/courseCart.service';
import { CoursesDetailsService } from '../courses-details.service';
import {
    faCircleCheck,
    faRefresh,
    faPaperPlane,
    faHeart
} from '@fortawesome/pro-duotone-svg-icons';
import {
    faChevronDown
} from '@fortawesome/pro-solid-svg-icons';
import { UserService } from 'src/app/services/user.service';
import { NotifierService } from 'angular-notifier';

@Component({
    selector: 'app-courses-details-tabs',
    templateUrl: './courses-details-tabs.component.html',
    styleUrls: ['./courses-details-tabs.component.scss'],
})
export class CoursesDetailsTabsComponent implements OnInit, OnChanges {

    faCircleCheck = faCircleCheck;
    faRefresh = faRefresh;
    faPaperPlane = faPaperPlane;
    faHeart = faHeart;
    faChevronDown = faChevronDown;

    theme = JSON.parse(localStorage.getItem('Theme')).theme;
    session = JSON.parse(localStorage.getItem('session'));

    @Input() StateSession;
    @Input() sessions: any;
    @Input() slug: any;
    @Input() data: any;
    @Input() lgMode: boolean;
    @Input() suggest: any;
    @Input() modeSuivi: boolean;
    @Input() objectifs: string;
    @Input() modules: Array<any>;
    @Input() user;

    @Output() clickUseCredit = new EventEmitter<any>();
    @Output() ToggleAvis = new EventEmitter<any>();

    public load = false;
    cart: Array<any> = [];
    public creditOpen = false;
    public videoPlay = false;
    public ref: ElementRef;
    private isContenuOn = false;
    public qAndAList: any = null;
    public ourModules: any = null;


    public rateAvColors: Array<string> = [
        'bg-red-800',
        'bg-orange-800',
        'bg-orange-500',
        'bg-yellow-500',
        'bg-yellow-300',
    ];
    public xDim = null;
    public yDim = null;
    private readonly notifier: NotifierService;

    // @ViewChildren(PopoverDirective) popovers: QueryList<PopoverDirective>;
    pavis: number = 1;
    psession: number = 1;
    pcertif: number = 1;
    private tabsListBase: Array<string> = [
        'apercu',
        'programme',
        'contenu',
        'sessions',
        'instructeur',
        'commentaire',
        'financeur',
        'q&a',
        'certification'
    ];
    public tabsList: Array<string> = [];

    @HostListener('window:resize', ['$event'])
    onResize(event) {
        this.xDim = event.target.innerWidth;
        this.filterTabs();
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.lgMode) this.filterTabs();

        if (changes.modules) this.initModDropdowns()
    }

    ngAfterViewInit() {

    }

    constructor(
        private modalService: ModalService,
        private userService: UserService,
        private coursesDetailsService: CoursesDetailsService,
        notifierService: NotifierService
    ) {
        var win = window,
            doc = document,
            docElem = doc.documentElement,
            body = doc.getElementsByTagName('body')[0],
            x = win.innerWidth || docElem.clientWidth || body.clientWidth;
        this.xDim = x;
        this.notifier = notifierService;

    }

    ngOnInit(): void {
        this.filterTabs();
        // this.initModDropdowns()
    }


    initModDropdowns() {
        const modulesCopy = [...this.modules]
        modulesCopy.forEach((m, i) => m['open'] = i == 0)
        this.ourModules = modulesCopy
    }

    toggleModDropdown(id, index) {
        const traget = this.ourModules ? this.ourModules[index] : null
        if (traget && traget.module.id == id && traget.open != undefined) {
            traget.open = !traget.open
        }
    }


    filterTabs() {
        this.tabsList = this.tabsListBase.filter((el) => {
            switch (el) {
                case 'apercu':
                    return true;
                    break;
                case 'programme':
                    return !this.modeSuivi;
                    break;
                case 'contenu':
                    return this.modeSuivi && (this.xDim < 980 || this.lgMode);
                    break;
                case 'sessions':
                    return !this.modeSuivi;
                    break;
                case 'instructeur':
                    return true;
                    break;
                case 'commentaire':
                    return true;
                    break;
                case 'financeur':
                    return !this.modeSuivi;
                    break;
                case 'certification':
                    return !this.modeSuivi;
                    break;
                case 'q&a':
                    return this.modeSuivi;
                    break;

                default:
                    break;
            }
        });
    }

    isInTabList(slug) {
        return this.tabsList.includes(slug);
    }

    async ActionOnTab(e) {
        if (this.tabsList[e] == 'q&a' && this.data && this.data.id) {
            const res = await this.coursesDetailsService
                .getQandA(this.data.id, this.session.id)
                .toPromise();

            if (res && Array.isArray(res)) {
                this.qAndAList = this.populateLiked(res);
            }

            this.load = true;
        }

        if (this.tabsList[e] == 'contenu') this.isContenuOn = true;
        else this.isContenuOn = false;
    }

    populateLiked(res: Array<any>) {
        res.forEach((el) => {
            el['liked'] =
                el.like.findIndex(
                    (l) => l.users_permissions_user == this.user.id
                ) != -1;
            el.reponse.forEach((rep) => {
                rep['liked'] =
                    rep.like.findIndex(
                        (l) => l.users_permissions_user == this.user.id
                    ) != -1;
            });
        });
        return res;
    }

    UseCredit(e) {
        this.clickUseCredit.emit(e);
    }


    toggleRating(e) {
        if (e) {
            this.ToggleAvis.emit(e)
        }

    }



    toggleStarReview(rate) {

        this.data.courseReviewAverage = rate;
    }

    CheckCanEdit(rating) {
        if (this.user) {
            if (rating.user.id == this.user.id) {
                return true
            } else {
                return false;
            }

        }
    }


    CompareDate(e) {
        var date = new Date();
        var date2 = new Date(e.date_finish);
        if (date2.getTime() > date.getTime()) {
            return 'Ouverte 🔓 ';
        } else if (date2.getTime() < date.getTime()) {
            return 'Fermée 🔒';
        }
    }

}
