import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {
    faPersonToDoor,
    faSchool,
    faTimes,
} from '@fortawesome/pro-duotone-svg-icons';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';

@Component({
    selector: 'app-confirm-leave-course',
    templateUrl: './confirm-leave-course.component.html',
    styleUrls: ['./confirm-leave-course.component.scss'],
})
export class ConfirmLeaveCourseComponent implements OnInit {
    public openLeavModal: boolean = false;

    subject: Subject<boolean>;

    faTimes = faTimes;
    faSchool = faSchool;
    faPersonToDoor = faPersonToDoor;
    constructor(public _bsModalRef: BsModalRef, private router: Router) {}
    buttonclick = false;
    action(value: boolean) {
        this._bsModalRef.hide();
        this.subject.complete();
    }

    ngOnInit(): void {}

    // closeModal(){
    //   this.bsModalRef.hide();
    // }

    public onConfirm(): void {
        this.buttonclick = true;
        this.subject.next(true);
        this._bsModalRef.hide();
        localStorage.removeItem('session');
        localStorage.removeItem('log');
        this.buttonclick = false;


    }

    public onCancel(): void {
        this.buttonclick = true;

        this.subject.next(false);
        this._bsModalRef.hide();
        this.buttonclick = false;

    }
}
