import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';

import { Router } from '@angular/router';
import {
    faCirclePlay, faEye, faFlagCheckered, faPersonToDoor, faHandPointRight, faEnvelope, faMobileButton, faUserTie, faCalendar
} from '@fortawesome/pro-duotone-svg-icons';
import { faUpload } from '@fortawesome/pro-regular-svg-icons';
import {
    faArrowRightFromBracket,
    faCowbellCirclePlus
} from '@fortawesome/pro-solid-svg-icons';
import { NotifierService } from 'angular-notifier';
import { UserService } from 'src/app/services/user.service';
class ImageSnippet {
    constructor(public src: string, public file: File) { }
}

@Component({
    selector: 'app-card-profile',
    templateUrl: './card-profile.component.html',
    styleUrls: ['./card-profile.component.scss'],
})
export class CardProfileComponent implements OnInit {
    faHandPointRight = faHandPointRight;
    faCowbellCirclePlus = faCowbellCirclePlus;
    faCirclePlay = faCirclePlay;
    faCalendar = faCalendar;
    faEye = faEye;
    faUserTie = faUserTie;
    faUpload = faUpload;
    faPersonToDoor = faPersonToDoor;
    faFlagCheckered = faFlagCheckered;
    faMobileButton = faMobileButton;
    faArrowRightFromBracket = faArrowRightFromBracket;
    faEnvelope = faEnvelope;
    selectedFile: ImageSnippet;
    @Input() ProfilList;
    @Input() userAvatar;
    @Input() formation;
    @Input() index;
    @Input() startFormation;
    @Input() overviewMode;
    @Input() profilStat;
    @Input() sessionId;
    @Output() clickToggleProfil = new EventEmitter<boolean>();
    @Output() AvatarEdit = new EventEmitter<any>();
    avatarForm: FormGroup;
    public user: any;
    public theme: any;

    notifier: NotifierService;

    constructor(
        private formBuilder: FormBuilder,
        private userService: UserService,
        private router: Router,
        notifierService: NotifierService,
    ) {
        this.notifier = notifierService;
        this.theme = JSON.parse(localStorage.getItem('Theme')).theme;
        this.user = this.userService.getUser();

    }

    logout() {
        this.userService.logOut();
        this.notifier.notify('success', 'On espère vous revoir 👋');
        this.router.navigate(['/']).then(() => {
            window.location.reload();
        });
    }

    AvatarUser() {
        if (this.userAvatar) {
            return this.userAvatar;
        } else {
            let userData = JSON.parse(localStorage.getItem('Theme'));

            return userData.default_avatar.url;
        }
    }

    GoProfil() {
        if (this.index) {
            this.clickToggleProfil.emit(this.index);
        }
    }

    FormatDate(timesecond) {
        var date = new Date(0);
        date.setSeconds(timesecond);
        var timeString = date.toISOString().substr(11, 8);
        return timeString;
    }

    ProfessionAnalyse(e) {
        if (e.length > 0) {
            return e;
        } else {
            return 'Aucun';
        }
    }

    ngOnInit() {
    }

    AnalyseStatut() {
        if (!this.startFormation && this.profilStat && this.profilStat.course.total_done == this.profilStat.course.total_activities) {
            return "Revoir"
        } else if (!this.startFormation && this.profilStat && this.profilStat.course.total_done != this.profilStat.course.total_activities) {
            return "Reprendre"
        } else {
            return "Au travail !"
        }

    }

    ChangeAvatar(imageInput) {
        const file: File = imageInput.files[0];
        this.AvatarEdit.emit(file);
    }
}
