import { Component, OnInit } from '@angular/core';
import { ModalService } from '../../../common/popup-form/popup-form.service'
@Component({
    selector: 'app-homepage-about',
    templateUrl: './homepage-about.component.html',
    styleUrls: ['./homepage-about.component.scss'],
})
export class HomepageAboutComponent implements OnInit {
    public data: any;
    public theme: any;
    public seeVideo: boolean = false;
    constructor( private modalService: ModalService) {


    
    }

    ngOnInit(): void {
        this.data = JSON.parse(this.data);

        this.theme = JSON.parse(localStorage.getItem('Theme')).theme;
    }

    openModal(){
        this.modalService.openCategoriesModal()
    }
}
