import { Component, ElementRef, Input, OnInit, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { UserService } from '../../../services/user.service';
import { NotifierService } from 'angular-notifier';
import { ActivatedRoute, Router } from '@angular/router';
import { CartService } from '../../../services/cart.service';
import { CourseCartService } from '../../../services/courseCart.service';
import { NavbarService } from './navbar.service';
import { CoursesService } from '../courses/courses.service';
import { CategoriesService } from '../../common/top-categories/categories.service';
import { faBooks, faGrid2, faCircleXmark, faPersonToDoor, faCartShopping } from '@fortawesome/pro-duotone-svg-icons';
import { faUser } from '@fortawesome/pro-regular-svg-icons';
import { faChevronDown } from '@fortawesome/pro-solid-svg-icons';
import hexRgb from 'hex-rgb';
import dynamics from 'dynamics.js';
import { Observable } from 'rxjs';
import { ProfileService } from '../../pages/profile/profile.service';

@Component({
    selector: 'app-navbar',
    templateUrl: './navbar.component.html',
    styleUrls: ['./navbar.component.scss'],
})
export class NavbarComponent implements OnInit {
    @Input() theme: string;
    faBooks = faBooks;
    faUser = faUser;
    faCartShopping = faCartShopping;
    faCircleXmark = faCircleXmark;
    faChevronDown = faChevronDown;
    faPersonToDoor = faPersonToDoor;
    faGrid2 = faGrid2;
    term = '';
    public data: any = [];
    public top_categ: any;
    public searchData: any;
    isLoggedIn = this.userService.isLoggedIn();
    private readonly notifier: NotifierService;
    cartProducts = this.cartService.getItems();
    cartCourses = this.courseCartService.getItems();
    navLogoUrl: string = null;
    classSearchApplied = false;
    classApplied = false;
    public logo: any = JSON.parse(localStorage.getItem('Theme')).logo.url;
    public waveSidebar: ElementRef;
    // public sidebar: ElementRef;
    public showSidebar: boolean = false;
    private sidebar: ElementRef;

    @ViewChild('waveSidebar', { static: false }) set ref(ref: ElementRef) {
        this.waveSidebar = ref;
    }
    public avatar;
    public sidebarContainer;
    public sidebarLinks;
    public path;
    public from;
    public to;
    public options = {
        type: dynamics.easeOut,
        duration: 450,
        friction: 450
    }

    constructor(
        private courseCartService: CourseCartService,
        private categoriesService: CategoriesService,
        private userService: UserService,
        private cartService: CartService,
        notifierService: NotifierService,
        private content: CoursesService,
        private navbar: NavbarService,
        public router: Router,
        private profileService: ProfileService,
    ) {
        if(this.isLoggedIn) {
            this.profileService.Image().then((user) => {
                this.avatar = user.avatar.url
            })
        }

        this.notifier = notifierService;
        // Route checker
        router.events.subscribe((val) => {
            return router.url;
        });
    }



    async ngOnInit() {
        this.navbar
            .getData()
            .then((res) => {
                this.isLoggedIn
                    ? (res.menuLists = res.menuLists.filter(
                        (el) => !el.menuName.includes('connecter')
                    ))
                    : null;
                this.data = res;
            })
            .catch((err) => {
                console.log('error navbar init', err);
            })


        this.categoriesService.getData().subscribe((data: any) => {
            let sortedData = [...data];
            sortedData = sortedData.sort((a, b) => {
                return b.formations.length - a.formations.length;
            });

            this.top_categ = sortedData.filter(
                (el) => el.formations.length > 0
            );

        });
    }

    ngAfterViewInit(): void {
        this.path = this.waveSidebar.nativeElement.querySelector('path');
        this.from = this.path.getAttribute('d');
        this.to = this.path.dataset.d;
    }

    public toggleSidebar() {
        document.body.classList.toggle('has-sidebar');

        if (document.body.classList.contains('has-sidebar')) {
            dynamics.animate(this.path, {
                d: this.to,
            }, this.options)
        } else {
            dynamics.animate(this.path, {
                d: this.from,
            }, this.options)
        }

        this.showSidebar = !this.showSidebar;
    }

    HideFormation(item) {
        if (item.menuLink.includes('formations')) {
            return this.top_categ == null || this.top_categ.length == 0;
        }
    }

    validateHexCode(code) {
        // code.match(new RegExp('/^#(?:[0-9a-f]{3}){1,2}$/i')) ? code : '#0b2faa'
        return (code.match('#') && code.length == 7) ? code : '#0b2faa';
    }

    hexToRgb(hex) {
        let arr = hexRgb(hex, { format: 'array' });
        arr[arr.length - 1] = 0.3;
        const str = arr.join(',');
        let obj = {
            key: 'background',
            value:
                'radial-gradient(circle, rgba(' +
                str +
                ') 0%, rgba(255,255,255,0) 60%);',
        };

        return obj.key + ': ' + obj.value;
    }

    toggleClass() {
        this.classApplied = !this.classApplied;
    }


    togglSearcheClass() {
        this.classSearchApplied = !this.classSearchApplied;
    }
    

    logOutUser() {
        this.userService.logOut();
        this.notifier.notify('success', 'On espère vous revoir 👋');
        this.router.navigate(['/']).then(() => {
            window.location.reload();
        });
    }
}
